import './connect.scss';
import { WalletConnect } from '../../Wallet';
import { Modal } from '../../Modal/Modal';
import walleticon from './icon/wallet.svg';
import { Button } from '../../Components/Button';
import { wallet } from '../../Wallet/icon';
import { SelectNetwork } from '../../Wallet/SelectNetwork';
import { Blockchain, chainList } from '../../../../constants/crosschain';

interface ConnectProps {
  mobile: boolean;
  walletConnected: boolean;
  onChainSelect: (chainId: any) => void;
  onWalletSelect: (walletType: any) => void;
  chain: Blockchain | null;
}

export const Connect = ({
  mobile,
  walletConnected,
  onChainSelect,
  onWalletSelect,
  chain,
  ...props
}: ConnectProps) => {
  return (
    <>
      <Modal
        trigger={
          <Button
            {...props}
            className="connect-wallet"
            variant="contained"
            size="small"
            disabled={walletConnected}
          >
            {' '}
            <>
              <img
                src={walleticon}
                width="17.5"
                alt="connect wallet"
                className="wallet-icon"
              />
              <span
                className="button-3"
                style={{ marginLeft: mobile ? 4 : 10 }}
              >
                {' '}
                {mobile ? 'connect' : 'connect wallet'}
              </span>
            </>
          </Button>
        }
      >
        <SelectNetwork
          chainList={chainList}
          selectedChain={chain}
          onSelect={(chain) => {
            onChainSelect(chain);
          }}
        />

        <WalletConnect
          walletList={[
            {
              walletType: 'wallet-connect',
              name: 'Wallet Connect',
              icon: wallet.walletConnect,
            },
            { walletType: 'metamask', name: 'MetaMask', icon: wallet.metamask },
          ]}
          onChainSelect={onChainSelect}
          onWalletSelect={onWalletSelect}
        />
      </Modal>
    </>
  );
};
