import React from 'react';
import './styles/WithdrawalSlider.scss';
import NumericInput from 'react-numeric-input';

// Material
import { SerializedTokenInfo } from '../../constants/tokenConfig';
import { Grid, Slider, useMediaQuery } from '@mui/material';
import { numberParse } from '../../helpers/utilities';
import { PositionInfo } from '../../types';
import { QuestionMarkTooltip } from '../v1/Modals/AssetModal/toolTip';

interface WithdrawalSliderProps {
  amount: number;
  token: SerializedTokenInfo;
  sliderValue: number;
  setSliderValue: React.Dispatch<React.SetStateAction<number>>;
  positionInfo: PositionInfo | null;
}
export default function WithdrawalSlider(props: WithdrawalSliderProps) {
  const { amount, token, setSliderValue, sliderValue, positionInfo } = props;

  const isPC = useMediaQuery('(min-width:1200px)');
  const handleSliderValue = (val: any) => {
    setSliderValue(val ?? 0);
  };

  const marks = ((steps: number, range: number) => {
    const step = range / steps;
    const marks = [];
    for (let i = 0; i <= steps; i++) {
      const value = i * step;

      marks.push({ value, label: value + '%' });
    }
    return marks;
  })(4, 100);

  function valuetext(value: number) {
    return `${value}%`;
  }

  return (
    <div className="withdrawal-slider">
      <Grid flexDirection={'row'} container>
        <Grid item>
          <div className="subtitle-1 semiBold" style={{ marginBottom: '24px' }}>
            WITHDRAWAL PERCENTAGE
            <QuestionMarkTooltip
              title={`Choose withdrawal amount by dragging the slider to desired
                withdraw percentage (e.g. withdraw everything = 100%).`}
              placementPC={'bottom'}
              placementMobile={'bottom'}
            />
          </div>
        </Grid>
        <Grid container item>
          <Grid container item>
            <Grid item className="slider-set-percentage">
              {/* TODO: Make re-usable slider component */}
              <Slider
                aria-label="Custom marks"
                value={sliderValue}
                getAriaValueText={valuetext}
                valueLabelFormat={valuetext}
                min={0}
                max={100}
                step={0.1}
                marks={marks}
                valueLabelDisplay="auto"
                sx={{
                  width: '100%',
                  'margin-top': '24px',
                  '& .MuiSlider-thumb': {
                    width: '15px',
                    height: '15px',
                    color: '#DFEEF5',
                  },
                  '& .MuiSlider-rail': {
                    color: '#0D17188C',
                    height: '10px',
                  },
                  '& .MuiSlider-track': {
                    background:
                      'linear-gradient(78.04deg, #A88EF5 2.79%, #77DFF2 208.53%)',
                    height: '10px',
                  },
                  '& .MuiSlider-markLabel': {
                    fontFamily: 'Poppins',
                    fontSize: '14px',
                    fontWeight: '600',
                    lineHeight: '16px',
                    letterSpacing: '0.0025em',
                    color: '#DFEEF5',
                    '&:first-of-type': {
                      left: '2%!important',
                    },
                    '&:last-of-type': {
                      left: '98%!important',
                    },
                  },
                  '& .MuiSlider-mark': {
                    background: '#DFEEF5',
                    height: '10px',
                    width: '10px',
                    borderRadius: '100%',
                  },

                  '& .MuiSlider-valueLabel': {
                    color: '#DFEEF5',
                    background: '#69838E59',
                    height: 'auto',
                    width: 'auto',
                    borderRadius: '10px',
                    padding: '8px',
                    fontFamily: 'Poppins',
                    fontSize: '16px',
                    fontWeight: '500',
                    lineHeight: '16px',
                    letterSpacing: '0.0025em',

                    // textAlign: 'right',
                  },
                  '& .MuiSlider-valueLabel:before': {
                    content: 'none',
                  },
                }}
                onChange={(e, v) => handleSliderValue(v)}
              />
            </Grid>
            <Grid
              item
              sx={{ margin: isPC ? 'auto' : '0px', marginRight: '0px' }}
            >
              <NumericInput
                className="selectValue headline-4 medium"
                style={false}
                min={0}
                max={100}
                precision={1}
                value={sliderValue}
                step={0.1}
                format={(x) => (x === null ? 0 : x) + '%'}
                parse={(x) => {
                  const f = parseFloat(x.replace(/%/, ''));
                  return f > 100 ? 100 : f < 0 ? 0 : f;
                }}
                onChange={handleSliderValue}
              />
            </Grid>
          </Grid>
          <Grid container item className="estimated-amount">
            <Grid item>
              <p className="subtitle-2">Estimated amount to receive</p>
            </Grid>
            <Grid item>
              <p className="headline-4 medium">
                ~{numberParse(amount * (sliderValue / 100), 'currency')}{' '}
                {token.ticker.toUpperCase()}
              </p>
            </Grid>
          </Grid>
        </Grid>
      </Grid>
    </div>
  );
}
