import {
  ConfirmationModal,
  ConfirmationProps,
  RejectedModal,
  SubmittedModal,
} from '../v1/Modals/TransactionModals';
import Asset from '../v1/Modals/AssetModal';

import { ModalType } from '../../store/base/AppSlice';
import { AssetProps } from '../v1/Modals/AssetModal/index';
import {
  useModalIsOpen,
  useCloseModal,
  useModalProps,
} from '../../store/base/application/hooks';
export type ModalProps = {
  isOpen: boolean;
  onClose: () => void;
};
export const ModalsContainer = () => {
  return (
    <div className="modals-container">
      <ConfirmationModal
        isOpen={useModalIsOpen(ModalType.CONFIRMATION)}
        onClose={useCloseModal(ModalType.CONFIRMATION)}
        {...(useModalProps(ModalType.CONFIRMATION) as ConfirmationProps)}
      />
      <RejectedModal
        onClose={useCloseModal(ModalType.TXN_REJECTED)}
        {...useModalProps(ModalType.TXN_REJECTED)}
      />
      <SubmittedModal
        onClose={useCloseModal(ModalType.TXN_SUBMITTED)}
        {...useModalProps(ModalType.TXN_SUBMITTED)}
      />
      <Asset
        isOpen={useModalIsOpen(ModalType.INVEST)}
        onClose={useCloseModal(ModalType.INVEST)}
        {...(useModalProps(ModalType.INVEST) as AssetProps)}
      />
    </div>
  );
};
