export const QuestionMarkTooltip = ({
  mobile = false,
  title = 'Question mark icon tooltip text',
  placementPC = 'bottom-start',
  placementMobile = 'bottom',
  children,
  toolTipClassName = '',
}: TooltipProps) => {
  return (
    <Tooltip
      classes={{ tooltip: toolTipClassName }}
      placement={mobile ? placementMobile : placementPC}
      title={title}
    >
      <span className="questionMark">{children ? children : QuestionMark}</span>
    </Tooltip>
  );
};
