import { BigNumber, ethers } from 'ethers';
import { formatUnits } from 'ethers/lib/utils';
import { useQuery } from 'react-query';
import { EvmChainMap } from '../../../constants/crosschain/evmChainidMap';
import { PositionInfoMap } from '../../../constants/position';
import { strategiesMap } from '../../../constants/strategy/strategies';
import { IStore, useStore } from '../../../store';
import { IEvmProvider } from '../../../store/provider/Evm/EvmProvider';
import { PositionInfo } from '../../../types';
import { keys } from '../../keyNames';
import { useMemo } from 'react';
import { EvmStrategyContractAbi } from '../../../constants/crosschain';
import {
  UnpopulatedPositionInfo,
  useFetchEvmPositionInfo,
} from './useFetchPositionInfo';
import { positionConfig } from './config';

export const getPosition = async (
  vaultLib,
  provider,
  strategyAbi,
  positionInfo
) => {
  // const posMap: { [strategyAddress: string]: PositionInfo } = {};
  if (!vaultLib || !provider || !strategyAbi || !positionInfo) return null;

  const getUserEquityUsd = async (position: {
    positionId: string;
    strategyChainId: number;
    strategyId: string;
  }) => {
    const strategyManager = new ethers.Contract(
      strategiesMap[position.strategyId].strategyAddress,
      strategyAbi,
      provider
    );
    const totalShares = (await strategyManager.vaultState())[0];
    // const ORACLE_ADDRESS = (await strategyManager.contractInfo())[2];
    const userShares = await strategyManager.getShareAmount([
      position.positionId,
      position.strategyChainId,
    ]);
    const stableTokenAddr =
      strategiesMap[position.strategyId].tokens[0].tokenContractAddress;
    const stableTokenDecimals =
      strategiesMap[position.strategyId].tokens[0].decimals;
    const decimalPower = BigNumber.from(10).pow(
      BigNumber.from(stableTokenDecimals)
    );
    const usdPriceETH = await strategyManager.getTokenETHValue(
      stableTokenAddr,
      decimalPower
    );
    const totalEquityEth = await strategyManager.getEquityETHValue();

    const totalEquityUsd = totalEquityEth.div(usdPriceETH);
    const userEquityUsd: BigNumber = totalEquityUsd.mul(
      userShares.mul(decimalPower).div(totalShares)
    );
    return [
      userEquityUsd,
      totalEquityUsd,
      totalShares,
      userShares,
      totalEquityEth,
      stableTokenDecimals,
      usdPriceETH,
    ];
  };
  try {
    const [
      userEquityUsd,
      totalEquityUsd,
      totalShares,
      userShares,
      totalEquityEth,
      stableTokenDecimals,
      usdPriceETH,
    ] = await getUserEquityUsd(positionInfo);
    const data = {
      ...positionInfo,
      totalEquity: totalEquityUsd,
      equity: formatUnits(userEquityUsd, stableTokenDecimals),
      totalShares,
      userShares,
      totalEquityEth,
      usdPriceETH,
      decimals: stableTokenDecimals,
    };
    return data;
  } catch (e: any) {
    console.log(e.stack);
  }
};
export function useFetchEvmPosition(
  positionInfo: UnpopulatedPositionInfo | null,
  options?: any
) {
  const { enable: extraEnable, ...ops } = options ?? { enable: true };
  const { chainId } = useStore(
    (state: IStore) =>
      (state.chainSlice.chainProvider as IEvmProvider)?._evmChainInfo
  ) ?? { chainId: null };

  const vaultLib = useStore(
    (state: IStore) =>
      (state.chainSlice.chainProvider as IEvmProvider)?._vaultLib
  );

  const { rpc } = useMemo(
    () => (chainId ? EvmChainMap[chainId] : { rpc: null }),
    [chainId]
  );
  const provider = useMemo(
    () => (rpc ? new ethers.providers.JsonRpcProvider(rpc[0]) : null),
    [rpc]
  );
  const strategyAbi = useMemo(
    () => (chainId ? EvmStrategyContractAbi[chainId] : null),
    [chainId]
  );
  const shouldFetch: boolean = positionInfo?.strategyId !== (undefined || null);
  const { queryKey, queryFn } = useMemo(
    () => positionConfig(vaultLib, strategyAbi, provider, positionInfo),
    [positionInfo, vaultLib, provider, strategyAbi]
  );
  return useQuery(
    queryKey,
    queryFn,
    {
      // refetchOnMount: false,
      enable: shouldFetch && extraEnable,
      ...ops,
    }
  );
}
