import { ethers } from 'ethers';
import { keys } from '../../keyNames';
import { getPosition } from './useFetchPosition';
import { Provider } from '@ethersproject/providers';
import { UnpopulatedPositionInfo } from './useFetchPositionInfo';

export const positionConfig = (
  vaultLib: ethers.Contract,
  strategyAbi: any,
  provider: Provider,
  positionInfo: UnpopulatedPositionInfo
) => ({
  queryKey: [keys.positions, positionInfo?.strategyId],
  queryFn: () => getPosition(vaultLib, provider, strategyAbi, positionInfo),
});
