import { useQuery } from 'react-query';
import {
  ironBankApi,
  homoraApi,
  HomoraApyResponse,
  homoraEndPoints,
  ironBankEndPoints,
  ironBankApyResponse,
  ironBankRate,
} from '../../../api/axios/api-config';
import { getAxios } from '../../../api/axios/generic-api-calls';
import { StrategyInfo, StrategyInfoMap } from '../../../constants/strategy';
import { strategies } from '../../../constants/strategy/strategies';
import { getStrategyInfo } from '../../../hooks/strategy/useStrategies';
import { keys } from '../../keyNames';
import { BigNumber, ethers, utils } from 'ethers';

export function useFetchStrategies(options?: any) {
  return useQuery(
    [keys.strategies],
    async () => {
      const strategyInfos: StrategyInfoMap = {};
      const apys = (
        await getAxios<HomoraApyResponse>(homoraApi, homoraEndPoints.apys)
      ).data;
      const rates = (
        await getAxios<ironBankApyResponse>(ironBankApi, ironBankEndPoints.rates)
      ).data;
      try {
        for (const strategy of strategies) {
          const strategyInfo = await getStrategyInfo(strategy);
          if (!strategyInfo) continue;

          strategyInfos[strategy.strategyId] = strategyInfo;

          const APR_TJ = Math.log((apys[strategy.poolKey].totalAPY / 100) + 1);
          const TVL = Number(strategyInfo.TVLInUnits);
          const interest_A = Number(rates.borrowRates.find((rate: ironBankRate) => rate.tokenSymbol.toLowerCase() === strategyInfo.tokens[0].ticker.toLowerCase())!.apy) * 1.2;
          const interest_B = Number(rates.borrowRates.find((rate: ironBankRate) => rate.tokenSymbol.toLowerCase() === strategyInfo.tokens[1].ticker.toLowerCase())!.apy) * 1.2;
          const debt_A = Number(utils.formatUnits(strategyInfo.debts[0], strategyInfo.tokens[0].decimals)) * strategyInfo.tokenATotokenB
          const debt_B = Number(utils.formatUnits(strategyInfo.debts[1], strategyInfo.tokens[1].decimals))
          const totalEquity = strategyInfo.totalEquityETHValue
          const apr = (APR_TJ * TVL - interest_A * debt_A - interest_B * debt_B) / totalEquity  // calculating apy
          strategyInfos[strategy.strategyId].apy = Math.exp(apr) - 1

        }
      } catch (e: any) {
        // throw new Error(e.msg);
        console.log(e);
      }
      return strategyInfos;
    },

    {
      refetchOnMount: false,
      refetchOnReconnect: false,
      refetchOnWindowFocus: false,
      ...options,
    }
  );
}
