import React from 'react';
import { useStore } from '../../../../store';
import textContext from '../../../../textContext';
import { Button } from '../../Components/Button';
import './whitelist.scss';
import { startup, whitelist } from '../../../../assets/images/modal';
import CloseIcon from '@mui/icons-material/Close';
import useGraph from '../../../../hooks/graph/useGraph';
import Modal from '@mui/material/Modal';
import { MobilePopOver } from '../../Modal/MobilePopOver';
import { useMediaQuery } from '@mui/material';

const PrivateBetaModal = () => {
  const { language, privateBeta, walletChecked, setWalletChecked } = useStore(
    (state) => state.appSlice
  );
  const chainProvider = useStore((state) => state.chainSlice.chainProvider);
  const isWalletConnected = useStore(
    (state) => state.chainSlice.isWalletConnected
  );
  const text = textContext[language].whitelistedConnect;
  const { congratulation, needAccess } = text;
  const { whitelistData } = useGraph();

  const isPC = useMediaQuery('(min-width:1200px)');

  function inWhitelist(address: string) {
    if (address) {
      const result = whitelistData.filter(
        (fil) => fil.wallet_address.toLowerCase() === address.toLowerCase()
      );
      return result.length > 0 ? true : false;
    }
    return false;
  }

  const handleEnterBeta = () => {
    if (chainProvider?.walletAddress) {
      setWalletChecked(chainProvider.walletAddress);
    }
    document.body.style.overflow = 'auto';
  };

  const handleJoinWhitelist = () => {
    window.open('https://aperturefinance.crew3.xyz/questboard', '_blank');
    handleClose();
  };

  const handleClose = () => {
    document.body.style.overflow = 'auto';

    // TODO: disconnect wallet automatically
    chainProvider && isWalletConnected && chainProvider.deleteWallet();
  };

  const AccessImg = () => (
    <img
      src={startup}
      alt="Congratulation"
      style={{ marginTop: isPC ? '49px' : '0px' }}
    ></img>
  );

  const NeedAccessImg = () => (
    <img
      src={whitelist}
      alt="Need Access"
      style={{ marginTop: isPC ? '62px' : '22px' }}
    ></img>
  );

  const Content = (gainedAccess: boolean) => (
    <div className={isPC ? 'notification-modal' : 'pop-over-modal'}>
      <div className="content-container">
        {isPC && !gainedAccess && (
          <CloseIcon className="cancel" onClick={handleClose} />
        )}
        <div>{gainedAccess ? <AccessImg /> : <NeedAccessImg />}</div>
        <div className="content">
          <div className={`headline-1${gainedAccess ? '' : ' title'}`}>
            {gainedAccess ? congratulation.header : needAccess.header}
          </div>
          <div className={`description${gainedAccess ? '' : ' access'}`}>
            {gainedAccess ? congratulation.description : needAccess.description}
          </div>
        </div>
        <div className="confirm">
          <Button
            variant="contained"
            size="large"
            light={true}
            text={gainedAccess ? congratulation.confirm : needAccess.confirm}
            onClick={gainedAccess ? handleEnterBeta : handleJoinWhitelist}
          />
        </div>
      </div>
    </div>
  );

  return (
    <React.Fragment>
      {isPC && (
        <React.Fragment>
          <Modal
            open={
              privateBeta &&
              isWalletConnected &&
              inWhitelist(chainProvider?.walletAddress!) &&
              !walletChecked.includes(chainProvider?.walletAddress!)
            }
            disableEnforceFocus
          >
            {Content(true)}
          </Modal>
          <Modal
            open={
              privateBeta &&
              isWalletConnected &&
              !inWhitelist(chainProvider?.walletAddress!)
            }
            disableEnforceFocus
          >
            {Content(false)}
          </Modal>
        </React.Fragment>
      )}
      {!isPC && (
        <React.Fragment>
          <MobilePopOver
            cross={false}
            open={
              privateBeta &&
              isWalletConnected &&
              inWhitelist(chainProvider?.walletAddress!) &&
              !walletChecked.includes(chainProvider?.walletAddress!)
            }
            className="notification-pop-over"
          >
            {Content(true)}
          </MobilePopOver>
          <MobilePopOver
            cross={true}
            open={
              privateBeta &&
              isWalletConnected &&
              !inWhitelist(chainProvider?.walletAddress!)
            }
            onClose={handleClose}
            className="notification-pop-over"
          >
            {Content(false)}
          </MobilePopOver>
        </React.Fragment>
      )}
    </React.Fragment>
  );
};

export default PrivateBetaModal;
