export default function TransactionPopup({
  hash,
  error,
  title,
}: {
  title?: string;
  hash?: string;
  error?: any;
}) {
  const { chainId } = useChainDetails();
  const tx = useTransaction(hash);
  // const theme = useContext(ThemeContext);

  const success = tx
    ? Boolean(
        tx.receipt?.status === 1 || typeof tx.receipt?.status === 'undefined'
      )
    : false;
  return (
    <GridNoFlex container>
      <div style={{ paddingRight: 16, margin: 'auto' }}>
        {success ? checkIcon : crossIcon}
      </div>
      <Grid container direction={'column'} item gap="8px">
        {/* TODO: Implement dynamic title. */}
        <span className="subtitle-2 medium" style={{ color: ' #CAD2D8' }}>
          {title ?? `Pseudo Delta Neutral strategy`}
        </span>
        {tx && tx.info && (
          <span style={{ wordBreak: 'break-all' }}>
            {`${transactionTypeParser(tx.info.type)} ${
              tx.receipt?.status === 1 ? 'success' : 'is in progress'
            } `}
          </span>
        )}
        {!tx && error && (
          <span style={{ wordBreak: 'break-all' }}>{error}</span>
        )}

        {chainId && hash && (
          <ExternalLink
            className="button-4 medium"
            href={getExplorerLink(chainId, hash, ExplorerDataType.TRANSACTION)}
            target="_blank"
          >
            View on Explorer
          </ExternalLink>
        )}
      </Grid>
    </GridNoFlex>
  );
}
