import React from 'react';
import { useStore } from '../../store';
import './styles/Disclaimer.scss';
import textContext from '../../textContext';
import Checkbox from '@mui/material/Checkbox';
import { Modal } from '@mui/material';
import { Button } from '../v1';
import { checkedicon } from '../../assets/images';

const Disclaimer = () => {
  const { language, setDisclaimerAccepted, disclaimerAccepted, width } =
    useStore((state) => state.appSlice);
  const text = textContext[language].disclaimer;
  const [checked, setChecked] = React.useState(false);
  const [state, setState] = React.useState(false);

  const handleChange = () => {
    setChecked(!checked);
  };

  const handleAccept = () => {
    setDisclaimerAccepted();
    document.body.style.overflow = 'auto';
    setState(true);
  };

  React.useEffect(() => {
    if (!disclaimerAccepted) {
      document.body.style.overflow = 'hidden';
    }
  }, []); // eslint-disable-line react-hooks/exhaustive-deps

  return (
    <React.Fragment>
      <Modal open={!disclaimerAccepted && !state} disableEnforceFocus>
        <div className="disclaimer">
          <div className="wrapper">
            <div
              className={`title ${width < 1200 ? 'headline-3' : 'headline-2'}`}
            >
              {text.header}
            </div>
            <div className={`text ${width < 1200 ? 'body-2' : 'body-1'}`}>
              <p>
                {text.text1}
                <a className="linkClass" href="/terms">
                  {text.text2}
                </a>
                {text.text3}{' '}
                <a
                  href={text.link.link}
                  className="linkClass"
                  target="_blank"
                  rel="noreferrer"
                >
                  {text.link.text}
                </a>{' '}
              </p>
            </div>
            <div className="disclaimer-confirm">
              <div className="flex" onClick={handleChange}>
                <Checkbox
                  disableRipple
                  checked={checked}
                  inputProps={{ 'aria-label': 'controlled' }}
                  checkedIcon={<img src={checkedicon} alt="Checked" />}
                />
                <small className="acknowledge">{text.acknowledge}</small>
              </div>
              <Button
                variant="contained"
                light={true}
                size={width < 1200 ? 'default' : 'large'}
                text={text.confirm}
                onClick={handleAccept}
                disabled={!checked}
              />
            </div>
          </div>
        </div>
      </Modal>
    </React.Fragment>
  );
};

export default Disclaimer;
