import { TokenInfo } from '../tokenConfig';
import { ProtocolInfo } from '../protocolConfig';
import { ethers } from 'ethers';
import { StrategyType } from '../crosschain';

export interface StrategyMetadata {
  name: string;
  version: string;
  address: string;
}
export interface Strategy {
  strategyAddress: string,
  strategyId: string;
  strategyChainId: number;
  strategyType: StrategyType;
  tokens: TokenInfo[];
  protocols: ProtocolInfo[];
  poolKey: string;
}

export interface StrategyInfo extends Strategy {
  oracleAddress: string
  leverage: number,
  TVL: number,
  TVLInUnits: number,
  capacity: number,
  apy: number | null,
  priceMovementThreshold: number,
  totalEquityETHValue: number,
  debts: any,
  maxDeposit: string,
  maxWithdraw: string,
  tokenATotokenB: number,
}

export interface StrategyInfoMap {
  [strategyId: string]: StrategyInfo;
}


export const STRATEGY_FEE_DEX_SWAP = .003;
export const STRATEGY_FEE_DEX_EXIT = .1;
