import textContext from '../../textContext';
import { useStore } from '../../store';
import { wallet } from '../../assets/images/wallet';

// Comps
import { BN, deepCopy } from '../../helpers/utilities';
import PositionList from './PositionList';
import { Box } from '../v1';
import './styles/Position.scss';
import { styled, useMediaQuery } from '@mui/material';
import TableLoader from '../Investment/TableLoader';
import { useFetchEvmPositions } from '../../features/positions/hooks';
import { useFetchStrategies } from '../../features/strategies/hooks';
import { PositionInfoMap } from '../../constants/position';
import { PositionInfo } from '../../types';

// TODO: add storybook

const LoadingContainer = styled(Box)`
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  /* justify-content: center; */
  align-items: center;
  /* text-align: center; */
  /* padding: 8% 24px; */
  /* grid-gap: 32px; */
  /* gap: 32px; */
  color: #cad2d8;
  background: rgba(13, 23, 24, 0.55);
  border-radius: 0px 0px 8px 8px;
`;
export default function PositionContainer() {
  const language = useStore((state) => state.appSlice.language);
  const text = textContext[language as keyof object]['portfolio']['reminder'];
  const isWalletConnected = useStore(
    (state) => state.chainSlice.isWalletConnected
  );
  const loaders = new Array(6).fill('');

  const isPC = useMediaQuery('(min-width:1200px)');

  const {
    data: strategies,
    // refetch: strategiesRefetch,
    // isLoading: strategiesIsLoading,
  } = useFetchStrategies({
    // refetchInterval: 30000, // 30 seconds
    // retryDelay: 1000, // 1 second
  });

  const positionResults = useFetchEvmPositions();

  const positionsIsLoading = positionResults.some((r) => !r.data);

  const positions = (() => {
    return positionResults
      .map((r) => r.data ?? {})
      .reduce((acc: PositionInfoMap, cur: PositionInfo): PositionInfoMap => {
        let { strategyId } = cur;
        return { ...acc, [strategyId]: cur };
      }, {});
  })();

  if (!isWalletConnected) {
    return (
      <Box className="reminder-container">
        <div
          style={{
            alignItems: 'center',
            display: 'flex',
          }}
        >
          <img
            src={wallet}
            width="22.5"
            height="22.5"
            alt="connect your wallet"
          />
          <span
            className={isPC ? 'headline-4 medium' : 'headline-5'}
            style={{
              paddingLeft: '10px',
              whiteSpace: 'nowrap',
            }}
          >
            {text.connectWallet}
          </span>
        </div>
      </Box>
    );
  }
  return (
    <>
      {strategies &&
      Object.keys(strategies).length &&
      positions &&
      Object.keys(positions).length ? (
        <PositionList
          positions={positions}
          data={deepCopy(
            Object.values(strategies)?.filter((fil) =>
              BN(positions[fil.strategyId]?.equity).gt(0)
            )
          )}
          strategyType={'PDN'}
        />
      ) : positionsIsLoading ? (
        <LoadingContainer>
          {loaders.map((d, i) => (
            <TableLoader key={i} />
          ))}
        </LoadingContainer>
      ) : (
        <Box className="reminder-container">
          <div
            style={{
              textAlign: 'center',
            }}
          >
            <span className={isPC ? 'headline-4 medium' : 'headline-5'}>
              {text.noPosition}
            </span>
          </div>
        </Box>
      )}
    </>
  );
}
