import React, { useEffect, useState } from 'react';
import { HashRouter, Routes, Route } from 'react-router-dom';
import GlobalStyle from './styles/GlobalStyle';
import './styles/typography.css';

// Comps
import { ModalsContainer } from './components/layout/ModalsContainer';

// Pages
import Investment from './pages/Investment';
import Portfolio from './pages/Portfolio';

// Zustand Store
import { IStore, useStore } from './store';

import { BlockNumberProvider as EvmBlockNumberProvider } from './lib/hooks/useBlockNumber';
import { TransactionUpdater as EvmTransactionUpdater } from './store/provider/transaction/TransactionUpdater';
import Popups from './components/v1/Popups/index';
import NavbarContainer from './components/layout/NavbarContainer';
import Disclaimer from './components/layout/Disclaimer';
import InitStore from './store/InitStore';
import Privacy from './pages/Privacy';
import Terms from './pages/Terms';
import Credits from './pages/Credits';
import PrivateBetaModal from './components/v1/Modals/WhitelistConnectModals/PrivateBetaModal';
import { useChainProvider, useChainType } from './store/base/chain/hooks';
import { BlockchainType } from './constants/crosschain';
import { ReactQueryDevtools } from 'react-query/devtools';
import { useFetchAllTokens } from './features/tokens/hooks';
// import { StrategyUpdater } from './store/strategy/Evm/updater';
import { keys } from './features/keyNames';
import { queryClient } from './';
import { IEvmProvider } from './store/provider/Evm/EvmProvider';
import {
  UnpopulatedPositionInfo,
  useFetchEvmPosition,
  useFetchEvmPositionInfo,
} from './features/positions/hooks';

export const TokensPrefetcher = (props: {
  setPrefetched: React.Dispatch<React.SetStateAction<boolean>>;
}) => {
  const { data: tokens } = useFetchAllTokens();

  useEffect(() => {
    if (tokens) {
      // queryClient.setQueryData(keys.tokens, tokens);
      props.setPrefetched(true);
    }
    return () => {};
  }, [tokens]);

  return null;
};
const PositionEntriesPrefetcher = (props: {
  positionInfo: UnpopulatedPositionInfo;
}) => {
  const { data: positions } = useFetchEvmPosition(props.positionInfo);
  return null;
};
export const PositionsPrefetcher = (props: {
  setPrefetched: React.Dispatch<React.SetStateAction<boolean>>;
}) => {
  const { data: positionInfo } = useFetchEvmPositionInfo();

  useEffect(() => {
    if (positionInfo) {
      // queryClient.setQueryData(keys.positions, positionInfo);

      props.setPrefetched(true);
    }
    return () => {};
  }, [positionInfo]);

  return (
  <>
    {positionInfo &&
    Object.values(positionInfo).map((pos) => (
      <PositionEntriesPrefetcher positionInfo={pos} />
    ))}
    </>
  );
};

function App() {
  // useEffect(() => {
  //   Object.freeze = function(obj) { return obj; };
  // } , []);
  const { palette, muiTheme } = useStore((state) => state.themeSlice);
  const { doneInit } = useStore((state) => state.appSlice);
  const [positionsPrefetched, setPositionsPrefetched] = useState(false);
  const [tokensPrefetched, setTokensPrefetched] = useState(false);
  const web3Provider = useStore(
    (state: IStore) =>
      (state.chainSlice.chainProvider as IEvmProvider)?._web3Provider
  );
  const walletAddress = useStore(
    (state: IStore) =>
      (state.chainSlice.chainProvider as IEvmProvider)?.walletAddress
  );
  const chainType = useChainType();
  const { data: tokens } = useFetchAllTokens();

  useEffect(() => {
    if (tokens) return;
    const prefetchTokens = async () => {
      await queryClient.setQueryData([keys.tokens], tokens);
    };
    prefetchTokens();
  }, [tokens]);

  return (
    <>
      {!doneInit && <InitStore />}
      {doneInit && (
        <>
          <GlobalStyle theme={{ palette, muiTheme }} />
          <HashRouter>
            <NavbarContainer>
              <Disclaimer />
              <PrivateBetaModal />
              <ModalsContainer />
              <Routes>
                <Route path="/" element={<Investment />} />
                <Route path="/dn/*" element={<Investment />} />
                <Route path="/portfolio" element={<Portfolio />} />
                {/* <Route path="/news" element={<News />} />
              <Route path="/settings" element={<Settings />} /> */}
                <Route path="/privacy" element={<Privacy />} />
                <Route path="/terms" element={<Terms />} />
                <Route path="/credits" element={<Credits />} />
              </Routes>
              {chainType === BlockchainType.EVM && (
                <>
                  {web3Provider && walletAddress && (
                    <>
                      {!positionsPrefetched && (
                        <PositionsPrefetcher
                          {...{ setPrefetched: setPositionsPrefetched }}
                        />
                      )}
                      {!tokensPrefetched && (
                        <TokensPrefetcher
                          {...{ setPrefetched: setTokensPrefetched }}
                        />
                      )}
                      {/* <PositionUpdater /> */}
                    </>
                  )}
                  <EvmBlockNumberProvider>
                    <EvmTransactionUpdater />
                  </EvmBlockNumberProvider>
                </>
              )}
              <Popups />
              <ReactQueryDevtools initialIsOpen />
            </NavbarContainer>
          </HashRouter>
        </>
      )}
    </>
  );
}

export default App;
