import { useMediaQuery } from '@mui/material';
import { ProtocolInfo } from '../../../../../constants/protocolConfig';
import { TokenInfo } from '../../../../../constants/tokenConfig';
import './VaultItem.scss';

// React functional component for vault item
interface IVaultItemProps {
  title: string;
  tokens: TokenInfo[];
  protocols?: ProtocolInfo[];
}
export const VaultItem = (props: IVaultItemProps) => {
  const { title, tokens, protocols } = props;
  const isPC = useMediaQuery('(min-width:1200px)');
  return (
    <div className="vaultItemContainer">
      <div className="imgContainer">
        {/* handle combining multiple token icons */}
        {tokens.length < 2 ? (
          <img src={tokens[0].icon} alt={tokens[0].ticker} />
        ) : (
          <div className="imgClass">
            <img
              src={tokens[0].icon}
              style={{ zIndex: tokens.length }}
              alt={title}
            />
            {tokens.slice(1).map((x, i) => (
              <img
                key={i}
                src={x.icon}
                style={{
                  marginLeft: '-8px',
                  zIndex: tokens.length - (i + 1),
                }}
                alt={x.ticker}
              />
            ))}
          </div>
        )}
      </div>
      <div style={{ display: 'flex', flexDirection: 'column', gap: '4px' }}>
        <div>
          <p className="headline-5">{title}</p>
          {/* {props.plus && (
            <p className="fontOrange">
              {tableAssetsText['investPlus' as keyof object]}
            </p>
          )} */}
        </div>
        {protocols &&
          protocols.map((x, i) => (
            <div key={`${x.name}_${i}`} className="protocols">
              <img src={x.icon} alt={x.name} />
              <p className={isPC ? 'subtitle-2' : 'subtitle-1'}>{x.name}</p>
            </div>
          ))}
      </div>
    </div>
  );
};
