import React, { useEffect, useState } from 'react';
import './styles/ApproveButton.scss';
import { SerializedTokenInfo } from '../../constants/tokenConfig';
import { MAX_UINT256, MIN_ALLOWANCE } from '../../constants/misc';
import { Button } from '../v1';
import { Grid } from '@mui/material';
import { useAddPopup } from '../../store/base/application/hooks';
import { PopupType } from '../../store/base/AppSlice';
import { Spinner } from '../v1/Components/Spinner';
import styled from 'styled-components';
import { logo, completedLogo } from '../../assets/images/invest';
import { QuestionMarkTooltip } from '../v1/Modals/AssetModal/toolTip';
import { BigNumber } from 'ethers';
import { useFetchAllTokens } from '../../features/tokens/hooks';
import { BN } from '../../helpers/utilities';

export interface ApproveButtonProps {
  token: SerializedTokenInfo;
  isApproved: boolean;
  setIsApproved: React.Dispatch<React.SetStateAction<boolean>>;
  isWalletConnected: boolean;
  approve: ((tokenAddr: string, amount: any) => Promise<any>) | null;
  amount: number;
}
const SpinnerContainer = styled.div`
  display: flex;
  justify-content: center;
`;

const QuestionMark = (
  <svg
    width="23"
    height="24"
    viewBox="0 0 23 24"
    fill="none"
    xmlns="http://www.w3.org/2000/svg"
  >
    <path
      d="M11.5 0.5C5.14933 0.5 0 5.64933 0 12C0 18.3507 5.14933 23.5 11.5 23.5C17.8507 23.5 23 18.3507 23 12C23 5.64933 17.8507 0.5 11.5 0.5ZM11.5 21.5491C6.22746 21.5491 1.95089 17.2725 1.95089 12C1.95089 6.72746 6.22746 2.45089 11.5 2.45089C16.7725 2.45089 21.0491 6.72746 21.0491 12C21.0491 17.2725 16.7725 21.5491 11.5 21.5491Z"
      fill="black"
    />
    <path
      d="M14.3645 6.98616C13.5944 6.31105 12.5778 5.94141 11.4997 5.94141C10.4216 5.94141 9.40508 6.31362 8.63499 6.98616C7.8341 7.68694 7.39258 8.62902 7.39258 9.63784V9.83292C7.39258 9.94587 7.48499 10.0383 7.59794 10.0383H8.83008C8.94302 10.0383 9.03544 9.94587 9.03544 9.83292V9.63784C9.03544 8.5058 10.1418 7.58426 11.4997 7.58426C12.8576 7.58426 13.964 8.5058 13.964 9.63784C13.964 10.4362 13.3993 11.1677 12.5239 11.504C11.9797 11.7119 11.5177 12.0765 11.1866 12.5539C10.8503 13.0416 10.6757 13.6269 10.6757 14.2199V14.7718C10.6757 14.8847 10.7681 14.9771 10.8811 14.9771H12.1132C12.2262 14.9771 12.3186 14.8847 12.3186 14.7718V14.1891C12.3199 13.9399 12.3963 13.6969 12.5378 13.4917C12.6793 13.2866 12.8793 13.1288 13.1118 13.0391C14.6263 12.4564 15.6043 11.1215 15.6043 9.63784C15.6069 8.62902 15.1653 7.68694 14.3645 6.98616ZM10.4729 17.6468C10.4729 17.9191 10.5811 18.1803 10.7737 18.3728C10.9662 18.5654 11.2274 18.6735 11.4997 18.6735C11.772 18.6735 12.0332 18.5654 12.2258 18.3728C12.4183 18.1803 12.5265 17.9191 12.5265 17.6468C12.5265 17.3744 12.4183 17.1133 12.2258 16.9207C12.0332 16.7282 11.772 16.62 11.4997 16.62C11.2274 16.62 10.9662 16.7282 10.7737 16.9207C10.5811 17.1133 10.4729 17.3744 10.4729 17.6468Z"
      fill="black"
    />
  </svg>
);

const CheckMark = (
  <svg
    width="23"
    height="24"
    viewBox="0 0 23 24"
    fill="none"
    xmlns="http://www.w3.org/2000/svg"
  >
    <path
      d="M11.5 22.5C12.8791 22.5018 14.245 22.231 15.5192 21.7032C16.7933 21.1754 17.9506 20.401 18.9245 19.4245C19.901 18.4506 20.6754 17.2933 21.2032 16.0192C21.731 14.745 22.0018 13.3791 22 12C22.0017 10.6209 21.731 9.25498 21.2032 7.98083C20.6754 6.70668 19.901 5.54939 18.9245 4.57546C17.9506 3.59897 16.7933 2.82458 15.5192 2.29679C14.245 1.769 12.8791 1.49822 11.5 1.50001C10.1209 1.49825 8.75498 1.76905 7.48083 2.29683C6.20668 2.82462 5.04939 3.59899 4.07546 4.57546C3.09899 5.54939 2.32462 6.70668 1.79683 7.98083C1.26905 9.25498 0.998251 10.6209 1.00001 12C0.998222 13.3791 1.269 14.745 1.79679 16.0192C2.32458 17.2933 3.09897 18.4506 4.07546 19.4245C5.04939 20.401 6.20668 21.1754 7.48083 21.7032C8.75498 22.231 10.1209 22.5017 11.5 22.5V22.5Z"
      stroke="#3A9A77"
      strokeWidth="2"
      strokeLinejoin="round"
    />
    <path
      d="M7.2998 12.0001L10.4498 15.1501L16.7498 8.8501"
      stroke="#3A9A77"
      strokeWidth="2"
      strokeLinecap="round"
      strokeLinejoin="round"
    />
  </svg>
);
export default function ApproveButton({
  token,
  isApproved,
  setIsApproved,
  isWalletConnected,
  approve,
  amount,
}: ApproveButtonProps) {
  const { isLoading: tokenIsLoading, refetch: refetchTokens } =
    useFetchAllTokens();
  const [isLoading, setIsLoading] = useState(false);
  const addPopup = useAddPopup();

  useEffect(() => {
    if (!setIsApproved || !token) return;
    BN(token.allowance.toString()).lt(
      BN(amount + '').times(BN(10).pow(token.decimals))
    )
      ? isApproved && setIsApproved(false)
      : !isApproved && setIsApproved(true);
  }, [isWalletConnected, setIsApproved, token, amount, isApproved]);
  const handleApprove = async () => {
    if (isWalletConnected) {
      if (approve) {
        try {
          // Approve max erc20 amount
          setIsLoading(true);
          await approve(token.tokenContractAddress, MAX_UINT256);
          refetchTokens();
          setIsLoading(false);
        } catch (err: any) {
          addPopup({
            [PopupType.ERROR]: {
              title: `${token?.name} Spending Approval`,
              msg: err.cause,
            },
          });
          console.error(err);
        } finally {
          setIsLoading(false);
        }
      }
    }
  };

  const ApertureLogo = () => (
    <img
      src={!isWalletConnected || isApproved ? completedLogo : logo}
      alt="Aperture"
    ></img>
  );

  return (
    <div className="approveButton">
      <Button
        light={true}
        onClick={handleApprove}
        disabled={!isWalletConnected || isApproved || isLoading}
        className={''}
      >
        <Grid
          alignItems="center"
          container
          direction="row"
          style={{ zIndex: '2' }}
        >
          <Grid item xs={1}>
            {ApertureLogo()}
          </Grid>
          <Grid item xs={10}>
            {isLoading || tokenIsLoading ? (
              <SpinnerContainer>
                <Spinner />
              </SpinnerContainer>
            ) : (
              <h4 className="medium">
                {!isApproved
                  ? `Allow Aperture Protocol to use your ${token?.ticker?.toUpperCase()}`
                  : `Completed!`}
              </h4>
            )}
          </Grid>
          <Grid item xs={1}>
            {isApproved ? (
              CheckMark
            ) : (
              <QuestionMarkTooltip
                title={`You must give the Aperture smart contracts permission to use
                  your ${token?.ticker?.toUpperCase()}. You only have to do this once per token.`}
                placementPC={'top-end'}
                placementMobile={'top-end'}
                children={QuestionMark}
              />
            )}
          </Grid>
        </Grid>
      </Button>
    </div>
  );
}
