import { forwardRef, useRef, useState, useEffect } from 'react';
import { LineGraph } from '../../../../Charts/LineGraph';
import './index.scss';
import { numberToPercentage } from '../../../../../../helpers/utilities';

interface ReturnMiniProps {
  color: any;
  data: any;
  percentage: IPercent;
  payload: any;
  token: any;
  details: boolean;
  lineCount?: 1 | 2;
  fill?: boolean;
}

interface dataInfo {
  name: string;
  value: number;
}

interface IPercent {
  week: number;
  month: number;
  inception: number;
}

export const ReturnMini = forwardRef<HTMLDivElement, ReturnMiniProps>(
  (
    {
      details = false,
      color,
      data = { week: [], month: [], inception: [], weekChangeRate: 0 },
      percentage = { week: 0, month: 0, inception: 0 },
      payload,
      token,
      lineCount = 1,
      fill = false,
      ...props
    },
    ref
  ) => {
    const [timeRage, setTimeRage] = useState<'week' | 'month' | 'inception'>(
      'week'
    );
    const [width, setWidth] = useState(0);
    const parentRef = useRef(null);

    useEffect(() => {
      parentRef.current && setWidth(parentRef.current!.offsetWidth);
      // console.log(parentRef.current!.offsetWidth);
    }, [parentRef]);

    const ButtonGroups = () => {
      const filterBy = ['week', 'month', 'inception'];
      const sectionName = ['1W', '1M', 'ALL'];

      const getRange = (name: string) => {
        return name === 'week'
          ? 'week'
          : name === 'month'
          ? 'month'
          : 'inception';
      };

      return filterBy.map((tabName, i) => {
        return (
          <div
            key={i}
            className={
              'selectebutton' + (timeRage === tabName ? ' selected' : '')
            }
            onClick={(e) => {
              e.stopPropagation();
              setTimeRage(getRange(tabName));
            }}
          >
            {sectionName[i]}
          </div>
        );
      });
    };

    const returnData: dataInfo[] = [
      { name: '1 Week', value: percentage.week },
      { name: '1 Month', value: percentage.month },
      { name: 'Inception', value: percentage.inception },
    ];

    let displayData = (dataInfo: dataInfo[]) =>
      dataInfo.map((item, i) => {
        return (
          <div key={i} className="center">
            <div className="subtitle-2" style={{ color: '#CAD2D8' }}>
              {item.name}
            </div>
            <div
              className={`subtitle-1${
                item.value > 0 ? ' positive' : item.value < 0 ? ' negative' : ''
              }`}
            >
              {item.value > 0 ? '+' : ''}
              {numberToPercentage(item.value)}
            </div>
          </div>
        );
      });

    return (
      <div ref={parentRef} {...props} className="return-chart">
        {details && (
          <>
            <div className="headline-5 medium" style={{ color: '#FFFFFF' }}>
              Return Chart
            </div>
            <div className="headline-5 grid3-mini">
              {displayData(returnData)}
            </div>
          </>
        )}
        <div className="line-graph-container">
          <div style={{ position: 'relative' }}>
            <div className="mini-selectedButtonList button-5">
              {ButtonGroups()}
            </div>
          </div>

          <LineGraph
            color={color}
            data={data[timeRage]}
            type="mini"
            legend={details}
            payload={payload}
            token={token}
            fill={fill}
            lineCount={lineCount}
          />
        </div>
      </div>
    );
  }
);
