import React, { Fragment, useEffect, useMemo } from 'react';
import Grid from '@mui/material/Grid';
import './styles/Overview.scss';
import { useStore } from '../../store';
import { Box } from '../v1/Components/Box';

import UpdateIcon from '@mui/icons-material/Update';
import { Link } from 'react-router-dom';
import textContext from '../../textContext';
import { BN, numberParse, numberParseLarge } from '../../helpers/utilities';
import { useMediaQuery } from '@mui/material';
import { useFetchEvmPositions } from '../../features/positions/hooks';
import { queryClient } from '../..';
import { keys } from '../../features/keyNames';
import { PositionInfo } from '../../types';
import { QueryObserver, useQuery, UseQueryResult } from 'react-query';
import BigNumber from 'bignumber.js';

type OverviewProps = {
  activeSubpage: 'PDN' | 'LO';
};

// component for the responsive top portion of page
export default function Overview(props: OverviewProps) {
  const language = useStore((state) => state.appSlice.language);
  const isWalletConnected = useStore(
    (state) => state.chainSlice.isWalletConnected
  );
  const text = textContext[language as keyof object]['portfolio']['overview'];
  const [total, setTotal] = React.useState('$0.00');
  const [dnVaults, setDNVaults] = React.useState('$0.00');
  const [lendOp, setLendOp] = React.useState('$0.00');
  const [observer, setObserver] = React.useState<any>();
  const [subpage, setSubpage] = React.useState<'PDN' | 'LO'>(
    props.activeSubpage
  );
  const isPC = useMediaQuery('(min-width:1200px)');

  const handleActivePage = (name: 'PDN' | 'LO') => {
    setSubpage(name);
  };
  const positionResults = useFetchEvmPositions();

  useEffect(() => {
    if (!positionResults || positionResults.length < 1) return;
    const total = positionResults
      .map((res: UseQueryResult<any, any>) => {
        if (!res.data) return '0';

        const data = res.data as PositionInfo;
        return data.equity;
      })
      .reduce((acc: BigNumber, cur: BigNumber) => acc.plus(BN(cur)), BN(0))
      .toString();
    setTotal(numberParse(total, 'currency'));
  }, [positionResults]);

  const ViewHistory = () => (
    <Link
      to="/portfolio/history"
      style={{ position: 'absolute', top: '33px', right: '2.50%' }}
    >
      <UpdateIcon fontSize="medium" sx={{ color: 'white' }} />
      <div className={`view-history${!isPC ? ' subtitle-3' : ' button-5'}`}>
        {text.history}
      </div>
    </Link>
  );

  const SectionGroups = () => (
    <div className="section-groups subtitle-1">
      <div
        className={`section-name${subpage === 'PDN' ? ' active' : ''}`}
        onClick={() => handleActivePage('PDN')}
      >
        {!isPC ? text.section.dn : text.section.pdn}
      </div>
      <div
        className={`section-name${subpage === 'LO' ? ' active' : ''}`}
        onClick={() => handleActivePage('LO')}
      >
        {text.section.lending}
      </div>
    </div>
  );

  const ValueOverview = () => {
    const values = [
      { title: text.values.total, value: total },
      // { title: text.values.dnv, value: dnVaults },
      // { title: text.values.lending, value: lendOp },
    ];

    return values.map((item, i) => {
      return (
        <Grid item key={i}>
          <div className="item-block">
            <p className={!isPC ? 'headline-5' : 'subtitle-2'}>{item.title}</p>
            <p className={!isPC ? 'headline-2' : 'headline-3'}>
              {isWalletConnected ? item.value : '$--'}
            </p>
          </div>
        </Grid>
      );
    });
  };

  return (
    <Fragment>
      <Box
        style={{
          position: 'relative',
        }}
        className="overviewClass"
      >
        {/* {width > 1199 && <SectionGroups />}
        <ViewHistory /> */}
        <div className={`title ${!isPC ? 'headline-3' : 'headline-2'}`}>
          {text.title}
        </div>
        <Grid container item className="totalContainer">
          {ValueOverview()}
        </Grid>
      </Box>
      {/* {width < 1200 && <SectionGroups />} */}
    </Fragment>
  );
}
