/**
 * Returns a function that returns a random strategy.
 * @returns {Function} A function that returns a random strategy.
 */
import { ProtocolInfo, ProtocolInfoMap } from '../../constants/protocolConfig';
import { TokenMap, StablecoinMap } from '../../constants/tokenConfig';
import { Strategy, StrategyInfo } from '../../constants/strategy';
import {
  Blockchain,
  BlockchainToEvmIdMap,
  BlockchainType,
  BlockchainTypeMap,
  EvmManagerAddress,
  EvmManagerContractAbi,
  EvmStrategyContractAbi,
  EvmVaultLibContractAbi,
  EvmVaultLibContractAddress,
  WormholeChainIdMap,
  WormholeChainIdToBlockchainMap,
} from '../../constants/crosschain';
import { generateRandomEvmAddress } from '../../helpers/utilities';
import { BigNumber, ethers, utils } from 'ethers';
import { EvmChainMap } from '../../constants/crosschain/evmChainidMap';
import { avax_HOMORA } from '../../constants/avax-homora'


const _getEvmStrategyInfo = async (strategy: Strategy) => {
  const { strategyAddress, strategyChainId, tokens } = strategy;
  const [stableToken,] = tokens;
  const chain: Blockchain = WormholeChainIdToBlockchainMap[strategyChainId];
  const evmChainId = BlockchainToEvmIdMap[chain as keyof object];
  const { rpc } = EvmChainMap[evmChainId];
  const rpcProvider = new ethers.providers.JsonRpcProvider(rpc[0]);
  // const evmManager = new ethers.Contract(
  //   EvmManagerAddress[evmChainId],
  //   EvmManagerContractAbi[evmChainId],
  //   rpcProvider
  // );
  // const [name, version, strategyManagerAddr] =
  //   await evmManager.strategyIdToMetadata(Number(strategyId));
  const strategyManager = new ethers.Contract(
    strategyAddress,
    EvmStrategyContractAbi[evmChainId],
    rpcProvider
  );
  const vaultLib = new ethers.Contract(
    EvmVaultLibContractAddress[evmChainId],
    EvmVaultLibContractAbi[evmChainId],
    rpcProvider
  );
  const contractInfo = await strategyManager.contractInfo();
  const [capacity, maxDeposit, maxWithdraw] =
    (await strategyManager.vaultLimits()).map((x: BigNumber) => utils.formatUnits(x, stableToken.decimals));
  const strategyInfo: StrategyInfo = {
    ...strategy,
    oracleAddress: contractInfo[2],
    leverage: (await strategyManager.leverageLevel()).div(1e4).toString(),
    TVL: (await strategyManager.getCollateralETHValue())
      .div(
        (
          await vaultLib.getETHPx(
            contractInfo[2],
            StablecoinMap.usdc.tokenContractAddress
          )
        )
          .mul(1e6)
          .div(BigNumber.from(2).pow(112))
      )
      .toNumber()
      .toFixed(2),

    TVLInUnits: Number(utils.formatUnits(await strategyManager.getCollateralETHValue(), 18)),
    capacity,
    maxDeposit,
    maxWithdraw,

    apy: null,

    priceMovementThreshold: Number(
      await strategyManager.deltaThreshold() * 4 / 100
    ),
    totalEquityETHValue: Number(
      utils.formatUnits(await strategyManager.getEquityETHValue(), 18)
    ),
    debts: await vaultLib.getDebtAmounts(
      avax_HOMORA.HOMORA_BANK_ADDRESS,
      await strategyManager.homoraPosId(),
      strategy.tokens[0].tokenContractAddress,
      strategy.tokens[1].tokenContractAddress
    ),
    tokenATotokenB: Number(
      utils.formatUnits(await strategyManager.getTokenETHValue(strategy.tokens[0].tokenContractAddress, utils.parseUnits("1", strategy.tokens[0].decimals)), 18)
    ),
  };
  return strategyInfo;
};
export const getStrategyInfo = async (strategy: Strategy) => {
  const { strategyChainId } = strategy;
  const chain: Blockchain = WormholeChainIdToBlockchainMap[strategyChainId];
  const chainType: BlockchainType = BlockchainTypeMap[chain];
  if (chainType === BlockchainType.EVM) {
    return await _getEvmStrategyInfo(strategy);
  }
};

