import React from 'react';
import { Box, Grid } from '@mui/material';
import { Modal } from '../../../../';
import { Tooltip } from '../../../Tooltip';

interface DropdownInfoBoxProps {
  title: string;
  tooltip?: JSX.Element;
  viewDetails?: { buttonText: string; modalChildren: JSX.Element };
  children: JSX.Element;
  last?: boolean;
  first?: boolean;
}

export function DropdownInfoBox(props: DropdownInfoBoxProps): JSX.Element {
  const [open, setOpen] = React.useState(false);
  const handleOpen = () => {
    setOpen(true);
  };
  const handleClose = () => {
    setOpen(false);
  };
  return (
    <div style={{ height: '100%', width: '100%' }}>
      <Grid
        container
        item
        justifyContent="space-between"
        sx={{ background: '#444444' }}
        className="dropdown-bar"
      >
        <Grid mx={2} item style={{ margin: '0px' }}>
          <span>
            <div className="subtitle-2">{props.title}</div>
            {props.tooltip && <Tooltip>{props.tooltip}</Tooltip>}
          </span>
        </Grid>
        {props.viewDetails && (
          <Grid my={'auto'} mx={2} item style={{ margin: '0px' }}>
            <Modal
              modalClassName="modal-pdn-strategy-details"
              trigger={
                <span className="view-details button-4">
                  {props.viewDetails.buttonText}
                </span>
              }
            >
              {props.viewDetails.modalChildren}
            </Modal>
          </Grid>
        )}
      </Grid>
      {props.children}
    </div>
  );
}
