import { IStore, useStore } from '../../store';
import textContext from '../../textContext';
import { useLocation, useNavigate } from 'react-router-dom';
import { anchor, terra, ust } from '../../assets/images/icons';
import { getVaulName } from '../../helpers/utilities';

import './styles/Invest.scss';
// Comps
// import Filter from '../layout/Filter';
import TableAssets from './TableAssets';
import TableLoader from './TableLoader';

// Material
import Grid from '@mui/material/Grid';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import ExpandLessIcon from '@mui/icons-material/ExpandLess';
import { StrategyInfo, StrategyInfoMap } from '../../constants/strategy';
import { TokenInfo } from '../../constants/tokenConfig';
import { useOpenModal } from '../../store/base/application/hooks';
import { ModalType } from '../../store/base/AppSlice';
import { Box, Button } from '../v1';
import { useMediaQuery } from '@mui/material';
import { IEvmProvider } from '../../store/provider/Evm/EvmProvider';
import { PositionInfo } from '../../types';
import { PositionInfoMap } from '../../constants/position';
import { BN } from '../../helpers/utilities';
import { useFetchEvmPositionInfo } from '../../features/positions/hooks';

interface ITableProps {
  reverse: Function;
  click: (s: string) => void | React.Dispatch<React.SetStateAction<undefined>>;
  text: { text: string }[];
}

interface Icon {
  type: string;
  asc: boolean;
}

const Table = (props: any) => {
  const [sort, setSort] = React.useState<
    '' | 'vault' | 'leverage' | 'tvl' | 'capacity' | 'apy' | 'holdings'
  >('');
  const ArowDown = (props: any) => (
    <span className={`${props.active ? 'active' : ''} sorting-icon`}>
      {' '}
      &#x25bc;{' '}
    </span>
  );

  const UpdateSort = (sortType: string) => {
    sort === sortType ? setSort('') : setSort(sortType);
    props.onSelect!(sortType);
  };


  return (
    <Grid
      className="tableItems"
      justifyContent="space-between"
      container
      style={{ padding: '0px 16px', alignItems: 'center' }}
    >
      <Grid item md={2.8}>
        <div className="subtitle-2" onClick={() => UpdateSort('vault')}>
          Vault <ArowDown active={sort === 'vault'} />
        </div>
      </Grid>
      <Grid item md={1.5}>
        <div className="subtitle-2" onClick={() => UpdateSort('leverage')}>
          Leverage <ArowDown active={sort === 'leverage'} />
        </div>
      </Grid>
      <Grid item md={3}>
        <span className="subtitle-2" onClick={() => UpdateSort('tvl')}>
          {' '}
          TVL <ArowDown active={sort === 'tvl'} />
        </span>
        {' / '}
        <span className="subtitle-2" onClick={() => UpdateSort('capacity')}>
          Capacity <ArowDown active={sort === 'capacity'} />
        </span>
      </Grid>
      <Grid item md={1.3}>
        <div className="subtitle-2" onClick={() => UpdateSort('apy')}>
          APY <ArowDown active={sort === 'apy'} />
        </div>
      </Grid>
      <Grid item md={1.8}>
        <div className="subtitle-2" onClick={() => UpdateSort('holdings')}>
          My holdings <ArowDown active={sort === 'holdings'} />
        </div>
      </Grid>
      <Grid item md={1.6}>
        <div className="subtitle-2">Action</div>
      </Grid>
    </Grid>
  );
};

interface IInvestProps {
  strategies: StrategyInfoMap;
  search: string;
  filters: { text: string }[];
  title: string;
  description: string;
}

export default function Invest(props: IInvestProps) {
  const isPC = useMediaQuery('(min-width:1200px)');

  const [expand, setExpand] = React.useState<boolean>(false);
  const [show, setShow] = React.useState<boolean>(false);
  const { language } = useStore((state) => state.appSlice);
  const text =
    textContext[language as keyof object]['investment' as keyof object][
      'invest' as keyof object
    ];

  const { strategies, search } = props;
  const [data, setData] = React.useState<StrategyInfo[]>([]);
  React.useEffect(() => {
    if (strategies) {
      setData(Object.values(strategies));
      // return setData([])
    }
  }, [strategies]);
  const [sort, setSort] = React.useState<
    '' | 'vault' | 'leverage' | 'tvl' | 'capacity' | 'apy' | 'holdings'
  >('');
  const [reverse, setReverse] = React.useState<boolean>(false);
  const [open, setOpen] = React.useState<boolean>(false);
  const [isOpen, setIsOpen] = React.useState<boolean>(false);
  const [def, setDefault] = React.useState<StrategyInfo | null>(null);
  const [slice, handleSlice] = useState<number>(
    data && data.length !== 0 ? (data.length < 10 ? data.length : 10) : 10
  );

  const handleClose = () => {
    // setOpen(false);
    navigate('/strategy');
  };

  const { pathname } = useLocation();
  const navigate = useNavigate();
  const openInvestModal = useOpenModal(ModalType.INVEST, {
    invest: true,
    data: def,
    img1: terra,
    img2: anchor,
    img: ust,
    token: def?.strategyId,
  });
  // const { data: positionInfo } = useFetchEvmpositionInfo();
  const { data: positionInfo } = useFetchEvmPositionInfo();


  React.useEffect(() => {
    const checker = (val: string) => {
      let newVal = val.split('&=');
      // default, setDefault
      let fil = data.filter((d) => d.strategyId === newVal[1]);
      setDefault(fil[0]);
      if (newVal.length === 2 && fil.length !== 0) {
        !isOpen && openInvestModal();
      }
    };
    pathname.includes('/strategy/asset') && checker(pathname);
  }, [data]); // eslint-disable-line react-hooks/exhaustive-deps

  const handleSort = (a: StrategyInfo, b: StrategyInfo) => {
    const getA = !reverse ? a : b;
    const getB = !reverse ? b : a;

    const poolSort = () => (getB.tokens[0].name < getA.tokens[0].name ? -1 : 1);
    const leverageSort = () => getB.leverage - getA.leverage;
    const tvlSort = () => getB.TVL - getA.TVL;
    const capacitySort = () => getB.capacity - getA.capacity;
    const apySort = () => (getB.apy && getA.apy ? getB.apy - getA.apy : 0);
    const holdingsSort = () =>
      positionInfo
        ? BN(positionInfo[getB.strategyId]?.equity ?? 0)
            .minus(positionInfo[getA.strategyId]?.equity ?? 0)
            .toNumber()
        : 0;

    switch (sort) {
      case 'vault':
        return poolSort();
      case 'leverage':
        return leverageSort();
      case 'tvl':
        return tvlSort();
      case 'capacity':
        return capacitySort();
      case 'apy':
        return apySort();
      case 'holdings':
        return holdingsSort();
      default:
        return poolSort();
    }
  };

  const loaders = new Array(6).fill('');

  const viewMore = () => {
    if (slice + 10 <= data.length) handleSlice(slice + 10);
    else handleSlice(data.length);
  };

  const GoTo = () => (
    <a
      href={
        'https://docs.aperture.finance/docs/aperture-originals/crab-market-leveraged-farming'
      }
      target="_blank"
      rel="noreferrer"
      className="goTo"
    >
      <svg width="22.5" height="22.5" viewBox="0 0 23 23" fill="none">
        <path
          d="M1.875 4.6875C1.875 3.94158 2.17132 3.22621 2.69876 2.69876C3.22621 2.17132 3.94158 1.875 4.6875 1.875H8.86875C9.11739 1.875 9.35585 1.77623 9.53166 1.60041C9.70748 1.4246 9.80625 1.18614 9.80625 0.9375C9.80625 0.68886 9.70748 0.450403 9.53166 0.274588C9.35585 0.0987722 9.11739 0 8.86875 0H4.6875C3.4443 0 2.25201 0.49386 1.37294 1.37294C0.49386 2.25201 0 3.4443 0 4.6875V17.8125C0 19.0557 0.49386 20.248 1.37294 21.1271C2.25201 22.0061 3.4443 22.5 4.6875 22.5H17.8125C19.0557 22.5 20.248 22.0061 21.1271 21.1271C22.0061 20.248 22.5 19.0557 22.5 17.8125V13.6313C22.5 13.3826 22.4012 13.1442 22.2254 12.9683C22.0496 12.7925 21.8111 12.6938 21.5625 12.6938C21.3139 12.6938 21.0754 12.7925 20.8996 12.9683C20.7238 13.1442 20.625 13.3826 20.625 13.6313V17.8125C20.625 18.5584 20.3287 19.2738 19.8012 19.8012C19.2738 20.3287 18.5584 20.625 17.8125 20.625H11.25V14.5688C11.25 13.6886 10.9003 12.8444 10.278 12.222C9.65557 11.5997 8.81144 11.25 7.93125 11.25H1.875V4.6875ZM1.875 13.125H7.93125C8.31416 13.125 8.68138 13.2771 8.95214 13.5479C9.22289 13.8186 9.375 14.1858 9.375 14.5688V20.625H4.6875C3.94158 20.625 3.22621 20.3287 2.69876 19.8012C2.17132 19.2738 1.875 18.5584 1.875 17.8125V13.125ZM12.6938 0.9375C12.6938 0.68886 12.7925 0.450403 12.9683 0.274588C13.1442 0.0987722 13.3826 0 13.6313 0H21.5625C21.8111 0 22.0496 0.0987722 22.2254 0.274588C22.4012 0.450403 22.5 0.68886 22.5 0.9375V8.86875C22.5 9.11739 22.4012 9.35585 22.2254 9.53166C22.0496 9.70748 21.8111 9.80625 21.5625 9.80625C21.3139 9.80625 21.0754 9.70748 20.8996 9.53166C20.7238 9.35585 20.625 9.11739 20.625 8.86875V3.2025L14.2931 9.5325C14.2066 9.62204 14.1032 9.69346 13.9888 9.74259C13.8744 9.79173 13.7514 9.81759 13.6269 9.81867C13.5025 9.81975 13.379 9.79603 13.2638 9.7489C13.1486 9.70176 13.0439 9.63214 12.9559 9.54412C12.8679 9.4561 12.7982 9.35142 12.7511 9.23621C12.704 9.12099 12.6802 8.99754 12.6813 8.87306C12.6824 8.74858 12.7083 8.62556 12.7574 8.51118C12.8065 8.3968 12.878 8.29336 12.9675 8.20687L19.2994 1.875H13.6313C13.3826 1.875 13.1442 1.77623 12.9683 1.60041C12.7925 1.4246 12.6938 1.18614 12.6938 0.9375Z"
          fill="white"
        />
      </svg>
    </a>
  );

  return (
    <React.Fragment>
      <Box className="investContainer">
        <GoTo />
        <div className={`title ${isPC ? 'headline-2' : 'headline-3'}`}>
          {' '}
          {props.title}{' '}
        </div>
        <div className="description body-1" style={{ marginTop: '16px' }}>
          {props.description}
        </div>

        <div className="investTable">
          <div className="tableHeaders">
            {isPC && (
              <Table
                onSelect={(data: any) => {
                  setSort(data);
                  if (data === sort) setReverse(!reverse);
                }}
              />
            )}

            {data && data.length !== 0
              ? data
                  .sort(handleSort)
                  .filter((fil) =>
                    getVaulName(fil.tokens)
                      .toLowerCase()
                      .includes(search.toLowerCase())
                  )
                  .map((item, i) => {
                    const title = getVaulName(item.tokens);
                    return (
                      <TableAssets
                        isOpen={setIsOpen}
                        title={title}
                        positionInfo={positionInfo?.[item.strategyId] ?? null}
                        strategyInfo={item}
                        key={i}
                      />
                    );
                  })
                  .slice(0, slice)
              : loaders.map((d, i) => <TableLoader key={i} />)}
          </div>
        </div>

        {data && data.length !== 0 && slice < data.length && (
          <div className="view-more">
            <Button
              variant="outline"
              size="small"
              text={'view more'}
              onClick={viewMore}
              style={{ padding: '0px 23px' }}
            />
          </div>
        )}
      </Box>
    </React.Fragment>
  );
}
