import { theme, IThemeColors } from '../../styles/theme';
import { createTheme, Theme } from '@mui/material/styles';
import { GetSelf, IStore, SetSelf, SetStore, SliceCreator } from '..';

export interface IThemeSlice {
  muiTheme: Theme;
  palette: IThemeColors;
  themeSelect: (t: string) => void;
  assetImg: (title: string) => string;
}

export const themeSlice: SliceCreator<IThemeSlice> = (
  set,
  get,
  api,
  setSelf,
  getSelf
) => ({
  palette: theme.colors.dark,
  muiTheme: createTheme(),
  themeUpdate: (t: string) => {
    // Update state of tguage in application and local storage
    set(
      (state: any) =>
        (state.themeSlice.palette = theme.colors[t as keyof object])
    );
  },
  themeSelect: (t: string) => {
    const { themeUpdate } = get().themeSlice;
    switch (t) {
      case 'light':
        themeUpdate('light');
        break;
      case 'dark':
        themeUpdate('dark');
        break;
      default:
        themeUpdate('light');
    }
  },
  assetImg: (title: string) => {
    const { palette } = get().themeSlice;
    if (!title) return '';
    let img =
      title && palette.name === 'light'
        ? `https://aperture-image-assets.s3.us-west-2.amazonaws.com/masset_icons/${title.substring(
            1
          )}.png`
        : `https://aperture-image-assets.s3.us-west-2.amazonaws.com/masset_icons_dark/${title.substring(
            1
          )}.png`;
    return img;
  },
});
