import { forwardRef, useRef, useState, useEffect } from 'react';
import { Box, NumberCounter } from '../../../../';
import { numberToPercentage } from '../../../../../../helpers/utilities';
import { LineGraph } from '../../../../Charts/LineGraph';
import './index.scss';

interface ReturnProps {
  color: any;
  data: any;
  percentage: any;
  payload: any;
  token: any;
}

export const Return = forwardRef<HTMLDivElement, ReturnProps>(
  (
    {
      color,
      data = { week: [], month: [], inception: [] },
      percentage = { week: 0, month: 0, inception: 0, weekChangeRate: 0 },
      payload,
      token,
      ...props
    },
    ref
  ) => {
    const [timeRage, setTimeRage] = useState<'week' | 'month' | 'inception'>(
      'week'
    );
    const [width, setWidth] = useState(0);
    const parentRef = useRef(null);

    useEffect(() => {
      parentRef.current && setWidth(parentRef.current!.offsetWidth);
      // console.log(parentRef.current!.offsetWidth);
    }, [parentRef]);

    const ButtonGroups = () => (
      <div className="selectedButtonList button-3">
        <div
          className={
            'selectebutton ' +
            (timeRage === 'week' ? 'selectedbutton' : 'unselectedbutton')
          }
          onClick={() => setTimeRage('week')}
        >
          1W
        </div>
        <div
          className={
            'selectebutton ' +
            (timeRage === 'month' ? 'selectedbutton' : 'unselectedbutton')
          }
          onClick={() => setTimeRage('month')}
        >
          1M
        </div>
        <div
          className={
            'selectebutton ' +
            (timeRage === 'inception' ? 'selectedbutton' : 'unselectedbutton')
          }
          onClick={() => setTimeRage('inception')}
        >
          ALL
        </div>
      </div>
    );

    return (
      <Box ref={parentRef} {...props} className="back-test-anlysis">
        <div style={{ position: 'relative' }}>
          <ButtonGroups />
        </div>
        <div className="headline-4 semiBold">Return Chart</div>
        <div className="headline-5" style={{ marginBottom: '33px' }}>
          1 Week:
          <span
            className={`span-1${
              percentage.week > 0 ? ' positive' : ' negative'
            }`}
          >
            <NumberCounter
              value={percentage.week}
              formatNumberFunction={(value: number) =>
                `${value > 0 ? '+' : ''}${numberToPercentage(value)}`
              }
            />
            <NumberCounter
              value={percentage.weekChangeRate}
              formatNumberFunction={(value: number) =>
                `(${numberToPercentage(value)})`
              }
            />
          </span>
          1 Month:
          <span
            className={`span-1${
              percentage.month > 0 ? ' positive' : ' negative'
            }`}
          >
            <NumberCounter
              value={percentage.month}
              formatNumberFunction={(value: number) =>
                numberToPercentage(value)
              }
            />
          </span>
          Inception:
          <span
            className={`span-1${
              percentage.inception > 0 ? ' positive' : ' negative'
            }`}
          >
            <NumberCounter
              value={percentage.inception}
              formatNumberFunction={(value: number) =>
                numberToPercentage(value)
              }
            />
          </span>
        </div>
        <div className="graph-title">
          <div className="subtitle-2">Return(%)</div>
          <div className="headline-5 float-right">{token} Price($)</div>
        </div>

        <LineGraph
          color={color}
          data={data[timeRage]}
          referenceAmount={0}
          type="full"
          legend
          payload={payload}
          token={token}
          lineCount={2}
        />
      </Box>
    );
  }
);
