import './index.scss';
import { NumberCounter } from '../../../../../';
import Grid from '@mui/material/Grid';
import {
  numberParse,
  numberParseLarge,
  numberToPercentage,
} from '../../../../../../../helpers/utilities';

interface OverallInfoProps {
  holdings: number;
  //yields: number;
  apy: number | null;
  leverage: number;
  date: string;
  tvl: number;
  capacity: number;
  threshold: number;
  pool: any;
  token: string;
}
export const OverallInfo = ({
  holdings = 0,
  //yields = 0,
  apy = 0,
  leverage = 0,
  date = '',
  tvl = 0,
  capacity = 0,
  threshold = 0,
  pool = <></>,
  token = '',
}: OverallInfoProps) => {
  const percentage = tvl >= 0 && capacity > 0 ? tvl / capacity : 0;

  const ProgressBar = () => (
    <div style={{ position: 'relative' }}>
      {tvl >= 0 ? (
        <NumberCounter
          value={tvl}
          formatNumberFunction={(value: number) =>
            `$${numberParseLarge(value, 2)}`
          }
        />
      ) : (
        '-'
      )}{' '}
      /
      {capacity >= 0 ? (
        <NumberCounter
          value={capacity}
          formatNumberFunction={(value: number) => numberParseLarge(value, 2)}
        />
      ) : (
        '-'
      )}{' '}
      <div className="overall-bar-percentage">
        {capacity > 0 ? (
          <NumberCounter
            value={tvl / capacity}
            formatNumberFunction={(value: number) => numberToPercentage(value)}
          />
        ) : (
          '-'
        )}
      </div>
      <div
        className="overall-bar"
        style={{
          display: 'grid',
          marginBottom: '24px',
          gridTemplateColumns:
            tvl > 0 && percentage > 0.0001
              ? percentage > 0.01
                ? `${percentage * 100}% ${(1 - percentage) * 100}%`
                : '1% 99%'
              : `100%`,
        }}
      >
        {tvl > 0 && percentage > 0.0001 && <div className="bar-left" />}
        {tvl < capacity && <div className="bar-right" />}
      </div>
    </div>
  );

  const getDate = () => {
    const data = new Date(date * 1000).toUTCString().split(' ');
    return `${data[1]} ${data[2]} ${data[3]}`;
  };
  return (
    <Grid container className="overrall-info-container">
      <Grid item container className="data-grid-left" direction={'column'}>
        <Grid item>
          <div className="overallInfo-title subtitle-2">My holdings</div>
          <div className="overallInfo-content headline-3">
            {Number.isNaN(holdings) ? (
              '$--'
            ) : (
              <NumberCounter
                value={holdings}
                formatNumberFunction={(value: number) =>
                  '$' + numberParseLarge(value, 2)
                }
              />
            )}
          </div>
        </Grid>
        {/* <Grid item>
          <div className="overallInfo-title subtitle-2">Yields</div>
          <div
            className={`overallInfo-content headline-5 ${yields > 0 ? 'positive' : yields < 0 ? 'negative' : ''
              }`}
          >
            {holdings ? (
              <>
                <NumberCounter
                  value={holdings * (1 + yields)}
                  formatNumberFunction={(value: number) =>
                    numberParse(value, 'currency')
                  }
                />
                <NumberCounter
                  value={yields}
                  formatNumberFunction={(value: number) =>
                    `(${numberToPercentage(value)})`
                  }
                />
              </>
            ) : (
              '$--'
            )}
          </div>
        </Grid> */}
      </Grid>
      <Grid item container className="data-grid-right">
        <Grid item container direction={'column'}>
          <Grid item>
            <div className="overallInfo-title subtitle-2">TVL / Capacity</div>
            <div className="overallInfo-content headline-5">
              <ProgressBar />
            </div>
          </Grid>
          <Grid item>
            <div className="overallInfo-title subtitle-2">
              Underlying Farming Pool
            </div>
            <div className="overallInfo-content headline-5">{pool}</div>
          </Grid>
        </Grid>
        <Grid item container direction={'column'}>
          <Grid item xl={3} lg={3} md={3}>
            <div className="overallInfo-title subtitle-2">APY</div>
            <div className="overallInfo-content headline-5">
              {apy !== null ? (
                <NumberCounter
                  value={apy}
                  formatNumberFunction={(value: number) =>
                    numberToPercentage(value)
                  }
                />
              ) : (
                '-'
              )}
            </div>
          </Grid>
          <Grid item xl={3} lg={3} md={3}>
            <div className="overallInfo-title subtitle-2">Target Leverage</div>
            <div className="overallInfo-content headline-5">
              {leverage ? (
                <NumberCounter
                  value={leverage}
                  formatNumberFunction={(value: number) => `${value}X`}
                />
              ) : (
                '-'
              )}
            </div>
          </Grid>
        </Grid>
        <Grid item container direction={'column'}>
          <Grid item>
            <div className="overallInfo-title subtitle-2">
              {' '}
              Vault Inception Date
            </div>
            <div className="overallInfo-content headline-5">{getDate()}</div>
          </Grid>
          <Grid item>
            <div className="overallInfo-title subtitle-2">
              {`Price Movement Rebalance Threshold`}
            </div>
            <div className="overallInfo-content headline-5">
              {threshold !== null && threshold !== undefined ? (
                <NumberCounter
                  value={threshold}
                  formatNumberFunction={(value: number) =>
                    numberToPercentage(value)
                  }
                />
              ) : (
                '-'
              )}
            </div>
          </Grid>
        </Grid>
      </Grid>
    </Grid>
  );
};
