import {
  BlockchainType,
  Blockchain,
  StrategyType,
  InvestType,
} from '../../constants/crosschain';
import {
  CrosschainTransactionStatus,
  SamechainTransactionStatus,
} from '../../constants/base/transaction';
import IChainProvider from '../provider/IChainProvider';
import { WithdrawlParams, InvestParams } from '../provider/IChainProvider';
import { TransactionResponse } from '@ethersproject/providers';
// import { TransactionType } from '../provider/transaction/types';
import { BN, deepCopy } from '../../helpers/utilities';
import { ModalType, PopupType } from '../base/AppSlice';
import { SliceCreator } from '..';

export interface ITransaction {
  initSubscription: () => void;
  providerChainType?: BlockchainType;
  status: CrosschainTransactionStatus | SamechainTransactionStatus;
  transactionPayload: InvestParams;
  response: any;
  error?: any;
  timestamp: number;
  hash?: string;
}
export interface ITransactionManagerSlice {
  initSubscription: () => void;

  pendingTransactions: {
    [chainType: string]: {
      [chainId: string]: {
        [key: string]: ITransaction;
      };
    };
  };
  completedTransactions: {
    [chainType: string]: {
      [chainId: string]: {
        [key: string]: ITransaction;
      };
    };
  };
  failedTransactions: {
    [chainType: string]: {
      [chainId: string]: {
        [key: string]: ITransaction;
      };
    };
  };
  addPendingTransaction: (key: string, txn: ITransaction) => void;
  removeAllTransactions: (chainType: BlockchainType, chainId: string) => void;

  //TODO: Reimplment these functions.
  // setStatus: (key: string, status: any) => void;
  // setPayload: (key: string, payload: any) => void;
  // setError: (key: string, error: string) => void;
  // removePendingTransaction: (key: string) => void;
}

/**
 * Adds a pending transaction to the transaction manager.
 * @param {string} key - the key of the transaction.
 * @param {ITransaction} txn - the transaction to add.
 * @returns None
 */
export const useTransactionManagerSlice: SliceCreator<
  ITransactionManagerSlice
> = (set, get, api, setSelf, getSelf) => {
  return {
    pendingTransactions: {},
    completedTransactions: {},
    failedTransactions: {},

    addPendingTransaction(key: string, txn: ITransaction) {
      const { chainType, chainId } = get().chainSlice;
      if (chainType === null || chainId === null) return;
      set((state: any) => {
        let { pendingTransactions } = state.transactionManagerSlice;
        pendingTransactions[chainType] === undefined &&
          (pendingTransactions[chainType] = {});
        pendingTransactions[chainType][chainId] === undefined &&
          (pendingTransactions[chainType][chainId] = {});
        pendingTransactions[chainType][chainId][key] = txn;
      });
    },
    // TODO: Cleanup logic
    removeAllTransactions(chainType: BlockchainType, chainId: string) {
      set((state: any) => {
        let { pendingTransactions, completedTransactions, failedTransactions } =
          state.transactionManagerSlice;
        pendingTransactions[chainType] === undefined &&
          (pendingTransactions[chainType] = {});
        pendingTransactions[chainType][chainId] === undefined &&
          (pendingTransactions[chainType][chainId] = {});
        pendingTransactions[chainType][chainId] = {};
        completedTransactions[chainType] === undefined &&
          (completedTransactions[chainType] = {});
        completedTransactions[chainType][chainId] === undefined &&
          (completedTransactions[chainType][chainId] = {});
        completedTransactions[chainType][chainId] = {};
        failedTransactions[chainType] === undefined &&
          (failedTransactions[chainType] = {});
        failedTransactions[chainType][chainId] === undefined &&
          (failedTransactions[chainType][chainId] = {});
        failedTransactions[chainType][chainId] = {};
      });
    },
    initSubscription() {
      api.subscribe(
        (state: any) => state.transactionManagerSlice.pendingTransactions,
        (
          pending: {
            [chainType: string]: {
              [chainId: string]: {
                [key: string]: ITransaction;
              };
            };
          },
          prevPending: {
            [chainType: string]: {
              [chainId: string]: {
                [key: string]: ITransaction;
              };
            };
          }
        ) => {
          const { addPopup } = get().appSlice;
          const { chainType, chainId, chainProvider, transactionProvider } =
            get().chainSlice;
          if (
            chainProvider === null ||
            transactionProvider === null ||
            chainType === null ||
            chainId === null
          )
            return;
          const { addTransaction } = transactionProvider;
          if (!pending[chainType] || !pending[chainType][chainId]) return;

          const changedTxns = Object.entries(
            pending[chainType][chainId]
          ).filter(
            ([k, v]) =>
              prevPending?.[chainType]?.[chainId]?.[k] === undefined ||
              prevPending?.[chainType]?.[chainId]?.[k]?.status !== v.status
          );
          changedTxns.forEach(async ([k, v]) => {
            // When providerChain is different than strategyChain, we know it's a crosschain transaction.
            // if (
            //   v.transactionPayload.providerChain !==
            //   v.transactionPayload.strategyChain
            // ) {
            // const status = v.status;
            // switch (status) {
            //   // TODO: Implement crosschain transaction status handling.
            //   case 'OPENED':
            //     // deltaNeutralGetVAA(k, v);
            //     break;
            //   case 'GOT_VAA':
            //     // _checkVAAs(k, v);
            //     break;
            //   case 'CHECK_VAA':
            //   // _checkVAAs(k, v);

            //   case 'CLOSED':
            //   default:
            // }
            // } else {
            // When providerChain is the same as strategyChain, it's a samechain transaction.
            const status = v.status;

            const { setOpenModal } = get().appSlice;
            switch (status) {
              case 'INIT':
                try {
                  const modalProps = {
                    isInvest: v.transactionPayload.type !== InvestType.DECREASE,
                    amount:
                      v.transactionPayload.type !== InvestType.DECREASE
                        ? v.transactionPayload.amount
                        : (Number(v.transactionPayload.positionInfo?.equity) ??
                            0) * v.transactionPayload.amount,
                    currency: v.transactionPayload.token.ticker,
                  };
                  setOpenModal(ModalType.CONFIRMATION, modalProps);
                  const res: TransactionResponse = await chainProvider.invest(
                    v.transactionPayload
                  );
                  const { from, hash } = res;
                  addTransaction({ chainId, from, hash, info: res });
                  set((state: any) => {
                    const { pendingTransactions } =
                      state.transactionManagerSlice;
                    // Deep copy the transaction so that the original transaction is not modified.
                    const txn = deepCopy(
                      pendingTransactions[chainType][chainId][k]
                    );
                    delete pendingTransactions[chainType][chainId][k];
                    pendingTransactions[chainType][chainId][hash] = txn;
                    pendingTransactions[chainType][chainId][hash].status =
                      'OPENED';
                    pendingTransactions[chainType][chainId][
                      hash
                    ].response = res;
                    pendingTransactions[chainType][chainId][hash].hash = hash;
                    pendingTransactions[chainType][chainId][hash].timestamp =
                      new Date().getTime();
                  });
                  // addPopup({ txn: { hash, status: 'OPENED' } }, hash, 5000);
                  addPopup(
                    {
                      [PopupType.TXN]: {
                        hash,
                        status: 'OPENED',
                      },
                    },
                    hash,
                    5000
                  );
                  setOpenModal(ModalType.TXN_SUBMITTED);
                } catch (err) {
                  const txn = {
                    ...get().transactionManagerSlice.pendingTransactions[
                      chainType
                    ][chainId][k],
                  };
                  txn.status = 'ERROR';
                  txn.error = err;
                  set((state: any) => {
                    const { pendingTransactions, failedTransactions } =
                      state.transactionManagerSlice;
                    delete pendingTransactions[chainType][chainId][k];
                    failedTransactions[chainType] === undefined &&
                      (failedTransactions[chainType] = {});
                    failedTransactions[chainType][chainId] === undefined &&
                      (failedTransactions[chainType][chainId] = {});
                    failedTransactions[chainType][chainId][k] = txn;
                    failedTransactions[chainType][chainId][k].timestamp =
                      new Date().getTime();
                  });
                  console.log(err);
                  setOpenModal(ModalType.TXN_REJECTED);
                  //addPopup({ txn: { k, status: 'ERROR' } }, k, 9999999);
                  addPopup(
                    {
                      [PopupType.ERROR]: {
                        title: err.title,
                        msg: err.reason,
                      },
                    },
                    k,
                    5000
                  );
                }
                break;
              case 'CLOSED':
                set((state: any) => {
                  const { pendingTransactions, completedTransactions } =
                    state.transactionManagerSlice;
                  const txn = pendingTransactions[chainType][chainId][k];
                  delete pendingTransactions[chainType][chainId][k];
                  completedTransactions[chainType] === undefined &&
                    (completedTransactions[chainType] = {});
                  completedTransactions[chainType][chainId] === undefined &&
                    (completedTransactions[chainType][chainId] = {});
                  completedTransactions[chainType][chainId][k] = txn;
                  completedTransactions[chainType][chainId][k].timestamp =
                    new Date().getTime();
                });
                break;
            }
            // }
          });
        },
        { fireImmediately: true }
      );
    },

    // processPendingTransaction(key: string, fn: Function, cb: Function) {
    //   get().
    // }
  };
};
