import { ProtocolInfo } from '../../../../../../constants/protocolConfig';
import { DetailsDropdown } from '../../../DetailsDropdown';
import { BacktestAnalysis, Info, Return } from '../DropdownInfo/PDN';
import { PdnStrategyDetails } from '../PdnStrategyDetails';
import { StrategyInfo } from '../../../../../../constants/strategy/index';
import { Grid } from '@mui/material';
import useGraph from '../../../../../../hooks/graph/useGraph';
import { find_x_days_ago, getReturnData } from '../../../../../../helpers/graph';
import { PositionInfo } from '../../../../../../types';

interface PdnDropdownProps {
  protocols?: ProtocolInfo[];
  vaultTitle?: string;
  accordionClose: any;
  strategyInfo: StrategyInfo;
  positionInfo: PositionInfo;
}

export const PdnDropdown = ({
  accordionClose,
  protocols = [],
  vaultTitle = '',
  strategyInfo,
  positionInfo,
}: PdnDropdownProps) => {
  const mockProps = {
    threshold:
      strategyInfo.priceMovementThreshold / strategyInfo.leverage / 100,
    // sharpeRation: strategyInfo.apy,
    leverage: strategyInfo.leverage,
    modalChildren: (
      <PdnStrategyDetails
        positionInfo={positionInfo}
        strategyInfo={strategyInfo}
        vaultTitle={vaultTitle}
      />
    ),
  };

  const { assetPrices, homoraPerformace, backtestAnalysisData } = useGraph();

  const returns = {
    week: find_x_days_ago(getReturnData(assetPrices, homoraPerformace, strategyInfo.tokens[0].ticker, strategyInfo.tokens[1].ticker), 7),
    month: find_x_days_ago(getReturnData(assetPrices, homoraPerformace, strategyInfo.tokens[0].ticker, strategyInfo.tokens[1].ticker), 30),
    inception: getReturnData(assetPrices, homoraPerformace, strategyInfo.tokens[0].ticker, strategyInfo.tokens[1].ticker),
  };

  return (
    <DetailsDropdown>
      <Grid container>
        <Grid item xl={2.9} lg={2.9} md={2.9}>
          <Info {...{ ...mockProps, protocols, vaultTitle }} />
        </Grid>
        <Grid item xl={4.55} lg={4.55} md={4.55} sx={{ paddingLeft: '2px' }}>
          <Return {...{ ...mockProps, strategyInfo, vaultTitle, returns }} />
        </Grid>
        <Grid item xl={4.55} lg={4.55} md={4.55} sx={{ paddingLeft: '2px' }}>
          <BacktestAnalysis
            {...{
              ...mockProps,
              strategyInfo,
              vaultTitle,
              backtestAnalysisData,
            }}
          />
        </Grid>
      </Grid>
    </DetailsDropdown>
  );
};
