export const approveERC20: (
  tokenAddr: string,
  amount: number,
  signer: ethers.Signer,
  evmManager: ethers.Contract
) => Promise<TransactionResponse> = async (
  tokenAddr: string,
  amount: number,
  signer: ethers.Signer,
  evmManager: ethers.Contract
) => {
  var ethUST = new ethers.Contract(tokenAddr, ERC20TokenABI, signer);
  return await ethUST.approve(evmManager.address, amount);
};

export const checkApprovalERC20 = async (
  tokenAddr: string,
  signer: ethers.Signer,
  evmManager: ethers.Contract
) => {
  var ethUST = new ethers.Contract(tokenAddr, ERC20TokenABI, signer);
  return await ethUST.allowance(signer.getAddress(), evmManager.address);
};

export const getPositions = async (
  ethManager: ethers.Contract,
  address: string
) => {
  const pos = await ethManager.getPositions(address);
  const positions: {
    positionId: string;
    strategyChainId: number;
    strategyId: string;
  }[] = await Promise.all(
    pos.map(async ([positionId]: [string]) => {
      const [, strategyChainId, strategyId] = await ethManager.positionIdToInfo(
        positionId
      );
      return {
        positionId: positionId.toString(),
        strategyChainId,
        strategyId: strategyId.toString(),
      };
    })
  );

  return positions;
};

export const getPositionsInfo = async (ethManager, posId) => {
  let result = await ethManager.GetPositionInfo(posId);
  return result;
};

export const openPosition = async (
  ethManager: ethers.Contract,
  stableTokenDepositAmount: BigNumber,
  strategyChainId: number,
  strategyId: string,
  spendingTokenAddr: string,
  stableTokenETHvalue: BigNumber,
  slippage: number,
  estimateGas?: boolean
) => {
  const minEquityETH = BigNumber.from(stableTokenDepositAmount)
    .mul(stableTokenETHvalue)
    .mul(BigNumber.from(1e4 * (1 - slippage)))
    .div(BigNumber.from(1e4));
  const minReinvestETH = BigNumber.from(0);

  let openPositionBytesArray = ethers.utils.arrayify(
    ethers.utils.defaultAbiCoder.encode(
      ['uint256', 'uint256'],
      [
        minEquityETH, // uint256 minEquityETH
        minReinvestETH, // uint256 minReinvestETH
      ]
    )
  );

  // TODO: Only needed for test chain, remove txOptions when using mainnet
  const txOptions = { gasPrice: 50000000000, gasLimit: 8500000 };
  const params = [
    strategyChainId,
    strategyId,
    [[spendingTokenAddr, stableTokenDepositAmount]],
    openPositionBytesArray,
    // txOptions,
  ];

  if (estimateGas) {
    const createPosEstimateRes = await ethManager.estimateGas.createPosition(
      ...params
    );
    return createPosEstimateRes;
  } else {
    const createPosRes = await ethManager.createPosition(...params);
    return createPosRes;
  }
};

export const getETHPx = async (
  vaultLib: ethers.Contract,
  oracleAddr: string,
  tokenAddr: string
) => {
  return (await vaultLib.getETHPx(oracleAddr, tokenAddr)).div(
    BigNumber.from(2).pow(112)
  );
};
