  console.log('withdraw amount 0: ', withdrawAmount.toString());
  const withdrawFee = 0;
  // TODO: Only needed for test chain, remove txOptions when using mainnet
  // const txOptions = { gasPrice: 27000000000, gasLimit: 4000000 }

  // var amtAMin = totalEquityEth
  //   .mul(withdrawAmount)
