  //   .div(1e4);

  // const amtAMin = totalEquity
  //   .mul(withdrawAmount.div(totalShares))
  //   .mul(1e4 - slippage)
  //   .div(BigNumber.from(1e4));
  // const minReinvest = sqrt(
  //   totalEquityEth.mul(BigNumber.from('50000000000000000'))
  // );
  // BigNumber.from(
  //   BN(totalEquity.toString()).times(BN(withdrawAmount.toString()).times(10).pow(decimals)).div(totalShares.toString()).times(1e4 *(1- slippage)).div(1e4).toString()
  // )
  let a = totalEquity.mul(withdrawAmount);
  let b = a.div(totalShares);
  let c = b.mul(BigNumber.from(10).pow(decimals));
  let d = c;
  let e = d
    .mul(1e4 - withdrawFee)
    .mul(1e4 * (1 - slippage))
