import {
  AvailableBlockchains,
  Blockchain,
  BlockchainToEvmIdMap,
  DefaultEvmChain,
  ERC20TokenABI,
  EvmIdToBlockchainMap,
  EvmManagerAddress,
  EvmManagerContractAbi,
  EvmStrategyContractAbi,
  EvmVaultLibContractAbi,
  EvmVaultLibContractAddress,
  IErrorInfo,
  InvestType,
  StrategyType,
} from '../../../constants/crosschain';
import IChainProvider from '../IChainProvider';
import { BlockchainExplorerType } from '../../../constants/crosschain';
import { ethers, Contract, utils, BigNumber } from 'ethers';
import Web3Modal from 'aperture-web3modal';
import WalletConnect from '@walletconnect/web3-provider';
import CoinbaseWalletSDK from '@coinbase/wallet-sdk';
import Torus from '@toruslabs/torus-embed';
import { NetworkType } from '../../../constants/base/network';
import { InvestParams } from '../IChainProvider';
import { TransactionResponse } from '@solana/web3.js';
import {
  EvmChainInfo,
  EvmChainMap,
} from '../../../constants/crosschain/evmChainidMap';
import {
  approveERC20,
  checkApprovalERC20,
  getETHPx,
  getPositions as evmGetPositions,
  openPosition as evmOpenPosition,
  decreasePosition as evmDecreasePosition,
  increasePosition as evmIncreasePosition,
} from '../../../helpers/crosschain';
import { TransactionDetails } from '../transaction/Evm/types';
import { strategies } from '../../../constants/strategy/strategies';
import { EvmProviderSubscriptionKeys } from '../../../constants/store';
import { IStore, SetStore } from '../..';
import { Address } from '../../../../../pancake-frontend/src/config/constants/types';
import { ITokenMap } from '../../../constants/tokenConfig';
import { WritableDraft } from 'immer/dist/internal';
import { BN, deepCopy, ethersErrorParser } from '../../../helpers/utilities';
import { EvmTokenContractAddress } from '../../../constants/crosschain/index';
import { Strategy } from '../../../constants/strategy/index';

export interface TransactionState {
  [chainId: number]: {
    [txHash: string]: TransactionDetails;
  };
}

export const transactions: TransactionState = {};

export interface IEvmProvider extends IChainProvider {
  _onConnect: (id?: string) => void;
  _web3Modal: Web3Modal;
  _setWeb3Modal: (web3Modal: Web3Modal) => void;
  _web3Provider: ethers.providers.Web3Provider | null;
  _evmManager: ethers.Contract | null;
  _evmChainInfo: EvmChainInfo;
  _switchNetwork: (network: NetworkType, chainId: number) => Promise<void>;
  _subscribeProvider: (chainId: number) => Promise<void>;
  _setEvmManager: (evmManager: ethers.Contract) => void;
  _vaultLib: ethers.Contract | null;
  _setVaultLib: (vaultLib: ethers.Contract) => void;
}

export const EvmProvider = (
  set: SetStore<IStore>,
  get: () => IStore,
  api: any,
  setSelf: (fn: (state: WritableDraft<IEvmProvider>) => void) => void,
  getSelf: () => IEvmProvider
) => {
  const { chain } = get().chainSlice;
  function getNetworkType(chainId: number) {
    return EvmChainMap[chainId].name.includes('Mainnet')
      ? NetworkType.MAINNET
      : NetworkType.TESTNET;
  }
  const getChainInfo = (chainId: number): EvmChainInfo => {
    const chainData = EvmChainMap[chainId];
    if (!chainData) {
      throw new Error('ChainId missing or not supported');
    }
    const API_KEY = process.env.REACT_APP_INFURA_ID;
    let rpc = chainData.rpc;
    if (API_KEY) {
      rpc = rpc.map((x) => {
        if (x.includes('infura.io') && x.includes('${INFURA_API_KEY}')) {
          return x.replace('${INFURA_API_KEY}', API_KEY);
        }
        return x;
      });
    }
    return chainData;
  };
  const initWeb3 = (provider: ExternalProvider | JsonRpcFetchFunc) => {
    return new ethers.providers.Web3Provider(provider);
  };
  const evmChainInfo = getChainInfo(
    chain === null ? 1 : BlockchainToEvmIdMap[chain as keyof object]
  );
  const getProviderOptions = () => {
    // const infuraId = process.env.REACT_APP_INFURA_ID;
    const providerOptions = {
      walletconnect: {
        package: WalletConnect,
        options: {
          rpc: { 43114: 'https://api.avax.network/ext/bc/C/rpc' }, // update this
          // infuraId,
        },
      },
      torus: {
        package: Torus,
      },
      coinbasewallet: {
        package: CoinbaseWalletSDK,
        options: {
          appName: 'Aperture Finance',
          // infuraId,
          rpc: { 43114: 'https://api.avax.network/ext/bc/C/rpc' },
        },
      },
    };
    return providerOptions;
  };

  const _state = {
    _web3Modal: null,
    _web3Provider: null,
    _evmManager: null,
    _evmChainInfo: evmChainInfo,
    _vaultLib: null,
  };

  const state = {
    walletAddress: null,
    balance: null,
  };

  const _actions = {
    _setVaultLib: (vaultLib: ethers.Contract) => {
      setSelf((state) => {
        state._vaultLib = vaultLib;
      });
    },
    _setWeb3Modal: (web3Modal: Web3Modal) => {
      set((state: any) => {
        state.chainSlice.chainProvider._web3Modal = web3Modal;
      });
    },
    _subscribeProvider: async (provider: any) => {
      const { _onConnect, _switchNetwork, _evmChainInfo } = getSelf();
      if (!provider.on) {
        return;
      }
      provider.on('accountsChanged', (accounts: string[]) => {
        _onConnect();
      });
      provider.on('chainChanged', async (chainId: string) => {
        // If user changes their chain on provider, check if it's supported, if not, prompt them to change it to default supported chain.
        // TODO: Maybe show a warning modal or toast message.
        const id = parseInt(chainId);
        if (
          EvmIdToBlockchainMap[chainId] === undefined ||
          EvmIdToBlockchainMap[chainId] === null
        ) {
          await provider.request({
            method: 'wallet_switchEthereumChain',
            params: [{ chainId: ethers.utils.hexlify(DefaultEvmChain) }],
          });
        }
        set((state: any) => {
          const { chainSlice, appSlice } = state;
          const { chainProvider } = chainSlice;
          chainProvider._evmChainInfo = getChainInfo(id);
          chainSlice.chain = EvmIdToBlockchainMap[chainId as keyof object];
          appSlice.networkType = getNetworkType(id);
        });
      });
    },

    // TODO: Implement network switch prompt like uniswap.
    _switchNetwork: async (provider: any, chainId: number) => {
      if (provider.networkVersion && provider.networkVersion !== chainId + '') {
        try {
          await provider.request({
            method: 'wallet_switchEthereumChain',
            params: [
              {
                chainId: ethers.utils.hexlify(
                  BlockchainToEvmIdMap[DefaultEvmChain]
                ),
              },
            ],
          });
        } catch (err: any) {
          // This error code indicates that the chain has not been added to MetaMask
          if (err.code === 4902) {
            await provider.request({
              method: 'wallet_addEthereumChain',
              params: [
                {
                  chainName: EvmChainMap[chainId].name,
                  chainId: ethers.utils.hexlify(chainId),
                  nativeCurrency: EvmChainMap[chainId].nativeCurrency,
                  rpcUrls: EvmChainMap[chainId].rpc,
                },
              ],
            });
          }
        }
      }
    },
    _onConnect: async (id?: string) => {
      const { _subscribeProvider, _web3Modal, _evmChainInfo, _switchNetwork } =
        getSelf();
      const { chainId } = _evmChainInfo;
      try {
        const provider = await (id
          ? _web3Modal.connectTo(id!)
          : _web3Modal.connect());
        await _subscribeProvider(provider);
        const web3: ethers.providers.Web3Provider = initWeb3(provider);
        await _switchNetwork(provider, chainId);
        const address = await web3.getSigner().getAddress();
        /**
         *
         * TODO: Determine what token balance to show on wallet connection UI.
         * Right new we are showing the native token of supported connected chain.
         * Use balance contract to get balance instead if we decide to show non-native token balance in the future.
         */
        const balance = ethers.utils.formatEther(
          await web3.getSigner().getBalance()
        );
        set((state: any) => {
          const chainProvider = state.chainSlice.chainProvider;
          chainProvider._web3Provider = web3;
          chainProvider.walletAddress = address;
          chainProvider.balance = balance;
          state.chainSlice.isWalletConnected = true;
          state.chainSlice.isWalletConnected = true;
        });
      } catch (err: any) {
        throw new Error(`EVM connecting to wallet failed: ${err.message}`);
      }
    },

    _setEvmManager: (evmManager: ethers.Contract) => {
      set((state: any) => {
        state.chainSlice.chainProvider._evmManager = evmManager;
      });
    },
  };

  const actions = {
    on: (event: string, callback: any) => {
      const { _web3Provider } = getSelf();
      if (!_web3Provider) {
        return;
      }
      _web3Provider.on(event, callback);
    },
    removeListener: (event: string, callback: any) => {
      const web3Provider = getSelf()._web3Provider;
      if (!web3Provider) return;
      web3Provider.removeListener(event, callback);
    },
    getBlockNumber: async (): Promise<number> => {
      const { _web3Provider } = getSelf();
      if (!_web3Provider) {
        throw new Error('Web3 provider not initialized');
      }
      return _web3Provider.getBlockNumber();
    },
    getTransactionReceipt(
      txHash: string
    ): Promise<ethers.providers.TransactionReceipt> {
      const _web3Provider = getSelf()._web3Provider;
      if (!_web3Provider) {
        throw new Error('Web3 provider not initialized');
      }
      return _web3Provider.getTransactionReceipt(txHash);
    },
    getReverReason: async (txHash: string) => {
      const _web3Provider = getSelf()._web3Provider;
      if (!_web3Provider) {
        throw new Error('Web3 provider not initialized');
      }
      const tx = await _web3Provider.getTransaction(txHash);
      try {
        await _web3Provider.call(tx, tx.blockNumber);
      } catch (err) {
        return err.reason;
      }
    },
    connectWallet: (id: string) => {
      const { _onConnect, walletAddress } = getSelf();

      return new Promise(async (resolve, reject) => {
        try {
          await _onConnect(id);
          resolve(walletAddress);
        } catch (err) {
          reject(err);
        }
      });
    },
    deleteWallet: () => {
      const removeChain = get().chainSlice.removeChain;
      const _web3Modal = getSelf()._web3Modal;
      _web3Modal.clearCachedProvider();
      removeChain();
    },
    approve: async (tokenAddr: string, amount: number) => {
      const { _evmManager, _web3Provider } = getSelf();
      if (_evmManager && _web3Provider) {
        try {
          const approvalRes = await (
            await approveERC20(
              tokenAddr,
              amount,
              _web3Provider.getSigner(),
              _evmManager
            )
          ).wait();
          return approvalRes;
        } catch (err: any) {
          const e = new Error(
            `Approval failed: ${err.message}`,
            ethersErrorParser(err)
          );
          throw e;
        }
      }
    },
    checkApproval: async (tokenAddr: string) => {
      const { _evmManager, _web3Provider } = getSelf();
      if (_evmManager && _web3Provider) {
        try {
          const checkApprovalRes = await checkApprovalERC20(
            tokenAddr,
            _web3Provider.getSigner(),
            _evmManager
          );
          return checkApprovalRes;
        } catch (err: any) {
          throw new Error(
            `Check approval failed: ${err.message}`,
            ethersErrorParser(err)
          );
        }
      }
    },
    invest: async ({
      strategyInfo,
      amount,
      token,
      slippage,
      type,
      positionInfo,
      estimateGas,
    }: // Only needed for delta neutral strategy.
    InvestParams) => {
      const { _evmManager, _web3Provider, _vaultLib, walletAddress } =
        getSelf();
      if (
        _evmManager &&
        _web3Provider &&
        _vaultLib &&
        token &&
        token.decimals
      ) {
        // Same-chain case.
        // if (providerChain === strategyChain) {
        if (type === InvestType.OPEN) {
          // ethManager: ethers.Contract,
          // stableTokenDepositAmount: BigNumber,
          // strategyChainId: number,
          // strategyTypeId: BigNumber,
          // spendingTokenAddr: string,
          // stableTokenETHvalue: BigNumber,
          // slippage: number,
          // estimateGas?: boolean
          const params: [
            Contract,
            BigNumber,
            number,
            string,
            string,
            BigNumber,
            number
          ] = [
            _evmManager,
            BigNumber.from(
              BN(amount).times(BN(10).pow(token.decimals)).toString()
            ),
            strategyInfo.strategyChainId,
            strategyInfo.strategyId,
            token.tokenContractAddress,
            await getETHPx(
              _vaultLib,
              strategyInfo.oracleAddress,
              token.tokenContractAddress
            ),
            slippage,
          ];
          if (estimateGas) {
            const gasPrice: BigNumber = await _web3Provider.getGasPrice();

            const functionGasFees = await evmOpenPosition(...params, true);
            const finalGasPrice = gasPrice.mul(functionGasFees);
            return utils.formatEther(finalGasPrice + '');
          } else {
            try {
              const pos: ethers.providers.TransactionResponse =
                await evmOpenPosition(...params);
              return pos;
            } catch (err: any) {
              //throw new Error(`Failed to open position: ${err.message}`);
              const e: IErrorInfo = {
                title: 'Failed to open position',
                reason: `${err.reason}`,
              };
              throw e;
            }
          }
        } else if (type === InvestType.DECREASE) {
          if (estimateGas) {
            const gasPrice: BigNumber = await _web3Provider.getGasPrice();
            const functionGasFees = await evmDecreasePosition(
              _evmManager,
              strategyInfo.strategyChainId,
              walletAddress!,
              positionInfo!.userShares,
              positionInfo!.totalShares,
              positionInfo!.totalEquity,
              positionInfo!.totalEquityEth,
              amount,
              slippage,
              positionInfo!.positionId,
              positionInfo!.usdPriceETH,
              strategyInfo.tokens[0].decimals,
              estimateGas
            );
            const finalGasPrice = gasPrice.mul(functionGasFees);
            return utils.formatEther(finalGasPrice + '');
          }
          try {
            const pos: ethers.providers.TransactionResponse =
              await evmDecreasePosition(
                _evmManager,
                strategyInfo.strategyChainId,
                walletAddress!,
                positionInfo!.userShares,
                positionInfo!.totalShares,
                positionInfo!.totalEquity,
                positionInfo!.totalEquityEth,
                amount,
                slippage,
                positionInfo!.positionId,
                positionInfo!.usdPriceETH,
                strategyInfo.tokens[0].decimals,
                estimateGas
              );
            return pos;
          } catch (err: any) {
            // throw new Error(`Failed to increase position: ${err.message}`);
            console.trace(err)
            const e: IErrorInfo = {
              title: 'Failed to decrease position',
              reason: `${err.reason}`,
            };
            throw e;
          }
        } else if (type === InvestType.INCREASE) {
          const params: [
            Contract,
            BigNumber,
            string,
            string,
            BigNumber,
            number
          ] = [
            _evmManager,
            BigNumber.from(
              BN(amount).times(BN(10).pow(token.decimals)).toString()
            ),
            positionInfo!.positionId,
            token.tokenContractAddress,
            await getETHPx(
              _vaultLib,
              strategyInfo.oracleAddress,
              token.tokenContractAddress
            ),
            slippage,
          ];
          if (estimateGas) {
            const gasPrice: BigNumber = await _web3Provider.getGasPrice();

            const functionGasFees = await evmIncreasePosition(...params, true);
            const finalGasPrice = gasPrice.mul(functionGasFees);
            return utils.formatEther(finalGasPrice + '');
          } else {
            try {
              const pos: ethers.providers.TransactionResponse =
                await evmIncreasePosition(...params);
              return pos;
            } catch (err: any) {
              // throw new Error(`Failed to open position: ${err.message}`);
              const e: IErrorInfo = {
                title: 'Failed to increase position',
                reason: `${err.reason}`,
              };
              throw e;
            }
          }
        } else {
          // @TODO: Implement cross-chain position opening.
        }
      }

      // if (strategy === StrategyType.DELTA_NEUTRAL) {
      //   await _investDelta(amount, MAssetAddr!, col!);
      // } else if (strategy === StrategyType.STABLE_YIELD) {
      //   await _investStable(amount, type);
      // }
    },
    // DEPRECATED
    withdraw: (strategy: StrategyType, amount?: number, posId?: string) => {},
    getPositions: async () => {
      const { _evmManager, walletAddress } = getSelf();
      if (_evmManager && walletAddress) {
        try {
          const pos = await evmGetPositions(_evmManager, walletAddress);
          const ret = pos?.map((x: (ethers.BigNumber | number)[]) => {
            const [positionId, chainId] = x;
            return { positionId: positionId.toString(), chainId };
          });
          return ret;
        } catch (err) {
          throw new Error('Failed to get evm positions.');
        }
      }
    },
    getPositionDetails: async (
      positionId: string,
      strategyChainId: number,
      strategyAddress: string
    ) => {
      const chainId = get().chainSlice.chainId;
      const { _evmManager, walletAddress, _web3Provider } = getSelf();

      if (_evmManager && walletAddress && chainId && _web3Provider) {
        try {
          const strategyManager = new ethers.Contract(
            strategyAddress,
            EvmStrategyContractAbi[chainId],
            _web3Provider.getSigner()
          );

          return await strategyManager.positions(strategyChainId, positionId);
        } catch (err) {
          throw new Error('Failed to get evm position details.');
        }
      }
    },

    getBlockchainExplorerUrl: (type: BlockchainExplorerType, address: string) =>
      null,

    initSubscribe: () => ({
      [EvmProviderSubscriptionKeys.EVM_CHAIN_INFO]: api.subscribe(
        (state: any) =>
          (state.chainSlice.chainProvider as IEvmProvider)._evmChainInfo,
        (_evmChainInfo: EvmChainInfo) => {
          const { chainId, network } = _evmChainInfo;
          const { chainSlice } = get();
          const { setChainId } = chainSlice;
          const { _setWeb3Modal } = chainSlice.chainProvider as IEvmProvider;
          if (chainId) {
            setChainId(chainId + '');
          }
          if (network) {
            const web3Modal = new Web3Modal({
              network,
              cacheProvider: true,
              providerOptions: getProviderOptions(),
              renderModal: false,
            });
            _setWeb3Modal(web3Modal);
          }
        },
        { fireImmediately: true }
      ),
      [EvmProviderSubscriptionKeys.WEB3_MODAL]: api.subscribe(
        (state: any) =>
          (state.chainSlice.chainProvider as IEvmProvider)._web3Modal,
        (_web3Modal: any) => {
          if (_web3Modal?.cachedProvider) {
            const { _onConnect } = get().chainSlice
              .chainProvider as IEvmProvider;
            _onConnect();
          }
        },
        { fireImmediately: true }
      ),
      [EvmProviderSubscriptionKeys.WEB3_PROVIDER]: api.subscribe(
        (state: any) =>
          (state.chainSlice.chainProvider as IEvmProvider)._web3Provider,
        async (_web3Provider: any) => {
          if (_web3Provider) {
            const { _evmChainInfo, walletAddress } = getSelf();
            if (!(_evmChainInfo && walletAddress)) return;
            const EvmManagerContract: ethers.Contract = new ethers.Contract(
              EvmManagerAddress[_evmChainInfo.chainId],
              EvmManagerContractAbi[_evmChainInfo.chainId],
              _web3Provider.getSigner()
            );
            const vaultLibContract: ethers.Contract = new ethers.Contract(
              EvmVaultLibContractAddress[_evmChainInfo.chainId],
              EvmVaultLibContractAbi[_evmChainInfo.chainId],
              _web3Provider.getSigner()
            );
            const _setEvmManager = getSelf()._setEvmManager;
            const _setVaultLib = getSelf()._setVaultLib;

            const setNetworkType = get().appSlice.setNetworkType;
            _setEvmManager(EvmManagerContract);
            _setVaultLib(vaultLibContract);

            if (_evmChainInfo.chainId) {
              // Check if we support the chain.
              if (
                EvmIdToBlockchainMap[_evmChainInfo.chainId] &&
                !AvailableBlockchains[
                  EvmIdToBlockchainMap[_evmChainInfo.chainId]
                ]
              ) {
                throw new Error('This evm chain id is not supported.');
              }
              setNetworkType(getNetworkType(_evmChainInfo.chainId));
            }
          }
        }
      ),
    }),
  };

  const slice: IEvmProvider = {
    ..._state,
    ...state,
    ..._actions,
    ...actions,
  };

  return slice;
};
