import React from 'react';
import { useStore } from '../../store';
import './styles/AssetSettings.scss';
import textContext from '../../textContext';
import { KeyboardArrowDown, ChevronRight } from '@mui/icons-material';
import { lockIcon } from '../../assets/images';
import NumberFormat from 'react-number-format';
import AssetSearch from './AssetSearch';
import theme from '../../styles/mixins.module.scss';
import {
  countDecimals,
  getDigits,
  numberParse,
  numberParseLarge,
} from '../../helpers/utilities';
// Material
import InputBase from '@mui/material/InputBase';
import { TokenInfo, StablecoinMap, SerializedTokenInfo } from '../../constants/tokenConfig';
import { Button } from '../v1';
import styled from 'styled-components';
import { QuestionMarkTooltip } from '../v1/Modals/AssetModal/toolTip';
import TableLoader from '../Investment/TableLoader';
import { MIN_DEPOSIT_AMOUNT } from '../../constants/crosschain';

interface AssetSettingsProps {
  invest?: boolean;
  amount: number;
  setAmount: React.Dispatch<React.SetStateAction<number>>;
  // maxInvest: number;
  withdrawValue?: number;
  token: SerializedTokenInfo;
  // setSelectedToken: React.Dispatch<React.SetStateAction<TokenInfo>>;
  maxDeposit: string;
  isLoading: boolean;
}
export default function AssetSettings(props: AssetSettingsProps) {
  const {
    invest,
    amount,
    setAmount,
    withdrawValue,
    token,
    // setSelectedToken,
    maxDeposit,
    isLoading,
  } = props;
  const language = useStore((state) => state.appSlice.language);
  const text = textContext[language].assets;
  const [anchorEl, setAnchorEl] = React.useState<null | HTMLElement>(null);
  const { tokenText } = text;
  const open = Boolean(anchorEl);

  const handleClick = (event: React.MouseEvent<HTMLDivElement>) => {
    setAnchorEl(event.currentTarget);
  };
  const handleClose = () => {
    setAnchorEl(null);
  };
  // const handleToken = (val: string) => {
  //   setSelectedToken(StablecoinMap[val]);
  // };

  const handleNumber = (val: string) => {
    let parsed =
      val === '' ? 0 : parseFloat(val.replace(/,/g, '').substring(1));
    if (parsed >= 0 && countDecimals(parsed)! < 7) {
      setAmount(parsed);
    }
  };
  const MaxContainer = styled('div')`
    display: flex; //grid;
    // grid-template-columns: auto 50px;
    @media (max-width: 1199px) {
      position: absolute;
      /* top: 70px; */
      width: 100%;
      left: 0;
      flex-direction: column;
      align-items: flex-end;
      top: 0;
      flex-direction: column;
      just-content: end;
    }
  `;

  const MaxBalance = styled('div').attrs({
    className: 'subtitle-2 light-grey',
  })`
    white-space: nowrap;
    margin-top: 9px;
    margin-right: 8px;
    @media (max-width: 1199px) {
      /* margin: 0; */
      margin: auto 16px;
    }
  `;
  const MaxBalanceContainer = styled('div').attrs({})`
    @media (max-width: 1199px) {
      height: 53px;
      margin: auto 0;
      display: flex;
    }
  `;
  const MaxSupply = styled('div').attrs({
    className: 'subtitle-2 medium',
  })`
    width: 100%;
    text-align: right;
    @media (max-width: 1199px) {
      text-align: left;
      margin-block: 10px;
    }
  `;

  const QuestionMark = (
    <svg
      width="12"
      height="12"
      viewBox="0 0 12 12"
      fill="none"
      xmlns="http://www.w3.org/2000/svg"
    >
      <path
        fillRule="evenodd"
        clipRule="evenodd"
        d="M6 0C2.7 0 0 2.7 0 6C0 9.3 2.7 12 6 12C9.3 12 12 9.3 12 6C12 2.7 9.3 0 6 0ZM6.45078 9.525C6.30078 9.675 6.15078 9.75 5.92578 9.75C5.70078 9.75 5.55078 9.675 5.40078 9.525C5.25078 9.375 5.17578 9.225 5.17578 9C5.17578 8.775 5.25078 8.625 5.40078 8.475C5.55078 8.325 5.70078 8.25 5.92578 8.25C6.15078 8.25 6.30078 8.325 6.45078 8.475C6.60078 8.625 6.67578 8.775 6.67578 9C6.67578 9.225 6.60078 9.375 6.45078 9.525ZM7.50705 5.93909C7.70129 5.75951 7.82952 5.64096 7.875 5.55C8.1 5.175 8.25 4.8 8.25 4.275C8.25 3.6 8.025 3.15 7.65 2.775C7.275 2.4 6.75 2.25 6.075 2.25C5.4 2.25 4.8 2.475 4.425 2.925C3.975 3.375 3.75 3.975 3.75 4.725H4.875C4.875 4.275 4.95 3.9 5.175 3.675C5.325 3.375 5.625 3.225 6.075 3.225C6.375 3.225 6.675 3.375 6.9 3.525C7.125 3.75 7.2 4.05 7.2 4.35C7.2 4.65 7.05 4.875 6.9 5.1L6.75 5.25C6.075 5.85 5.7 6.225 5.625 6.6C5.475 6.825 5.4 7.2 5.4 7.575V7.725H6.525V7.575C6.525 7.35 6.6 7.125 6.675 6.9C6.75 6.675 6.9 6.525 7.05 6.375C7.22711 6.19789 7.38098 6.05564 7.50705 5.93909Z"
        fill="#9AAAAF"
      />
    </svg>
  );

  return (
    <>
      <div
        className={
          (amount >= MIN_DEPOSIT_AMOUNT &&
            amount <=
              (invest ? Number(token?.displayBalance) : withdrawValue!) &&
            amount <= (invest ? Number(maxDeposit) : Number.MAX_VALUE)) ||
          amount === 0
            ? 'token'
            : 'token errorColor'
        }
      >
        <div
          onClick={(e) => handleClick(e)}
          aria-describedby="token-popover"
          className="selectToken"
        >
          <div className="textUpper semiBold subtitle-1">{tokenText}</div>
          <div
            className="flex"
            style={{
              justifyContent: 'flex-start',
              alignItems: 'center',
              marginTop: '13px',
            }}
          >
            {token && <img src={token.icon} alt="usdc" width="30" />}
            <h4 style={{ padding: '1px .5rem' }}>
              {token && token.ticker.toUpperCase()}
            </h4>
          </div>
        </div>
        {/* <Popover
        className="token-popover"
        open={open}
        anchorEl={anchorEl}
        onClose={handleClose}
        anchorOrigin={{
          vertical: 'top',
          horizontal: 'left',
        }}
        transformOrigin={{
          vertical: 'top',
          horizontal: 'left',
        }}
      >
        <div>
          <div className="flex">
            <p>Select Token</p>
            <div onClick={handleClose}>&#x2715;</div>
          </div>
          <AssetSearch close={handleClose} setToken={handleToken} />
        </div>
      </Popover> */}
        <div className={`selectValue ${!invest ? 'withdraw' : ''}`}>
          {!invest && <img src={lockIcon} alt="lock icon" />}
          <NumberFormat
            value={amount}
            onChange={(event) => handleNumber(event.target.value)}
            customInput={InputBase}
            type="text"
            decimalScale={6}
            placeholder={'0.00'}
            thousandSeparator={true}
            // disabled={currentValue ? true : false}
            prefix="$"
          />
          {/* {!invest && <p>{partialWithdrawl}</p>} */}
          {invest && (
            <MaxContainer>
              <MaxBalanceContainer>
                <MaxBalance>{!isLoading?`Balance : ${
                  getDigits(Number(token?.displayBalance)) > 3
                    ? '$' + numberParseLarge(Number(token?.displayBalance), 2)
                    : numberParse(Number(token?.displayBalance), 'currency')
                }`:<>Balance : <TableLoader /> </>}</MaxBalance>
              </MaxBalanceContainer>
              <Button
                disabled={!invest && !withdrawValue}
                onClick={() => {
                  setAmount(
                    withdrawValue
                      ? withdrawValue
                      : Number(token?.displayBalance)
                  );
                }}
                className="textUpper"
                style={{ height: '32px' }}
              >
                <span className="button-3 medium">{`Max`}</span>
              </Button>
            </MaxContainer>
          )}
        </div>
        <MaxSupply>
          <span>
            Max Deposit:{' '}
            {maxDeposit ? numberParse(maxDeposit, 'currency') : '--'}
          </span>
          <QuestionMarkTooltip
            title={`If you’d like to deposit more than the max, you can do it over
              multiple transactions. This is to limit the price impact from
              opening a large position.`}
            placementPC={'bottom-end'}
            placementMobile={'bottom-end'}
          />
        </MaxSupply>
      </div>
    </>
  );
}
