import React from 'react';
import './connectDialogCard.scss';
import { metaIcon } from '../../../../assets/images/icons';
import { Button } from '../../Components/Button';

interface ConnectDialogCardProps {
  text: string;
  img: string;
  onClickReturn: any;
  onSelect?: (onClickReturn: any) => void;
  selected: boolean;
  disabled: boolean;
}

export const ConnectDialogCard = ({
  text = 'Metamask',
  img = metaIcon,
  onClickReturn = null,
  onSelect,
  selected,
  disabled,
  ...props
}: ConnectDialogCardProps) => {
  return (
    <Button
      variant={'outline'}
      light={true}
      className={`connectCard ${selected ? 'connectCard-selected' : ''}`}
      disabled={disabled}
      onClick={() => onSelect && !disabled && onSelect(onClickReturn)}
    >
      <div className="chain-info">
        <img width="25" src={img} alt={text} />
        <span className="button-2">{text}</span>
        {disabled && (
          <div className="comingSoon">
            <div>Coming soon</div>
          </div>
        )}
      </div>
    </Button>
  );
};
