import React from 'react';
import {
  AreaChart,
  Area,
  XAxis,
  YAxis,
  CartesianGrid,
  Tooltip,
  Legend,
  ResponsiveContainer,
  ReferenceLine,
} from 'recharts';

import { Tooltip as CustomTooltip } from './Tooltip';

export interface LGProps {
  type: 'full' | 'mini';
  data: any;
  fill?: boolean;
  legend?: boolean;
  payload?: any;
  referenceAmount?: number;
  color: any;
  token?: string | null;
  lineCount: 1 | 2;
}

export const LineGraph = ({
  type = 'full',
  data = [],
  legend = false,
  fill = false,
  referenceAmount = undefined,
  payload,
  color = { percentage: '#7BEFFF', price: '#19EE94' },
  token,
  lineCount,
  ...props
}: LGProps) => {
  const [xy, setXY] = React.useState([0, 0]);
  const parentRef = React.useRef(null);

  const random = Math.round(Math.random() * 1000);
  React.useLayoutEffect(() => {
    function updateSize() {
      const element = document.getElementById(`line-chart-parent-${random}`);
      console.log();
      if (element) setXY([element.offsetWidth, element.offsetWidth * 0.4]);
    }
    window.addEventListener('resize', updateSize);
    updateSize();
    return () => window.removeEventListener('resize', updateSize);
  }, []);

  const ran1 = Math.random() * 100;
  const ran2 = Math.random() * 100;
  return (
    <div
      id={`line-chart-parent-${random}`}
      ref={parentRef}
      style={{ width: '100%' }}
      {...props}
    >
      <ResponsiveContainer width={xy[0]} height={xy[1]}>
        <AreaChart
          data={data}
          margin={{ top: 5, right: 20, left: 20, bottom: 5 }}
        >
          <Tooltip
            content={<CustomTooltip token={token} color={color} />}
            cursor={{
              stroke: 'white',
              strokeWidth: '0.75',
              strokeLinecap: 'round',
              strokeDasharray: '5 5',
            }}
          />
          <CartesianGrid
            vertical={false}
            style={{
              opacity: '0.2',
              strokeWidth: '0.75',
              strokeLinecap: 'round',
              strokeDasharray: '3 3',
            }}
          />
          {referenceAmount !== undefined && (
            <ReferenceLine y={0} opacity="0.6" />
          )}
          {legend && <Legend payload={payload} />}
          <XAxis
            dataKey="timestamp_sec"
            interval={
              type === 'mini'
                ? Math.round(data.length / 4)
                : Math.round(data.length / 10)
            }
            color="white"
            width={30}
            strokeWidth={2}
            tickFormatter={(tick) => {
              try {
                const date = new Date(tick * 1000).toUTCString().split(' ');
                return `${date[2]} ${date[1]}`;
              } catch (e) {
                console.error(e);
                return tick;
              }
            }}
          />

          <YAxis
            dataKey="percentage"
            tickCount={type === 'mini' ? 4 : 9}
            width={type === 'mini' ? 30 : 50}
            tick={{ fontSize: '14px' }}
            tickFormatter={(tick) => {
              return `${Math.round(tick * 10000) / 100}%`;
            }}
            strokeWidth={2}
          />

          {lineCount === 2 && (
            <YAxis
              dataKey="price"
              tickCount={type === 'mini' ? 4 : 9}
              width={30}
              yAxisId="right"
              orientation="right"
              tick={{ fontSize: '14px' }}
              strokeWidth={2}
            />
          )}

          <Area
            type="monotone"
            dataKey={'percentage'}
            stroke={color.percentage}
            fillOpacity={1}
            fill={`url(#percentage${ran1})`}
            strokeWidth={1}
          />
          {lineCount === 2 && (
            <Area
              type="monotone"
              yAxisId="right"
              dataKey={'price'}
              stroke={color.price}
              fillOpacity={1}
              fill={`url(#price${ran2})`}
              strokeDasharray="6 2"
              strokeWidth={1}
            />
          )}

          {fill && (
            <defs>
              <linearGradient
                id={'percentage' + ran1}
                x1="0"
                y1="0"
                x2="0"
                y2="1"
              >
                <stop
                  offset="5%"
                  stopColor={color.percentage}
                  stopOpacity={0.8}
                />
                <stop
                  offset="95%"
                  stopColor={color.percentage}
                  stopOpacity={0}
                />
              </linearGradient>
              {lineCount === 2 && (
                <linearGradient id={'price' + ran2} x1="0" y1="0" x2="0" y2="1">
                  <stop offset="5%" stopColor={color.price} stopOpacity={0.8} />
                  <stop offset="95%" stopColor={color.price} stopOpacity={0} />
                </linearGradient>
              )}
            </defs>
          )}
        </AreaChart>
      </ResponsiveContainer>
    </div>
  );
};
