import { DropdownInfoBox } from '../../../';
import { BackTestAnlysisMini, Box, Modal } from '../../../../../..';
import { calculate_backtest_analysis_apy } from '../../../../../../../../helpers/graph';
import { PdnStrategyDetails } from '../../../PdnStrategyDetails';

export interface InfoProps {
  modalChildren: any;
  strategyInfo: any;
  vaultTitle: any;
  backtestAnalysisData: any[];
}

export const BacktestAnalysis = ({
  modalChildren,
  strategyInfo,
  vaultTitle,
  backtestAnalysisData,
}: InfoProps) => {
  return (
    <>
      {
        // TODO: replace with real data.
      }{' '}
      <DropdownInfoBox
        title={'Backtest Analysis'}
        viewDetails={{
          buttonText: 'View Details',
          modalChildren,
        }}
        first
      >
        <Modal
          modalClassName="modal-pdn-strategy-details"
          trigger={
            <Box
              className="line-graph-box"
              style={{ margin: '12px 16px 16px', padding: '0px' }}
            >
              <BackTestAnlysisMini
                details={false}
                color={{ percentage: '#19EE94' }}
                data={backtestAnalysisData}
                percentage={
                  backtestAnalysisData && backtestAnalysisData.length > 0
                    ? {
                        return:
                          backtestAnalysisData[backtestAnalysisData.length - 1]
                            .percentage,
                        apy: calculate_backtest_analysis_apy(
                          backtestAnalysisData
                        ),
                      }
                    : {
                        return: 0,
                        apy: 0,
                      }
                }
                // style={{ marginLeft: '4%' }}
              />
            </Box>
          }
        >
          <PdnStrategyDetails
            strategyInfo={strategyInfo}
            vaultTitle={vaultTitle}
            scrollToAnalysis
          />
        </Modal>
      </DropdownInfoBox>
    </>
  );
};
