import './topbar.scss';
import { Logo } from './../Components/Logo';
import { Connect } from './Connect';
import { Navbar } from './../Navbar';
import { AuthUser } from './AuthUser';
import { SwitchNetwork } from './SwitchNetwork';
import { useRef, forwardRef, PropsWithChildren } from 'react';
import { Avatar } from './Avatar';
import { Blockchain, BlockchainType } from '../../../constants/crosschain';

interface TopBarProps {
  mobile: boolean;
  walletConnected: boolean;
  onSelect: (chainId: any) => void;
  balance: number;
  address: string;
  explorerURL: string;
  disconnectFunc: () => void;
  transactionsHistory: any;
  walletIcon: string;
  onChainSelect: (chainId: any) => void;
  onWalletSelect: (walletType: any) => void;
  chain: Blockchain | null;
  chainType: BlockchainType;
  clearAllTransactions: () => void;
}

export const TopBar = forwardRef<TopBarProps, PropsWithChildren<any>>(
  (
    {
      mobile,
      walletConnected,
      onSelect,
      balance,
      address,
      explorerURL,
      disconnectFunc,
      transactionsHistory,
      walletIcon,
      onChainSelect,
      onWalletSelect,
      children,
      chain,
      chainType,
      clearAllTransactions,
      ...props
    },
    ref
  ) => {
    return (
      <div>
        <div className={'topbar'}>
          {mobile && (
            <Logo
              colored
              direction="horizontal"
              mode="dark"
              text
              style={{ width: '105.24px', height: '36px' }}
            />
          )}
          <div className="topbar-right">
            <SwitchNetwork
              mobile={mobile}
              onSelect={onSelect}
              chain={chain}
              style={{ marginRight: '10px' }}
            />
            {!walletConnected ? (
              <div>
                <Connect
                  mobile={mobile}
                  walletConnected={walletConnected}
                  onChainSelect={onChainSelect}
                  onWalletSelect={onWalletSelect}
                  chain={chain}
                />
              </div>
            ) : (
              <>
                <AuthUser
                  mobile={mobile}
                  balance={balance}
                  address={address}
                  explorerURL={explorerURL}
                  disconnectFunc={disconnectFunc}
                  transactionsHistory={transactionsHistory}
                  walletIcon={walletIcon}
                  chain={chain}
                  clearAllTransactions={clearAllTransactions}
                />
                {!mobile && <Avatar />}
              </>
            )}
            {mobile && <Navbar active mobile={mobile} />}
          </div>
        </div>
        <div
          style={{
            height: mobile ? 'calc(100vh - 151px)' : 'calc(100vh - 92px)',
            overflowY: 'auto',
            width: mobile ? '100vw' : 'calc(100vw - 250px)',
            overflowX: 'hidden',
            marginLeft: mobile ? '0px' : '250px',
          }}
        >
          {children}
        </div>
      </div>
    );
  }
);
