import { forwardRef, useRef, useState, useEffect } from 'react';
import { LineGraph } from '../../../../Charts/LineGraph';
import './index.scss';
import { numberToPercentage } from '../../../../../../helpers/utilities';
interface BackTestAnlysisMiniProps {
  color: any;
  data: any;
  percentage: IPercent;
  details: boolean;
}

interface IPercent {
  return: number;
  apy: number;
  // shapeRation: number;
}

interface dataInfo {
  name: string;
  value: number;
}

export const BackTestAnlysisMini = forwardRef<
  HTMLDivElement,
  BackTestAnlysisMiniProps
>(
  (
    {
      details = true,
      color,
      data = {},
      percentage = { return: 0, apy: 0, shapeRation: 0 },
      ...props
    },
    ref
  ) => {
    const [width, setWidth] = useState(0);
    const parentRef = useRef(null);

    useEffect(() => {
      parentRef.current && setWidth(parentRef.current!.offsetWidth);
      // console.log(parentRef.current!.offsetWidth);
    }, [parentRef]);

    const backtestData: dataInfo[] = [
      { name: 'Return', value: percentage.return },
      { name: 'APY', value: percentage.apy },
      // { name: 'Shape Ration', value: percentage.shapeRation },
    ];

    let displayData = (dataInfo: dataInfo[]) =>
      dataInfo.map((item, i) => {
        return (
          <div key={i} className="center">
            <div className="subtitle-2" style={{ color: '#CAD2D8' }}>
              {item.name}
            </div>
            <div
              className={`subtitle-1${
                item.name === 'Return'
                  ? item.value > 0
                    ? ' positive'
                    : item.value < 0
                    ? ' negative'
                    : ''
                  : ''
              }`}
            >
              {item.name === 'Return' && item.value > 0 ? '+' : ''}
              {numberToPercentage(item.value)}
            </div>
          </div>
        );
      });

    const getDateString = (unixDate: number): string => {
      const date = new Date(unixDate * 1000).toUTCString().split(' ');
      return `${date[3]} ${date[2]}`;
    };

    return (
      <div ref={parentRef} {...props} className="backtest-anlysis">
        {details && (
          <>
            <div className="headline-5 medium" style={{ color: '#FFFFFF' }}>
              Backtest Analysis
            </div>
            <div className="headline-5 grid2-mini">
              {displayData(backtestData)}
            </div>
          </>
        )}
        <div className="line-graph-container">
          <div className="date-title headline-5 center">
            {data && data.length > 0
              ? `${getDateString(data[0].timestamp_sec)} - ${getDateString(
                  data[data.length - 1].timestamp_sec
                )}`
              : ''}
          </div>
          <LineGraph color={color} data={data} type="mini" fill lineCount={1} />
        </div>
      </div>
    );
  }
);
