import { useQuery } from 'react-query';
import { getPositions } from '../../../helpers/crosschain';
import { IStore, useStore } from '../../../store';
import { IEvmProvider } from '../../../store/provider/Evm/EvmProvider';
import { keys } from '../../keyNames';

export interface UnpopulatedPositionInfoMap {
  [strategyId: string]: UnpopulatedPositionInfo;
}
export interface UnpopulatedPositionInfo {
  positionId: string;
  strategyChainId: number;
  strategyId: string;
}

export function useFetchEvmPositionInfo(options?: any) {
  const evmManager = useStore(
    (state: IStore) =>
      (state.chainSlice.chainProvider as IEvmProvider)?._evmManager
  );

  const address = useStore(
    (state: IStore) =>
      (state.chainSlice.chainProvider as IEvmProvider)?.walletAddress
  );

  const shouldFetch = !!(evmManager && address);
  return useQuery(
    [keys.positionInfo],
    async () => {
      if (!evmManager || !address) return null;
      const positions: {
        positionId: string;
        strategyChainId: number;
        strategyId: string;
      }[] = await getPositions(evmManager, address);
      return positions.reduce(
        (acc, position) => ({ ...acc, [position.strategyId]: position }),
        {}
      ) as UnpopulatedPositionInfoMap;
    },

    {
      // refetchOnMount: false,
      ...options,
      enable: shouldFetch,
    }
  );
}
