import { Navbar } from '../../Navbar';
import { TopBar } from '../../TopBar';
import { useMediaQuery } from '@mui/material';
import { forwardRef, PropsWithChildren } from 'react';
import { MobileNavMenu } from '../../Navbar/MobileNavMenu';
import { Blockchain, BlockchainType } from '../../../../constants/crosschain';

interface NavContainerProps {
  walletConnected: boolean;
  balance: number;
  address: string;
  explorerURL: string;
  disconnectFunc: () => void;
  transactionsHistory: any;
  walletIcon: string;
  onChainSelect: (chainId: any) => void;
  onWalletSelect: (walletType: any) => void;
  chain: Blockchain;
  chainType: BlockchainType;
  clearAllTransactions: () => void;
}

export const NavContainer = forwardRef<
  NavContainerProps,
  PropsWithChildren<any>
>(
  (
    {
      walletConnected,
      balance,
      address,
      explorerURL,
      disconnectFunc,
      transactionsHistory,
      walletIcon,
      onChainSelect,
      onWalletSelect,
      chain,
      chainType,
      clearAllTransactions,
      children,
      ...props
    },
    ref
  ) => {
    const mobile = !useMediaQuery('(min-width:1200px)');

    return (
      <div style={{ width: '100vw', height: '100vh' }}>
        {!mobile && <Navbar mobile={mobile} active />}
        <TopBar
          {...props}
          mobile={mobile}
          walletConnected={walletConnected}
          balance={balance}
          address={address}
          explorerURL={explorerURL}
          disconnectFunc={disconnectFunc}
          transactionsHistory={transactionsHistory}
          walletIcon={walletIcon}
          onChainSelect={onChainSelect}
          onWalletSelect={onWalletSelect}
          onSelect={onChainSelect}
          chain={chain}
          chainType={chainType}
          clearAllTransactions={clearAllTransactions}
        >
          {children}
        </TopBar>
        {mobile && <MobileNavMenu />}
      </div>
    );
  }
);
