import { forwardRef, useRef, useState, useEffect } from 'react';
import { Box, NumberCounter } from '../../../../';
import { numberToPercentage } from '../../../../../../helpers/utilities';
import { LineGraph } from '../../../../Charts/LineGraph';
import './index.scss';
interface BackTestAnlysisProps {
  color: any;
  data: any;
  referenceAmount: number;
  percentage: any;
  title: string;
  date: string;
}

export const BackTestAnlysis = forwardRef<HTMLDivElement, BackTestAnlysisProps>(
  (
    {
      color,
      data = {},
      referenceAmount = 0,
      percentage = { return: 0, apy: 0, shapeRation: 0, returnChangeRate: 0 },
      title = "Backtest Result",
      date = "Jun 2021 - May2022",
      ...props
    },
    ref
  ) => {
    const [width, setWidth] = useState(0);
    const parentRef = useRef(null);

    useEffect(() => {
      parentRef.current && setWidth(parentRef.current!.offsetWidth);
      // console.log(parentRef.current!.offsetWidth);
    }, [parentRef]);

    return (
      <Box ref={parentRef} {...props} className="back-test-anlysis">
        <div className="headline-4 semiBold">Backtest Analysis</div>
        <div className="headline-5" style={{ marginBottom: '33px' }}>
          Return:
          <span
            className={`span-1${percentage.return > 0 ? ' positive' : ' negative'
              }`}
          >
            <NumberCounter
              value={percentage.return}
              formatNumberFunction={(value: number) =>
                `${value > 0 ? '+' : ''}${numberToPercentage(value)}`
              }
            />
            {/* <NumberCounter
              value={percentage.returnChangeRate}
              formatNumberFunction={(value: number) =>
                `(${numberToPercentage(value)})`
              }
            /> */}
          </span>
          APY:
          <span className="span-1">
            <NumberCounter
              value={percentage.apy}
              formatNumberFunction={(value: number) =>
                numberToPercentage(value)
              }
            />
          </span>
          {/* Shape Ration:
          <span className="span-1">
            <NumberCounter
              value={percentage.shapeRation}
              formatNumberFunction={(value: number) =>
                numberToPercentage(value)
              }
            />
          </span> */}
        </div>

        <div className="graph-title">
          <div className="subtitle-2">Backtest Result ({title})</div>
          <div className="headline-5 float-right">{date}</div>
        </div>
        <LineGraph
          color={color}
          data={data}
          referenceAmount={referenceAmount}
          type="full"
          lineCount={1}
        />
      </Box>
    );
  }
);
