import { Fragment, useState } from 'react';
import Grid from '@mui/material/Grid';
// import Asset from '../../pages/Asset';
import { useNavigate } from 'react-router-dom';
import { useStore } from '../../store';
import textContext from '../../textContext';
import { numberParseLarge } from '../../helpers/utilities';
import { StrategyInfo } from '../../constants/strategy/index';
import { StrategyCard, VaultItem } from '../v1/Components/Strategy';
import { PdnDropdown } from '../v1/Components/Strategy/Dropdown/PdnDropdown/index';
import { Button, NumberCounter } from '../v1';
import Accordion from '@mui/material/Accordion';
import AccordionSummary from '@mui/material/AccordionSummary';
import AccordionDetails from '@mui/material/AccordionDetails';
import { useOpenModal } from '../../store/base/application/hooks';
import { ModalType } from '../../store/base/AppSlice';
import { PositionInfo } from '../../types';
import { useMediaQuery } from '@mui/material';

interface ITableAssetsProps {
  info?: Object;
  isOpen: React.Dispatch<React.SetStateAction<boolean>>;
  allStrategies: StrategyInfo[];
  strategyInfo: StrategyInfo;
  positionInfo: PositionInfo;
  key: number;
  title: string;
}

export default function TablePositions(props: ITableAssetsProps) {
  const language = useStore((state) => state.appSlice.language);

  const isWalletConnected = useStore(
    (state) => state.chainSlice.isWalletConnected
  );

  const [open, setOpen] = useState(false);
  const [drop, setDrop] = useState(false);
  const [investModalProps, setInvestmodalProps] = useState({});

  const text = textContext[language as keyof object]['portfolio']['asset'];
  const deltaNeutralText =
    textContext[language as keyof object]['assets']['deltaNeutralText'];
  const { strategyInfo: data, positionInfo, title } = props;

  const handleClose = () => {
    setOpen(false);
    navigate('/portfolio');
  };

  const navigate = useNavigate();
  // const handleInvest = (e: React.MouseEvent<HTMLButtonElement, MouseEvent>) => {
  //   e.stopPropagation();
  //   navigate(`/portfolio/asset&=${data.strategy.address}`);
  //   setOpen(true);
  //   props.isOpen(true);
  // };

  const accordionClose = () => {
    setDrop(false);
  };

  const vaultItem = (isModal = false) => {
    return (
      <VaultItem
        {...{
          title: isModal ? title : `${title}(${data.leverage}X)`,
          tokens: data.tokens,
          protocols: data.protocols,
        }}
      />
    );
  };

  const handleInvest = (e: React.MouseEvent<HTMLButtonElement, MouseEvent>) => {
    e.stopPropagation();
    openInvestModal();
  };

  const handleWithdraw = (
    e: React.MouseEvent<HTMLButtonElement, MouseEvent>
  ) => {
    e.stopPropagation();
    openWithdrawlModal();
  };

  const modalProps = {
    strategyInfo: data,
    positionInfo,
    vaultItem: vaultItem(true),
    title: deltaNeutralText,
  };
  const openInvestModal = useOpenModal(ModalType.INVEST, {
    invest: true,
    ...modalProps,
  });

  const openWithdrawlModal = useOpenModal(ModalType.INVEST, {
    invest: false,
    ...modalProps,
  });

  const Full = () => <span className="fontFull subtitle-3">Full</span>;
  const Available = () => (
    <span className="fontAvailable subtitle-3">Available</span>
  );
  const isPC = useMediaQuery('(min-width:1200px)');

  return (
    <Fragment>
      {isPC && (
        <Accordion>
          <AccordionSummary>
            <Grid container style={{ height: 'max-content' }}>
              <Grid item md={4}>
                {' '}
                {vaultItem()}{' '}
              </Grid>
              <Grid item md={3}>
                <span className="subtitle-2">
                  {data.TVL >= 0 ? (
                    <NumberCounter
                      value={data.TVL}
                      formatNumberFunction={(value: number) =>
                        `$${numberParseLarge(value, 2)}`
                      }
                    />
                  ) : (
                    '-'
                  )}
                </span>
                {` / `}
                <span className="subtitle-2" style={{ marginRight: '5px' }}>
                  {data.capacity >= 0 ? (
                    <NumberCounter
                      value={data.capacity}
                      formatNumberFunction={(value: number) =>
                        numberParseLarge(value)
                      }
                    />
                  ) : (
                    '-'
                  )}
                </span>
                {data.TVL < data.capacity ? <Available /> : <Full />}
              </Grid>
              {/* <Grid item md={1.5}>
                <div className="subtitle-2">
                  {
                    <NumberCounter
                      value={data.holdings}
                      formatNumberFunction={(value: number) =>
                        numberParse(value, 'currency')
                      }
                    />
                  }
                </div>
              </Grid> */}
              <Grid item md={2}>
                <div className="subtitle-2">
                  {
                    <NumberCounter
                      value={Number(positionInfo.equity)}
                      formatNumberFunction={(value: number) =>
                        '$' + numberParseLarge(value, 2)
                      }
                    />
                  }
                </div>
              </Grid>
              {/* <Grid item md={1.45}>
                <div
                  className={`subtitle-2 ${
                    data.apy !== null
                      ? data.apy > 0
                        ? 'positive'
                        : data.apy < 0
                        ? 'negative'
                        : ''
                      : ''
                  }`}
                >
                  {' '}
                  {
                    <NumberCounter
                      value={data.holdings}
                      formatNumberFunction={(value: number) =>
                        numberParse(value, 'currency')
                      }
                    />
                  }
                  {
                    <NumberCounter
                      value={data.apy}
                      formatNumberFunction={(value: number) =>
                        `(${numberToPercentage(value)})`
                      }
                    />
                  }{' '}
                </div>
              </Grid> */}
              <Grid className="action-group" item md={3}>
                <div>
                  <Button
                    variant="outline"
                    size="small"
                    text={text.invest}
                    disabled={data.TVL >= data.capacity}
                    onClick={(event) => handleInvest(event)}
                    style={{ padding: '1px 9px', width: '96px' }}
                  />
                </div>
                <div>
                  <Button
                    variant="info"
                    size="small"
                    text={text.withdraw}
                    disabled={Number(positionInfo.equity) > 0 ? false : true} // TODO: need update to deposit value
                    onClick={(event) => handleWithdraw(event)}
                    style={{ padding: '1px 9px', width: '96px' }}
                  />
                </div>
              </Grid>
            </Grid>
          </AccordionSummary>
          <AccordionDetails>
            <PdnDropdown
              strategyInfo={data}
              positionInfo={positionInfo}
              protocols={data.protocols}
              vaultTitle={props.title}
              accordionClose={accordionClose}
            />
          </AccordionDetails>
        </Accordion>
      )}
      {!isPC && (
        <StrategyCard
          strategyInfo={data}
          pageType="Portfolio"
          positionInfo={positionInfo}
          walletConnected={isWalletConnected}
        />
      )}
      {/* {open && (
        <Asset
          invest
          strategyInfo={props.strategyInfo}
          vaultItem={vaultItem}
          close={handleClose}
          title={props.title}
          leverage={leverage}
        />
      )} */}
    </Fragment>
  );
}
