import React, { Fragment, useState } from 'react';
import { StrategyInfo } from '../../../../../../constants/strategy/index';
import { VaultItem } from '../../VaultItem';
import './tab.scss';
import { CardInfo } from '../CardInfo';
import { ReturnMini } from '../../LineGraph/ReturnMini';
import { BackTestAnlysisMini } from '../../LineGraph/BackTestAnlysisMini';
import { Button } from '../../../Button';
import { getVaulName } from '../../../../../../helpers/utilities';
import useGraph from '../../../../../../hooks/graph/useGraph';
import {
  calculate_backtest_analysis_apy,
  find_x_days_ago,
  getReturnData
} from '../../../../../../helpers/graph';

interface DetailsProps {
  clickDeposit: (e: React.MouseEvent<HTMLButtonElement, MouseEvent>) => void;
  clickWithdraw: (e: React.MouseEvent<HTMLButtonElement, MouseEvent>) => void;
  strategyInfo: StrategyInfo;
  holdings: number;
  walletConnected: boolean;
}

export const StrategyTabs = ({
  walletConnected = false,
  ...props
}: DetailsProps) => {
  const { strategyInfo: data, holdings } = props;
  const [activeTab, setActiveTab] = useState('Overview');

  const { assetPrices, homoraPerformace, backtestAnalysisData } = useGraph();

  const returnData = getReturnData(assetPrices, homoraPerformace, strategyInfo.tokens[0].ticker, strategyInfo.tokens[1].ticker)
  const week_data = find_x_days_ago(returnData, 7);
  const month_data = find_x_days_ago(returnData, 30);

  const returns = {
    week: week_data,
    month: month_data,
    inception: returnData
  };


  const title = `${getVaulName(data.tokens)}(${data.leverage}X)`;
  const vaultItem = (
    <VaultItem
      {...{
        title,
        tokens: data.tokens,
        protocols: data.protocols,
      }}
    />
  );

  const handleActiveTab = (tabName: string) => {
    setActiveTab(tabName);
  };

  const navBar = () => {
    const tabName = ['Overview', 'Return', 'Backtest'];
    return tabName.map((tabName, i) => {
      return (
        <li
          key={i}
          className={activeTab === tabName ? 'active' : ''}
          onClick={() => handleActiveTab(tabName)}
        >
          <div className="tabName button-3">{tabName}</div>
        </li>
      );
    });
  };

  return (
    <div className="strategyContainer">
      <div className="tabNav">{navBar()}</div>
      <div>{vaultItem}</div>
      <div className="strategy-content">
        {activeTab === 'Overview' && (
          <Fragment>
            <div className="headline-5">{'Overview'}</div>
            <CardInfo
              walletConnected={walletConnected}
              cardInfo={data}
              holdings={holdings}
              pageType="Details"
            />
          </Fragment>
        )}
        {activeTab === 'Return' && (
          <Fragment>
            <ReturnMini
              color={{ percentage: '#7BEFFF', price: '#19EE94' }}
              data={returns}
              percentage={
                returnData && returnData.length > 0
                  ? {
                      week: week_data[week_data.length - 1].percentage,
                      month: month_data[month_data.length - 1].percentage,
                      inception: returnData[returnData.length - 1].percentage,
                    }
                  : {
                      week: 0,
                      month: 0,
                      inception: 0,
                    }
              }
              token={
                returnData && returnData.length > 0
                  ? returnData[returnData.length - 1].ticker
                  : 'Token'
              }
              payload={[
                {
                  value: 'Return',
                  type: 'line',
                  id: 'percentage',
                  color: '#7BEFFF',
                },
                {
                  value: `${
                    returnData && returnData.length > 0
                      ? returnData[returnData.length - 1].ticker
                      : 'Token'
                  } Price`,
                  type: 'line',
                  id: 'price',
                  color: '#19EE94',
                },
              ]}
              details={true}
              lineCount={2}
              fill={true}
            />
          </Fragment>
        )}
        {activeTab === 'Backtest' && (
          <Fragment>
            <BackTestAnlysisMini
              details={true}
              color={{ percentage: '#19EE94' }}
              data={backtestAnalysisData}
              percentage={
                backtestAnalysisData && backtestAnalysisData.length > 0
                  ? {
                      return:
                        backtestAnalysisData[backtestAnalysisData.length - 1]
                          .percentage,
                      apy: calculate_backtest_analysis_apy(
                        backtestAnalysisData
                      ),
                    }
                  : {
                      return: 0,
                      apy: 0,
                    }
              }
            />
          </Fragment>
        )}
      </div>
      <div className="itemRow" style={{ justifyContent: 'space-between' }}>
        <Button
          variant="contained"
          size="small"
          text={'Deposit'}
          disabled={data.TVL >= data.capacity || !walletConnected}
          onClick={(event) => props.clickDeposit(event)}
          style={{ width: '40%', padding: '3px' }}
        />
        <Button
          variant="info"
          size="small"
          text={'Withdraw'}
          disabled={!(walletConnected && holdings > 0)}
          onClick={(event) => props.clickWithdraw(event)}
          style={{ width: '40%', padding: '3px' }}
        />
      </div>
    </div>
  );
};
