import React from 'react';
import './navbar.scss';
import Drawer from '@mui/material/Drawer';

import { NavLink } from './NavLink';
import { Footer } from './Footer';
import { Logo } from './../Components/Logo';

interface NavbarProps {
  mobile: boolean;
  active?: boolean;
}

export const Navbar = ({
  mobile = false,
  active = false,
  ...props
}: NavbarProps) => {
  const [nav, setNav] = React.useState(false);

  const Dot = () => (
    <svg
      width="18"
      height="4"
      viewBox="0 0 18 4"
      fill="none"
      xmlns="http://www.w3.org/2000/svg"
    >
      <path
        d="M2 4C3.10457 4 4 3.10457 4 2C4 0.89543 3.10457 0 2 0C0.89543 0 0 0.89543 0 2C0 3.10457 0.89543 4 2 4Z"
        fill="#F8F8F8"
      />
      <path
        d="M16 4C17.1046 4 18 3.10457 18 2C18 0.89543 17.1046 0 16 0C14.8954 0 14 0.89543 14 2C14 3.10457 14.8954 4 16 4Z"
        fill="#F8F8F8"
      />
      <path
        d="M9 4C10.1046 4 11 3.10457 11 2C11 0.89543 10.1046 0 9 0C7.89543 0 7 0.89543 7 2C7 3.10457 7.89543 4 9 4Z"
        fill="#F8F8F8"
      />
    </svg>
  );

  const Cross = () => (
    <svg
      width="14"
      height="15"
      viewBox="0 0 14 15"
      fill="none"
      xmlns="http://www.w3.org/2000/svg"
    >
      <path
        d="M1.81591 1.24064L6.99991 6.42464L12.1626 1.26197C12.2531 1.16567 12.362 1.08863 12.483 1.03548C12.604 0.982322 12.7345 0.954145 12.8666 0.952637C13.1495 0.952637 13.4208 1.06502 13.6208 1.26506C13.8209 1.46509 13.9332 1.73641 13.9332 2.0193C13.9357 2.15008 13.9115 2.27998 13.8619 2.40104C13.8124 2.5221 13.7387 2.63177 13.6452 2.7233L8.42925 7.88597L13.6452 13.102C13.821 13.274 13.9241 13.5069 13.9332 13.7526C13.9332 14.0355 13.8209 14.3068 13.6208 14.5069C13.4208 14.7069 13.1495 14.8193 12.8666 14.8193C12.7306 14.8249 12.595 14.8023 12.4683 14.7527C12.3416 14.7031 12.2266 14.6277 12.1306 14.5313L6.99991 9.3473L1.82658 14.5206C1.73645 14.6137 1.62878 14.688 1.50978 14.7393C1.39078 14.7906 1.26281 14.8178 1.13325 14.8193C0.850349 14.8193 0.579038 14.7069 0.378999 14.5069C0.17896 14.3068 0.0665798 14.0355 0.0665798 13.7526C0.0640929 13.6219 0.0883564 13.492 0.137881 13.3709C0.187406 13.2498 0.261147 13.1402 0.35458 13.0486L5.57058 7.88597L0.35458 2.66997C0.178777 2.49798 0.0756887 2.26508 0.0665798 2.0193C0.0665798 1.73641 0.17896 1.46509 0.378999 1.26506C0.579038 1.06502 0.850349 0.952637 1.13325 0.952637C1.38925 0.955837 1.63458 1.0593 1.81591 1.24064Z"
        fill="white"
      />
    </svg>
  );
  const NavbarContent = () => (
    <>
      {mobile && (
        <button
          className="btn-outline-white btn cross"
          onClick={() => setNav(false)}
          style={{
            padding: '8px 10px',
            height: '40px',
            position: 'absolute',
            right: '6px',
            top: '12px',
            width: '40px',
          }}
        >
          <Cross />
        </button>
      )}
      <div className="nav-container">
        <div className="logoClass">
          <Logo colored text mode="dark" direction="horizontal" />
          <span className="private-beta">Private Beta</span>
        </div>

        <NavLink active={active} mobile={mobile} />
      </div>
      <Footer mobile={mobile} />
    </>
  );

  const mobileNav = (
    <div className="mobile-nav">
      <button
        className="btn-outline-white btn dot"
        onClick={() => setNav(true)}
        style={{
          padding: '8px 10px',
          marginRight: '0px',
          height: '36px',
          width: '36px',
        }}
      >
        <Dot />
      </button>
      <Drawer
        className="mobile-menu-bar"
        anchor="right"
        open={nav}
        onClose={() => setNav(false)}
      >
        <NavbarContent />
      </Drawer>
    </div>
  );

  return (
    <>
      {mobile ? (
        mobileNav
      ) : (
        <nav>
          <NavbarContent />
        </nav>
      )}
    </>
  );
};
