import React from 'react';
import '../../styles/search.scss';
import textContext from '../../textContext';
import { useStore } from '../../store';

// Material
import InputBase from '@mui/material/InputBase';
import HighlightOffIcon from '@mui/icons-material/HighlightOff';
import { searchIcon } from '../../assets/images';

interface ISearchBarProps {
  pageType: 'portfolio' | 'investment' | 'history';
  handleSearch: (search: string) => void;
}

export default function SearchBar({ ...props }: ISearchBarProps) {
  const { language } = useStore((state) => state.appSlice);
  const text = textContext[language as keyof object][props.pageType]['search'];
  const [search, setSearch] = React.useState('');

  const handleEnter = (t: string) => {
    setSearch(t);
    props.handleSearch(t);
  };

  const clearSearch = () => {
    setSearch('');
    props.handleSearch('');
  };

  return (
    <div className="search">
      <div className="wrapper">
        <div className="searchWrapper">
          {search !== '' && (
            <HighlightOffIcon onClick={clearSearch} className="close-icon" />
          )}
        </div>
        <InputBase
          value={search}
          onChange={(event) => handleEnter(event.target.value)}
          placeholder={search === '' ? text.searchBar : ''}
          inputProps={{ 'aria-label': 'search' }}
          startAdornment={
            <img
              src={searchIcon}
              alt="serach"
              style={{
                width: '20px',
                height: '20px',
                paddingRight: '8px',
              }}
            />
          }
        />
      </div>
    </div>
  );
}
