import React, { Fragment } from 'react';
import { StrategyInfo } from '../../../../../constants/strategy/index';
import { StrategyTabs } from './StrategyTabs';
import { VaultItem } from '../VaultItem';
import './card.scss';
import { CardInfo } from './CardInfo';
import { Button } from '../../Button';
import { useOpenModal } from '../../../../../store/base/application/hooks';
import { ModalType } from '../../../../../store/base/AppSlice';
import { getVaulName, numberParse } from '../../../../../helpers/utilities';
import { MobilePopOver } from '../../../Modal/MobilePopOver';
import { PositionInfo } from '../../../../../types';
import { BigNumber } from 'ethers';
import { useStore } from '../../../../../store';
import textContext from '../../../../../textContext';

interface ICardProps {
  strategyInfo: StrategyInfo;
  positionInfo: PositionInfo | null;
  pageType: 'Portfolio' | 'Details' | 'DeltaNeutral';
  walletConnected: boolean;
}

export const StrategyCard = (props: ICardProps) => {
  const {
    strategyInfo,
    strategyInfo: data,
    positionInfo,
    walletConnected,
    pageType,
  } = props;

  const { language } = useStore((state) => state.appSlice);
  const text = textContext[language].assets;
  const { deltaNeutralText } = text;

  const [open, setOpen] = React.useState(false);
  const handleOpen = () => setOpen(true);
  const handleClose = () => setOpen(false);
  const vaultTitle = getVaulName(data.tokens);

  const vaultItem = (isCard = true) => {
    return (
      <VaultItem
        {...{
          title: isCard ? `${vaultTitle}(${data.leverage}X)` : vaultTitle,
          tokens: data.tokens,
          protocols: data.protocols,
        }}
      />
    );
  };

  const handleInvest = (e: React.MouseEvent<HTMLButtonElement, MouseEvent>) => {
    e.stopPropagation();
    openInvestModal();
  };

  const handleWithdraw = (
    e: React.MouseEvent<HTMLButtonElement, MouseEvent>
  ) => {
    e.stopPropagation();
    openWithdrawlModal();
  };

  const modalProps = {
    strategyInfo,
    positionInfo,
    vaultItem: vaultItem(false),
    title: deltaNeutralText,
  };

  const openInvestModal = useOpenModal(ModalType.INVEST, {
    invest: true,
    ...modalProps,
  });

  const openWithdrawlModal = useOpenModal(ModalType.INVEST, {
    invest: false,
    ...modalProps,
  });

  return (
    <Fragment>
      <div onClick={handleOpen} className="cardContainer">
        <div>{vaultItem()}</div>
        <CardInfo
          walletConnected={walletConnected}
          cardInfo={data}
          holdings={Number(positionInfo?.equity)}
          pageType={pageType}
        />
        <div className="itemRow" style={{ justifyContent: 'space-between' }}>
          <Button
            variant="outline"
            size="small"
            text={'Deposit'}
            onClick={(event) => handleInvest(event)}
            disabled={data.TVL >= data.capacity || !walletConnected}
            style={{ width: '40%', padding: '3px' }}
          />
          <Button
            variant="info"
            size="small"
            text={'Withdraw'}
            onClick={(event) => handleWithdraw(event)}
            disabled={!(walletConnected && positionInfo && positionInfo.equity)}
            style={{ width: '40%', padding: '3px' }}
          />
        </div>
      </div>
      <MobilePopOver
        open={open}
        onClose={handleClose}
        className="strategy-pop-over"
        sx={{ padding: 0 }}
      >
        <StrategyTabs
          clickDeposit={(event) => handleInvest(event)}
          clickWithdraw={(event) => handleWithdraw(event)}
          strategyInfo={data}
          holdings={Number(positionInfo?.equity)}
          walletConnected={walletConnected}
        />
      </MobilePopOver>
    </Fragment>
  );
};
