import { ERC20TokenABI } from '../../../constants/crosschain';

import { Contract, Signer, utils } from 'ethers';
import { Provider } from '@ethersproject/providers';
import { SerializedTokenInfo, TokenInfo } from '../../../constants/tokenConfig';
export const fetchSingleToken = async (
  token: TokenInfo,
  walletAddress: string,
  evmManagerAddress: string,
  signerOrProvider?: Signer | Provider,
): Promise<SerializedTokenInfo> => {
  const contract = new Contract(
    token.tokenContractAddress,
    ERC20TokenABI,
    signerOrProvider
  );
  const allowance = await contract.allowance(walletAddress, evmManagerAddress);

  const balance = await contract.balanceOf(walletAddress);
  return {
    ...token,
    balance: balance,
    displayBalance: utils.formatUnits(balance, token.decimals || 18),
    allowance
  };
};
