import './index.scss';
import { VaultItem } from '../../../Strategy';
import { StrategyInfo } from '../../../../../../constants/strategy/index';
import { OverallInfo } from './OverallInfo';
import { BackTestAnlysis } from '../../../Strategy/LineGraph/BackTestAnlysis';
import { Return } from '../../../Strategy/LineGraph/Return';
import { useEffect, useRef } from 'react';
import useGraph from '../../../../../../hooks/graph/useGraph';
import {
  find_x_days_ago,
  calculate_backtest_analysis_apy,
  getReturnData,
} from '../../../../../../helpers/graph';
import textContext from '../../../../../../textContext';
import { Button } from '../../../Button';
import { useOpenModal } from '../../../../../../store/base/application/hooks';
import { ModalType } from '../../../../../../store/base/AppSlice';
import { useStore } from '../../../../../../store';
import { PositionInfo } from '../../../../../../types';

interface PdnStrategyDetailsProps {
  strategyInfo: StrategyInfo;
  positionInfo?: PositionInfo;
  vaultTitle: string;
  scrollToReturn?: boolean;
  scrollToAnalysis?: boolean;
}
export const PdnStrategyDetails = ({
  strategyInfo,
  vaultTitle,
  scrollToReturn,
  scrollToAnalysis,
  positionInfo,
}: PdnStrategyDetailsProps) => {
  const returnRef = useRef(null);
  const analysisRef = useRef(null);
  const language = useStore((state) => state.appSlice.language);
  const isWalletConnected = useStore(
    (state) => state.chainSlice.isWalletConnected
  );

  const { assetPrices, homoraPerformace, backtestAnalysisData } = useGraph();

  const returnData = getReturnData(
    assetPrices,
    homoraPerformace,
    strategyInfo.tokens[0].ticker,
    strategyInfo.tokens[1].ticker
  );
  const week_data = find_x_days_ago(returnData, 7);
  const month_data = find_x_days_ago(returnData, 30);

  const returns = {
    week: week_data,
    month: month_data,
    inception: returnData,
  };

  const text = textContext[language]['investment'];
  const { invest } = text;

  const getFarmingPoolInfo = (tokens: any[], protocols: any[]) => {
    const tokenString =
      `${tokens[0].ticker} - ${tokens[1].ticker}`.toUpperCase();
    if (protocols.length === 2)
      return (
        <div className="farming-pool">
          <div>{`${protocols[0].name} ${tokenString}`}</div>
          <div>{`${protocols[1].name} ${tokenString}`}</div>
        </div>
      );
    else return <>{`${protocols[0].name} ${tokenString}`}</>;
  };

  const poolData = getFarmingPoolInfo(
    strategyInfo.tokens,
    strategyInfo.protocols
  );

  const vaultItem = (
    <VaultItem
      {...{
        title: vaultTitle,
        tokens: strategyInfo.tokens,
        protocols: strategyInfo.protocols,
      }}
    />
  );

  useEffect(() => {
    setTimeout(() => {
      if (scrollToReturn && returnRef && returnRef.current) {
        returnRef!.current!.scrollIntoView({ behavior: 'smooth' });
      }
      if (scrollToAnalysis && analysisRef && analysisRef.current) {
        analysisRef!.current!.scrollIntoView({ behavior: 'smooth' });
      }
    }, 100);
  }, [returnRef, analysisRef]);

  const handleInvest = (e: React.MouseEvent<HTMLButtonElement, MouseEvent>) => {
    e.stopPropagation();
    openInvestModal();
  };

  const handleWithdraw = (
    e: React.MouseEvent<HTMLButtonElement, MouseEvent>
  ) => {
    e.stopPropagation();
    openWithdrawlModal();
  };

  const modalProps = {
    strategyInfo,
    positionInfo,
    vaultItem,
    title: invest.PDN.title,
  };

  const openInvestModal = useOpenModal(ModalType.INVEST, {
    invest: true,
    ...modalProps,
  });

  const openWithdrawlModal = useOpenModal(ModalType.INVEST, {
    invest: false,
    ...modalProps,
  });

  const getDateString = (unixDate: number): string => {
    const date = new Date(unixDate * 1000).toUTCString().split(' ');
    return `${date[3]} ${date[2]}`;
  };

  return (
    <div className="pdn-strategy-details">
      <div className="pdn-strategy-details-title">
        <div className="headline-2">{`${invest.PDN.title}(${strategyInfo.leverage}X)`}</div>
        <div className="details-row">
          {vaultItem}
          <div className="action-button-group">
            <Button
              variant="contained"
              size="medium"
              text={'Deposit'}
              onClick={(event) => handleInvest(event)}
              disabled={
                strategyInfo.TVL ===
                  strategyInfo.capacity * strategyInfo.leverage ||
                !isWalletConnected
              }
            ></Button>
            <Button
              className="action-withdraw"
              variant="info"
              size="medium"
              text={'Withdraw'}
              disabled={
                !(
                  isWalletConnected &&
                  positionInfo &&
                  Number(positionInfo.equity) > 0
                )
              }
              onClick={(event) => handleWithdraw(event)}
            ></Button>
          </div>
        </div>
      </div>
      <div className="pdn-strategy-details-content">
        <div className="subtitle-1" style={{ paddingBottom: '12px' }}>
          Strategy Overview
        </div>
        <div className="body-1">{invest.PDN.description}</div>
        <OverallInfo
          holdings={Number(positionInfo?.equity)}
          apy={strategyInfo.apy}
          leverage={strategyInfo.leverage}
          date={
            returnData && returnData.length > 0
              ? returnData[0]!.timestamp_sec
              : ''
          }
          tvl={strategyInfo.TVL}
          capacity={strategyInfo.capacity * strategyInfo.leverage}
          threshold={
            strategyInfo.priceMovementThreshold / strategyInfo.leverage / 100
          }
          pool={poolData}
          token={strategyInfo.tokens[0].ticker}
        />

        <div ref={returnRef}>
          <Return
            color={{ percentage: '#7BEFFF', price: '#19EE94' }}
            data={returns}
            percentage={
              returnData && returnData.length > 0
                ? {
                    week: week_data[week_data.length - 1].percentage,
                    month: month_data[month_data.length - 1].percentage,
                    inception: returnData[returnData.length - 1].percentage,
                    weekChangeRate:
                      Math.pow(
                        week_data[week_data.length - 1].percentage + 1,
                        52
                      ) - 1,
                  }
                : {
                    week: 0,
                    month: 0,
                    inception: 0,
                    weekChangeRate: 0,
                  }
            }
            token={
              returnData && returnData.length > 0
                ? returnData[returnData.length - 1].ticker
                : 'Token'
            }
            payload={[
              {
                value: 'Return',
                type: 'line',
                id: 'percentage',
                color: '#7BEFFF',
              },
              {
                value: `${
                  returnData && returnData.length > 0
                    ? returnData[0].ticker
                    : 'token'
                } Price`,
                type: 'line',
                id: 'price',
                color: '#19EE94',
              },
            ]}
            style={{ marginTop: '16px' }}
          />
        </div>

        <div ref={analysisRef}>
          <BackTestAnlysis
            title={`${poolData.props.children} ${strategyInfo.leverage}X`}
            date={
              backtestAnalysisData && backtestAnalysisData.length > 0
                ? `${getDateString(
                    backtestAnalysisData[0].timestamp_sec
                  )} - ${getDateString(
                    backtestAnalysisData[backtestAnalysisData.length - 1]
                      .timestamp_sec
                  )}`
                : ''
            }
            color={{ percentage: '#19EE94' }}
            data={backtestAnalysisData}
            percentage={
              backtestAnalysisData && backtestAnalysisData.length > 0
                ? {
                    return:
                      backtestAnalysisData[backtestAnalysisData.length - 1]
                        .percentage,
                    apy: calculate_backtest_analysis_apy(backtestAnalysisData),
                    shapeRation: 0.12664,
                    returnChangeRate: 0.3068,
                  }
                : {
                    return: 0,
                    apy: 0,
                    shapeRation: 0,
                    returnChangeRate: 0,
                  }
            }
            style={{ marginTop: '16px' }}
          />
        </div>
      </div>
    </div>
  );
};
