import React, { Fragment, useEffect, useMemo } from 'react';
import { IStore, useStore } from '../store';
// import { useToken } from '../deprecate/context/TokenContext';
import textContext from '../textContext';
import { StrategyInfo, StrategyInfoMap } from '../constants/strategy';

// Comps
// import Portfolio from '../components/Investment/Portfolio';
import InvestContainer from '../components/Investment/InvestContainer';
import { TvlBox } from '../components/v1';

// Material UI
import Grid from '@mui/material/Grid';
import styled from 'styled-components';
import { useFetchEvmPositions } from '../features/positions/hooks';
import { useFetchStrategies } from '../features/strategies/hooks';
import { BN } from '../helpers/utilities';
import BigNumber from 'bignumber.js';

const Investment = () => {
  const {
    data: strategies,
    // refetch: strategiesRefetch,
    // isLoading: strategiesIsLoading,
  } = useFetchStrategies({
    // refetchInterval: 30, // 30 seconds
    // retryDelay: 1000, // 1 second
  });
  const [totalTvl, setTotalTvl] = React.useState('0');

  useEffect(() => {
    if (strategies) {
      const newVal = Object.values(strategies)
        .reduce(
          (acc: BigNumber, strategy: StrategyInfo) => acc.plus(strategy.TVL),
          BN(0)
        )
        .toString();
      if (totalTvl !== newVal) {
        setTotalTvl(newVal);
      }
    }
  }, [setTotalTvl, strategies, totalTvl]);

  const language = useStore((state) => state.appSlice.language);
  // const totalTvl  = 0;

  const text = textContext[language].investment;

  React.useEffect(() => {
    window.scrollTo(0, 0);
  }, []);

  const BoxContainer = styled.div`
    padding: 0px 24px 16px;
    @media (max-width: 1199px) {
      padding: 0px 16px 16px;
    }
  `;

  const Container = styled.div`
    padding: 0px 24px 26px;
    @media (max-width: 1199px) {
      padding: 0px;
    }
  `;

  const TVLGrid = styled(Grid)`
    gap: 16px;
    @media (max-width: 1199px) {
      gap: 8px;
      .tvl-item {
        width: 100%;
      }
      // .tvl-item: first-child {
      //   width: 100%;
      // }
      // .tvl-item:not(:first-child) {
      //   width: calc(50% - 4px);
      // }
    }
  `;

  return (
    <Fragment>
      <BoxContainer>
        <TVLGrid container>
          <Grid item className="tvl-item">
            <TvlBox
              isPrimary={true}
              heading={text.tvlBox.total}
              amount={totalTvl ? totalTvl : 0}
              decimals={2}
            />
          </Grid>
          <Grid item className="tvl-item">
            <TvlBox
              heading={text.tvlBox.PDN}
              amount={totalTvl ? totalTvl : 0}
              decimals={2}
            />
          </Grid>
          {/* Hide TVL in lending optimizer for Beta Launch
          <Grid item className="tvl-item">
            <TvlBox
              heading={text.tvlBox.lendingOptimizer}
              amount={totalTvl ? totalTvl : 0}
              decimals={2}
            />
          </Grid> */}
        </TVLGrid>
      </BoxContainer>
      <Container>
        <InvestContainer strategies={strategies ?? {}} />
      </Container>
    </Fragment>
  );
};

export default Investment;
