import * as LogoIcon from '../../assets/logo';
import {
  forwardRef,
  ImgHTMLAttributes,
  useRef,
  useImperativeHandle,
} from 'react';
import { Link } from 'react-router-dom';

interface LogoProps {
  colored?: boolean;
  mode?: 'dark' | 'light';
  text?: boolean;
  direction?: 'vertical' | 'horizontal';
}

interface Props extends ImgHTMLAttributes<HTMLImageElement>, LogoProps {}

export const Logo = forwardRef<HTMLImageElement, Props>(
  ({ colored, mode, text, direction, ...props }, ref) => {
    const domRef = useRef<HTMLImageElement>(null);
    useImperativeHandle(ref, () => domRef.current!);

    const getLogo = () => {
      if (!text && colored) {
        return LogoIcon.logoColor;
      }
      if (!text && !colored && mode === 'light') {
        return LogoIcon.logoBWLight;
      }
      if (!text && !colored && mode === 'dark') {
        return LogoIcon.logoBWDark;
      }

      if (text && !colored && mode === 'light' && direction === 'vertical') {
        return LogoIcon.logoTBWLightV;
      }
      if (text && !colored && mode === 'light' && direction === 'horizontal') {
        return LogoIcon.logoTBWLightH;
      }
      if (text && !colored && mode === 'dark' && direction === 'vertical') {
        return LogoIcon.logoTBWDarkV;
      }
      if (text && !colored && mode === 'dark' && direction === 'horizontal') {
        return LogoIcon.logoTBWDarkH;
      }

      if (text && colored && mode === 'light' && direction === 'vertical') {
        return LogoIcon.logoTColorLightV;
      }
      if (text && colored && mode === 'light' && direction === 'horizontal') {
        return LogoIcon.logoTColorLightH;
      }
      if (text && colored && mode === 'dark' && direction === 'vertical') {
        return LogoIcon.logoTColorDarkV;
      }
      if (text && colored && mode === 'dark' && direction === 'horizontal') {
        return LogoIcon.logoTColorDarkH;
      } else {
        return LogoIcon.logoColor;
      }
    };

    return (
      <Link to="/" style={{ display: 'flex' }}>
        <img ref={domRef} {...props} src={getLogo()} alt="aperture logo" />
      </Link>
    );
  }
);
