import { BigNumber } from 'ethers';
import React, { Fragment } from 'react';
import { ProtocolInfo } from '../../../../../../constants/protocolConfig';
import { StrategyInfo } from '../../../../../../constants/strategy/index';
import { TokenInfo } from '../../../../../../constants/tokenConfig';
import {
  numberParse,
  numberParseLarge,
  numberToPercentage,
} from '../../../../../../helpers/utilities';
import useGraph from '../../../../../../hooks/graph/useGraph';
import { NumberCounter } from '../../../NumberCounter';
import './info.scss';

interface ICardProps {
  walletConnected: boolean;
  cardInfo: StrategyInfo;
  holdings: number;
  pageType: 'Portfolio' | 'Details' | 'DeltaNeutral';
}

export const CardInfo = (props: ICardProps) => {
  const {
    cardInfo: data,
    walletConnected: connected,
    pageType: page,
    holdings,
  } = props;

  const { backtestAnalysisData } = useGraph();

  const getDate = () => {
    const date =
      backtestAnalysisData && backtestAnalysisData.length > 0
        ? backtestAnalysisData[0]!.timestamp_sec
        : '';
    const data = new Date(date * 1000).toUTCString().split(' ');
    return `${data[1]} ${data[2]} ${data[3]}`;
  };

  const getTime = (t: Date) => {
    const months = [
      'Jan',
      'Feb',
      'Mar',
      'Apr',
      'May',
      'Jun',
      'Jul',
      'Aug',
      'Sep',
      'Oct',
      'Nov',
      'Dec',
    ];

    let month = months[t.getMonth()];
    let date = t.getDate();
    let year = t.getFullYear();

    return `${date} ${month} ${year}`;
  };

  const getFarmingPoolInfo = (
    tokens: TokenInfo[],
    protocols: ProtocolInfo[]
  ) => {
    const tokenString = tokens
      .map((token) => token.ticker)
      .join('-')
      .toUpperCase();
    if (protocols.length === 2)
      return (
        <>
          {`${protocols[0].name} ${tokenString}`}
          <br />
          {`${protocols[1].name} ${tokenString}`}
        </>
      );
    else return <>{`${protocols[0].name} ${tokenString}`}</>;
  };

  const getClass = (name: string, apy: number | null) => {
    if (name === 'APY' || name === 'Yields') {
      return apy !== null
        ? apy > 0
          ? ' positive'
          : apy < 0
          ? ' negative'
          : ''
        : '';
    }
    return '';
  };

  const Full = () => <span className="fontFull subtitle-4">Full</span>;
  const Available = () => (
    <span className="fontAvailable subtitle-4">Available</span>
  );

  const FormatTVL = () => {
    return (
      <>
        {data.TVL !== null && data.TVL !== undefined ? (
          <NumberCounter
            value={data.TVL}
            formatNumberFunction={(value: number) =>
              `$${numberParseLarge(value, 2)}`
            }
          />
        ) : (
          '-'
        )}
        {' / '}
        {data.capacity !== null && data.capacity !== undefined ? (
          <NumberCounter
            value={data.capacity * data.leverage}
            formatNumberFunction={(value: number) => numberParseLarge(value, 2)}
          />
        ) : (
          '-'
        )}
      </>
    );
  };

  const FormatAPY = () => {
    return (
      <>
        {data.apy !== null ? (
          <NumberCounter
            value={data.apy}
            formatNumberFunction={(value: number) =>
              `${numberToPercentage(value)}`
            }
          />
        ) : (
          '-'
        )}
      </>
    );
  };

  const FormatHoldings = () => {
    return (
      <>
        {connected && !Number.isNaN(holdings) ? (
          <NumberCounter
            value={holdings}
            formatNumberFunction={(value: number) =>
              '$' + numberParseLarge(value, 2)
            }
          />
        ) : (
          '-'
        )}
      </>
    );
  };

  // const FormatDeposits = () => {
  //   return (
  //     <>
  //       {data.holdings ? (
  //         <NumberCounter
  //           value={data.holdings}
  //           formatNumberFunction={(value: number) =>
  //             numberParse(value, 'currency')
  //           }
  //         />
  //       ) : (
  //         '-'
  //       )}
  //     </>
  //   );
  // };

  const FormatCurrentValue = () => {
    return (
      <>
        {!Number.isNaN(holdings) ? (
          <NumberCounter
            value={holdings}
            formatNumberFunction={(value: number) =>
              '$' + numberParseLarge(value, 2)
            }
          />
        ) : (
          '-'
        )}
      </>
    );
  };

  // const FormatYields = () => {
  //   return (
  //     <>
  //       {data.holdings ? (
  //         <NumberCounter
  //           value={data.holdings}
  //           formatNumberFunction={(value: number) =>
  //             numberParse(value, 'currency')
  //           }
  //         />
  //       ) : (
  //         '-'
  //       )}
  //       {data.apy ? (
  //         <NumberCounter
  //           value={data.apy}
  //           formatNumberFunction={(value: number) =>
  //             `(${numberToPercentage(value)})`
  //           }
  //         />
  //       ) : (
  //         '-'
  //       )}
  //     </>
  //   );
  // };

  const FormatLeverage = () => {
    return (
      <>
        {data.leverage ? (
          <NumberCounter
            value={data.leverage}
            formatNumberFunction={(value: number) => `${value}X`}
          />
        ) : (
          '-'
        )}
      </>
    );
  };

  const FormatThrehold = () => {
    const cal_threhold = data.priceMovementThreshold / data.leverage / 100;
    return (
      <>
        {cal_threhold !== null && cal_threhold !== undefined ? (
          <NumberCounter
            value={cal_threhold}
            formatNumberFunction={(value: number) =>
              `${numberToPercentage(value)}`
            }
          />
        ) : (
          '-'
        )}
      </>
    );
  };

  const infoProperty = {
    TVL: FormatTVL(),
    APY: FormatAPY(),
    HOLDINGS: FormatHoldings(),
    // DEPOSITS: FormatDeposits(),
    CURRENTVALUE: FormatCurrentValue(),
    // YIELDS: FormatYields(),
    LEVERAGE: FormatLeverage(),
    INCEPTIONDATE: getDate(),
    THREHOLD: FormatThrehold(),
    FARMINGPOOL: getFarmingPoolInfo(data.tokens, data.protocols),
  };

  interface ICardInfo {
    name: keyof typeof infoProperty;
    title: string;
  }

  const dnProperty: ICardInfo[] = [
    { name: 'TVL', title: 'TVL/Capacity' },
    { name: 'APY', title: 'APY' },
    { name: 'HOLDINGS', title: 'My Holdings' },
  ];

  const portfolioProperty: ICardInfo[] = [
    { name: 'TVL', title: 'TVL/Capacity' },
    { name: 'CURRENTVALUE', title: 'Current Value' },
    { name: 'APY', title: 'APY' },
  ];
  const investProperty: ICardInfo[] = [
    // { name: 'DEPOSITS', title: 'My Deposits' },
    { name: 'CURRENTVALUE', title: 'Current Value' },
    // { name: 'YIELDS', title: 'Yields' },
  ];
  const detailProperty: ICardInfo[] = [
    { name: 'TVL', title: 'TVL/Capacity' },
    { name: 'APY', title: 'APY' },
    { name: 'LEVERAGE', title: 'Leverage' },
    { name: 'INCEPTIONDATE', title: 'Vault Inception Date' },
    { name: 'THREHOLD', title: 'Debt Ratio Rebalance Threhold' },
    { name: 'FARMINGPOOL', title: 'Underlying Farming Pool' },
  ];

  let displayData = (dataInfo: ICardInfo[]) =>
    dataInfo.map((item, i) => {
      return (
        <div key={i} className="info-row">
          <div className="fieldName">
            <p className="subtitle-3">{item.title}</p>
            {item.name === 'TVL' &&
              (data.TVL < data.capacity ? <Available /> : <Full />)}
          </div>
          <div className={`fieldValue`}>
            <p className={`subtitle-1${getClass(item.title, data.apy)}`}>
              {infoProperty[item.name]}
            </p>
          </div>
        </div>
      );
    });

  return (
    <Fragment>
      {page === 'DeltaNeutral' && displayData(dnProperty)}
      {page === 'Portfolio' && displayData(portfolioProperty)}
      {page === 'Details' &&
        (connected && !Number.isNaN(holdings)
          ? displayData(investProperty.concat(detailProperty))
          : displayData(detailProperty))}
    </Fragment>
  );
};
