import { Fragment, useState } from 'react';
import Grid from '@mui/material/Grid';
import { useStore } from '../../store';
import textContext from '../../textContext';
import {
  numberParse,
  numberParseLarge,
  numberToPercentage,
} from '../../helpers/utilities';
import { StrategyInfo } from '../../constants/strategy/index';
import { StrategyCard, VaultItem } from '../v1/Components/Strategy';
import { PdnDropdown } from '../v1/Components/Strategy/Dropdown/PdnDropdown/index';
import { useOpenModal } from '../../store/base/application/hooks';

import { ModalType } from '../../store/base/AppSlice';
import { Button, NumberCounter } from '../v1';
import Accordion from '@mui/material/Accordion';
import AccordionSummary from '@mui/material/AccordionSummary';
import AccordionDetails from '@mui/material/AccordionDetails';
import { LinearProgress, useMediaQuery } from '@mui/material';
import { PositionInfo } from '../../types';
import { UnpopulatedPositionInfo } from '../../features/positions/hooks';
import { useFetchEvmPosition } from '../../features/positions/hooks/useFetchPosition';
import TableLoader from './TableLoader';

interface ITableAssetsProps {
  info?: Object;
  isOpen: React.Dispatch<React.SetStateAction<boolean>>;
  strategyInfo: StrategyInfo;
  positionInfo: UnpopulatedPositionInfo | null;
  key: number;
  title: string;
}

export default function TableAssets(props: ITableAssetsProps) {
  const isPC = useMediaQuery('(min-width:1200px)');
  const language = useStore((state) => state.appSlice.language);
  const isWalletConnected = useStore(
    (state) => state.chainSlice.isWalletConnected
  );

  const [tooltipIsActive, setToolTipIsActive] = useState(false);
  const investText = textContext[language]['investment']['invest'];
  const tableAssetsText = investText['tableAssets'];
  const tooltipText = tableAssetsText['tooltip'];
  const deltaNeutralText = investText['PDN']['title'];
  const { strategyInfo: data, positionInfo, title } = props;

  // const handleClose = () => {
  //   setOpen(false);
  //   // navigate('/dn');
  // };

  // const navigate = useNavigate();
  const { isLoading: positionIsLoading, data: position } =
    useFetchEvmPosition(positionInfo);

  const handleInvest = (e: React.MouseEvent<HTMLButtonElement, MouseEvent>) => {
    e.stopPropagation();
    // navigate(`/dn/asset&=${data.strategy.strategyId}`);
    openInvestModal();
    props.isOpen(true);
  };

  // const accordionClose = () => {
  //   setDrop(false);
  // };

  const vaultItem = (
    <VaultItem
      {...{
        title,
        tokens: data.tokens,
        protocols: data.protocols,
      }}
    />
  );
  const openInvestModal = useOpenModal(ModalType.INVEST, {
    invest: true,
    strategyInfo: props.strategyInfo,
    positionInfo: position,
    vaultItem,
    title: deltaNeutralText,
  });

  const Full = () => <span className="fontFull subtitle-3 medium">Full</span>;
  const Available = () => (
    <span className="fontAvailable subtitle-3 medium">Available</span>
  );

  return (
    <Fragment>
      {isPC && (
        // TODO: Refetch
        <Accordion>
          <AccordionSummary>
            <Grid container>
              <Grid item md={2.8}>
                {' '}
                {vaultItem}{' '}
              </Grid>
              <Grid item md={1.5}>
                <div className="subtitle-2">
                  {data.leverage ? (
                    <NumberCounter
                      value={data.leverage}
                      formatNumberFunction={(value: number) =>
                        numberParse(value, 'table') + 'X'
                      }
                    />
                  ) : (
                    '-'
                  )}
                </div>
              </Grid>
              <Grid item md={3}>
                <span className="subtitle-2">
                  {data.TVL !== null && data.TVL !== undefined ? (
                    <NumberCounter
                      value={data.TVL}
                      formatNumberFunction={(value: number) =>
                        '$' + numberParseLarge(value, 2) + ' '
                      }
                    />
                  ) : (
                    '-'
                  )}
                </span>
                {` / `}
                <span className="subtitle-2" style={{ marginRight: '5px' }}>
                  {data.capacity !== null && data.capacity !== undefined ? (
                    <NumberCounter
                      value={data.capacity * data.leverage}
                      formatNumberFunction={(value: number) =>
                        numberParseLarge(value, 2)
                      }
                    />
                  ) : (
                    '-'
                  )}
                </span>
                {data.TVL === data.capacity ? <Full /> : <Available />}
              </Grid>
              <Grid item md={1.3}>
                <div
                  className={`subtitle-2 ${
                    data.apy !== null
                      ? data.apy > 0
                        ? 'positive'
                        : data.apy < 0
                        ? 'negative'
                        : ''
                      : ''
                  }`}
                >
                  {data.apy !== null ? (
                    <NumberCounter
                      value={data.apy}
                      formatNumberFunction={(value: number) =>
                        numberToPercentage(value)
                      }
                    />
                  ) : (
                    '-'
                  )}
                </div>
              </Grid>
              <Grid item md={1.8}>
                <div className="subtitle-2">
                  {' '}
                  {positionIsLoading ? (
                    <TableLoader size={3} />
                  ) : position && position.equity ? (
                    <NumberCounter
                      value={Number(position.equity)}
                      formatNumberFunction={(value: number) =>
                        '$' + numberParseLarge(value, 2)
                      }
                    />
                  ) : (
                    '-'
                  )}
                </div>
              </Grid>
              <Grid className="investButton" item md={1.6}>
                <Button
                  className="action-deposit"
                  variant="outline"
                  size="small"
                  text={investText['action' as keyof object]}
                  onClick={(event) => handleInvest(event)}
                  disabled={data.TVL === data.capacity || !isWalletConnected}
                  style={{ padding: '0px 16px' }}
                />
              </Grid>
            </Grid>
          </AccordionSummary>
          <AccordionDetails>
            <PdnDropdown
              positionInfo={position}
              strategyInfo={props.strategyInfo}
              protocols={props.strategyInfo.protocols}
              vaultTitle={props.title}
              // accordionClose={accordionClose}
            />
          </AccordionDetails>
        </Accordion>
      )}

      {!isPC && (
        <StrategyCard
          strategyInfo={data}
          positionInfo={position}
          pageType="DeltaNeutral"
          walletConnected={isWalletConnected}
        />
      )}
    </Fragment>
  );
}
