interface NumberCounterProps {
  value: number | string;
  formatNumberFunction?: (value: number | string) => number | string;
}

const toNumber = (val: number | string) => {
  let str = `${val}`;
  str = str.replace(',', '');
  str = str.replace(/[^\d,.-]/g, '');
  const float = parseFloat(str);
  return Number.isNaN(float) ? 0 : float;
};

