import { Grid } from '@mui/material';
import { TransactionModal } from '..';
import { ConfirmationSpinner } from '../../../Components/Spinner';
import { useMediaQuery } from '@mui/material';
import { MobilePopOver } from '../../../Modal/MobilePopOver';
import { ModalProps } from '../../../../layout/ModalsContainer';

export interface ConfirmationProps {
  amount: number;
  isInvest?: boolean;
  currency?: string;
}
export const ConfirmationModal = (props: ConfirmationProps & ModalProps) => {
  const isPC = useMediaQuery('(min-width:1200px)');

  const Content = () => (
    <Grid
      container
      sx={{
        textAlign: 'center',
        padding: isPC ? '0px' : '8px 0px',
        gap: isPC ? '56px' : '24px',
      }}
    >
      <Grid item md={12} sm={12} xs={12}>
        <ConfirmationSpinner />
        <div
          style={{ marginTop: isPC ? '36px' : '24px', color: 'white' }}
          className={isPC ? 'headline-3' : 'headline-2'}
        >
          {' '}
          Waiting for Confirmation
        </div>
      </Grid>
      <Grid item md={12} sm={12} xs={12}>
        <div
          className={isPC ? 'headline-4 regular' : 'subtitle-2 regular'}
          style={{ color: 'white' }}
        >
          {props.isInvest ? 'Depositing' : 'Withdrawing'} {props.amount}{' '}
          {props.currency ?? 'USDC'}
        </div>
        <div
          className="headline-5 medium"
          style={{ marginTop: isPC ? '16px' : '8px', color: '#CAD2D8' }}
        >
          Confirm this transaction in your wallet{' '}
        </div>
      </Grid>
    </Grid>
  );

  return (
    <>
      {props.isOpen && isPC ? (
        <TransactionModal
          isOpen={props.isOpen}
          onClose={props.onClose}
          modalClassName="modal-confirmation"
        >
          <Content />
        </TransactionModal>
      ) : (
        <MobilePopOver
          open={props.isOpen}
          onClose={props.onClose}
          className="transaction-pop-over"
        >
          <Content />
        </MobilePopOver>
      )}
    </>
  );
};
