import { useState } from 'react';
import textContext from '../../textContext';
import { useStore } from '../../store';

// Comps
// import Search from './Search';
import Invest from './Invest';
import { StrategyInfoMap } from '../../constants/strategy/index';
import { PositionInfo } from '../../types';

// TODO: add storybook
export default function InvestContainer(props: {
  strategies: StrategyInfoMap;
}) {
  const language = useStore((state) => state.appSlice.language);
  const text =
    textContext[language as keyof object]['investment' as keyof object][
      'invest' as keyof object
    ];
  const [search, setSearch] = useState('');

  const handleSearch = (fil: string) => {
    setSearch(fil);
  };

  return (
    <>
      {/* <Search handleSearch={handleSearch} /> */}
      <Invest
        strategies={props.strategies ?? {}}
        search={search}
        filters={text['filterPlus' as keyof object]}
        title={text['PDN' as keyof object]['title' as keyof object]}
        description={text['PDN' as keyof object]['description' as keyof object]}
      />
    </>
  );
}
