import { useQuery } from 'react-query';
import {
  SerializedTokenInfo,
  StablecoinMap,
  TokenInfo,
} from '../../../constants/tokenConfig';
import { IStore, useStore } from '../../../store';
import { IEvmProvider } from '../../../store/provider/Evm/EvmProvider';
import { keys } from '../../keyNames';
import { useMemo } from 'react';
import { fetchSingleToken } from '../api';
import { TokenInfoMap } from '../tokens';
export function useFetchAllTokens(options?: any) {
  const evmManager = useStore(
    (state: IStore) =>
      (state.chainSlice.chainProvider as IEvmProvider)?._evmManager
  );
  const web3Provider = useStore(
    (state: IStore) =>
      (state.chainSlice.chainProvider as IEvmProvider)?._web3Provider
  );
  const walletAddress = useStore(
    (state: IStore) =>
      (state.chainSlice.chainProvider as IEvmProvider)?.walletAddress
  );

  const shouldFetch = useMemo(
    () => !!(web3Provider && walletAddress),
    [web3Provider, walletAddress]
  );

  const fetchTokens = async () => {
    const tokenInfoMap: TokenInfoMap = {};
    const tokenInfos: TokenInfo[] = Object.values(StablecoinMap);

    const serializedTokenInfos = await Promise.all(
      tokenInfos.map(async (token: TokenInfo) => {
        const serializedTokenInfo: SerializedTokenInfo = await fetchSingleToken(
          token,
          walletAddress!,
          evmManager!.address,
          web3Provider!.getSigner()
        );
        return serializedTokenInfo;
      })
    );

    serializedTokenInfos.forEach((tokenInfo: SerializedTokenInfo) => {
      tokenInfoMap[tokenInfo.tokenContractAddress] = tokenInfo;
    });
    return tokenInfoMap;
  };

  return useQuery(
    [keys.tokens],
    async () => {
      if (!shouldFetch) return {};
      return fetchTokens();
    },
    {
      // refetchOnMount: false,
      ...options,
      enable: shouldFetch,
    }
  );
}
