import { dat2a } from "../components/v1/Components/Strategy/LineGraph/BackTestAnlysis/mockdata";

const mergeByTime = (a1: any[], a2: any[]) =>
    a1.map((itm) => ({
        ...a2.find((item) => item.timestamp_sec === itm.timestamp_sec && item),
        ...itm,
    }));

export const find_x_days_ago = (data: any[], days: number) => {
    if (data.length > 0) {
        const earlist = data[data.length - 1].timestamp_sec - (86400 * days)
        const index = data.findIndex((item) => item.timestamp_sec >= earlist)
        return data.slice(index, data.length)
    }
    return data
}

export const calculate_backtest_analysis_apy = (data: any[]) => {
    if (data.length > 0) {
        const days_count = (data[data.length - 1].timestamp_sec - data[0].timestamp_sec) / 86400
        const period = 365 / days_count
        return Math.pow(data[data.length - 1].percentage + 1, period) - 1
    }
    return 0
}

/* token A stable coin, token B unit coin */
export const getReturnData = (assetPrices: any[], homoraPerformace: any[], tokenA: string, tokenB: string) => {
    if (isNotEmpty(assetPrices) && isNotEmpty(homoraPerformace)) {
        const asset = assetPrices[tickerMap[tokenB]]
        const performace = homoraPerformace[stableCoinMap[tokenA]]
        const initial_price = performace[0].price
        const percentage_data = performace.map((itm: any) => ({ timestamp_sec: itm.timestamp_sec, percentage: (itm.price - initial_price) / initial_price }))
        const return_data = mergeByTime(percentage_data, asset)
        return (return_data)
    }
    return []
}

function isNotEmpty(obj: Object) {
    return Object.keys(obj).length !== 0;
}

/* timeGap unit second, 1800 = 30mins */
export const bucketTimestamp = (data: any[], timeGap: number) => {
    if (data && data.length > 1) {
        const list = []
        let time_counter = Math.ceil(data[0].timestamp_sec / timeGap) * timeGap
        data.forEach((itm: any, index: number) => {
            if (index + 1 === data.length || (data[index].timestamp_sec <= time_counter && data[index + 1].timestamp_sec > time_counter)) {
                itm.timestamp_sec = time_counter
                list.push(itm)
                time_counter += 1800
            }
        })
        return list
    }
    return data

}

const stableCoinMap = {
    'DAI.e': 0,
    'USDC.e': 1,
    'USDT.e': 2,
}

const tickerMap = {
    'wavax': "AVAX",
}