const linkIcon = (
  <svg
    width="12"
    height="13"
    viewBox="0 0 12 13"
    fill="none"
    xmlns="http://www.w3.org/2000/svg"
  >
    <path
      d="M3.87864 3.60909H8.82839V8.55884M3.17154 9.26594L8.82839 3.60909L3.17154 9.26594Z"
      stroke="white"
      strokeLinecap="round"
      strokeLinejoin="round"
    />
  </svg>
);

export const AuthUser = forwardRef<AuthUserProps, PropsWithChildren<any>>(
  (
    {
      mobile,
      balance,
      address,
      explorerURL,
      disconnectFunc,
      transactionsHistory,
      walletIcon,
      chain,
      clearAllTransactions,
    },
    ref
  ) => {
    const [open, setOpen] = useState(false);
    const { chainId } = useChainDetails();

    const childRef = useRef(null);

    const slideAddress = (address: string, long: boolean) => {
      address = address + '0';
      return long
        ? address.slice(0, 11) + '...' + address.slice(-12, -1)
        : address.slice(0, 6) + '...' + address.slice(-5, -1);
    };

    const txnHistoryEntry = (txn: ITransaction) => {
      return (
        <li key={txn.timestamp} className="txnHistory-entry">
          {txn.status === 'OPENED' ? (
            pendingTxnIcon
          ) : txn.status === 'CLOSED' ? (
            completedTxnIcon
          ) : txn.status === 'ERROR' ? (
            failedTxnIcon
          ) : (
            <></>
          )}
          {/* {txn.status === 'ERROR' && txn.hash !== undefined && (
            <span>{txn.error ? errorInfoParser(txn.error) : 'Failed'}</span>
          )} */}
          {/* {txn.status === 'ERROR' && txn.hash === undefined && (
            <span>{errorInfoParser(txn.error)}</span>
          )} */}
          {/* how to figure out the transaction type */}
          {txn.status === 'ERROR' && <span>Your transaction of {txn.transactionPayload.amount?.toFixed(2)} {txn.transactionPayload.token.ticker} has failed.</span>}
          {txn.status === 'CLOSED' && <span>Your transaction of {txn.transactionPayload.amount?.toFixed(2)} {txn.transactionPayload.token.ticker} has been completed.</span>}
          {txn.status === 'OPENED' && <span>Your transaction of {txn.transactionPayload.amount?.toFixed(2)} {txn.transactionPayload.token.ticker} is pending.</span>}
          {txn.hash !== undefined && chainId && (
            <a
              href={getExplorerLink(
                chainId,
                txn.hash,
                ExplorerDataType.TRANSACTION
              )}
              target="_blank"
              rel="noreferrer"
            >
              {linkIcon}
            </a>
          )}
        </li>
      );
    };

    const Content = () => (
      <div className={['authUser-content', mobile ? '' : 'desktop'].join(' ')}>
        <div className="authUser-content-top">
          <div className="headline-5">Connected with MetaMask</div>
          <div className="flex-vertical">
            <img
              src={walletIcon}
              alt="MetaMask"
              height={25}
              style={{ marginRight: '8px' }}
            />
            <div className="headline-5">{slideAddress(address, true)}</div>
          </div>
          <div className="flex-vertical">
            <ViewOnExplorer
              mobile={mobile}
              address={getExplorerLink(
                chainId,
                address,
                ExplorerDataType.ADDRESS
              )}
            />
            <CopyButton mobile={mobile} copyContent={address} />
          </div>

          <Button
            className={mobile ? 'button-3' : 'button-5'}
            variant="info"
            size="xsmall"
            style={{ width: '100%', padding: mobile ? '8px 6px' : '4px 20px' }}
            onClick={disconnectFunc}
            text="Disconnect"
          />
        </div>
        <div className="authUser-content-bottom">
          <div className="authUser-content-bottom-title">
            <span className={mobile ? 'subtitle-1' : 'subtitle-2'}>
              Recent Activities
            </span>
            {transactionsHistory && transactionsHistory.length > 0 && (
              <button
                className="button-4"
                onClick={() => clearAllTransactions()}
              >
                Clear all
              </button>
            )}
          </div>
          <div className="authUser-content-bottom-body">
            {transactionsHistory && transactionsHistory.length > 0 ? (
              <ul className="body-2">
                {transactionsHistory.map((txn: ITransaction) =>
                  txnHistoryEntry(txn)
                )}
              </ul>
            ) : (
              <p className={mobile ? 'body-1' : 'subtitle-2'}>
                Your recent transactions will appear here...
              </p>
            )}
          </div>
        </div>
      </div>
    );

    const AuthBtn = (props: any) => (
      <div
        onClick={props.onClick}
        className="authUser-trigger"
        style={{ marginRight: mobile ? '8px' : '0px' }}
      >
        <span className="authUser-address-block">
          <img
            src={walletIcon}
            alt="MetaMask"
            width={16}
            height={16}
            style={{ marginRight: '4px' }}
          />
          <div
            className={mobile ? 'button-5' : 'headline-5'}
            style={{ letterSpacing: '0.0025em' }}
          >
            {slideAddress(address, false)}
          </div>
        </span>
        {!mobile && (
          <div className="headline-5 top" style={{ letterSpacing: '0.0025em' }}>
            <NumberCounter
              value={balance}
              formatNumberFunction={(value: number) =>
                Math.floor(value * 100) / 100 +
                ' ' +
                chainList[NetworkMap[chain]].nativeDenom
              }
            />
          </div>
        )}
      </div>
    );
    return (
      <>
        {mobile ? (
          <>
            <AuthBtn onClick={() => setOpen(true)} />
            <MobilePopOver
              open={open}
              onClose={() => setOpen(false)}
              cross={true}
              sx={{ padding: 0 }}
              className="connected-pop-over"
            >
              <Content />
            </MobilePopOver>
          </>
        ) : (
          <Dropdown
            ref={childRef}
            direction={{
              anchorOrigin_vertical: 'bottom',
              anchorOrigin_horizontal: 'right',
              transformOrigin_vertical: 'top',
              transformOrigin_horizontal: 'right',
            }}
            trigger={<AuthBtn />}
            style={{ padding: 0 }}
          >
            <Content />
          </Dropdown>
        )}
      </>
    );
  }
);
