import LibUpdater from '../../../../lib/hooks/transactions/updater';
import { useCallback, useMemo } from 'react';
import { useAddPopup } from '../../../base/application/hooks';
import { IStore, useStore } from '../../..';
import { PopupType } from '../../../base/AppSlice';

export default function EvmTransactionUpdater() {
  const { transactionProvider, chainId } = useStore(
    (state: IStore) => state.chainSlice
  );
  const { checkedTransaction, finalizeTransaction, transactions } =
    transactionProvider || {};
  const addPopup = useAddPopup();

  const onCheck = useCallback(
    ({ chainId, hash, blockNumber }) => {
      if (!checkedTransaction) return;
      checkedTransaction({ chainId, hash, blockNumber });
    },
    [checkedTransaction]
  );
  const onReceipt = useCallback(
    ({
      chainId,
      hash,
      receipt,
    }: {
      chainId: number;
      hash: string;
      receipt: any;
    }) => {
      if (!finalizeTransaction) return;
      finalizeTransaction({
        chainId,
        hash,
        receipt: {
          blockHash: receipt.blockHash,
          blockNumber: receipt.blockNumber,
          contractAddress: receipt.contractAddress,
          from: receipt.from,
          status: receipt.status,
          to: receipt.to,
          transactionHash: receipt.transactionHash,
          transactionIndex: receipt.transactionIndex,
        },
      });
      addPopup(
        {
          [PopupType.TXN]: { hash },
        },
        hash
      );
    },
    [addPopup, finalizeTransaction]
  );

  const pendingTransactions = useMemo(
    () => (chainId && transactions ? transactions[Number(chainId)] ?? {} : {}),
    [chainId, transactions]
  );

  return (
    <>
      {transactionProvider && chainId && (
        <LibUpdater
          pendingTransactions={pendingTransactions}
          onCheck={onCheck}
          onReceipt={onReceipt}
        />
      )}
    </>
  );
}
