import * as AWS from 'aws-sdk';
import { useEffect, useState } from 'react';
import { bucketTimestamp } from '../../helpers/graph';

const useGraph = () => {

    const awsConfig = {
        accessKeyId: process.env.REACT_APP_AWS_ACCESS_KEY,
        accessSecretKey: process.env.REACT_APP_AWS_SECRET_KEY,
        region: "us-west-2"
    }
    AWS.config.update(awsConfig);
    const documentClient = new AWS.DynamoDB.DocumentClient();

    const [returnData, setReturnData] = useState([]);
    const [assetPrices, setAssetPrices] = useState({});
    const [homoraPerformace, setHomoraPerformace] = useState({});
    const [backtestAnalysisData, setBacktestAnalysisData] = useState([]);
    const [whitelistData, setWhitelistData] = useState([]);

    useEffect(() => {
        const params_price = {
            TableName: "asset_prices",
            Select: "ALL_ATTRIBUTES"
        };
        documentClient.scan(params_price, function (err, data) {
            if (err) {
                console.error("Error: get asset_prices Data", err);
            } else {
                const result = data.Items.reduce(function (r, a) {
                    r[a.ticker] = r[a.ticker] || [];
                    r[a.ticker].push(a);
                    return r;
                }, Object.create(null))
                Object.keys(result).forEach(key => {
                    result[key] = bucketTimestamp(result[key], 1800)
                })
                setAssetPrices(result)
            }
        });
    }, [])

    useEffect(() => {
        const param_performance = {
            TableName: "homora_performance",
            Select: "ALL_ATTRIBUTES"
        };
        documentClient.scan(param_performance, function (err, data) {
            if (err) {
                console.error("Error: get homora_performance Data", err);
            } else {
                // const initial_price = data.Items![0].price
                // const percentage_data = data.Items!.map((itm) => ({ timestamp_sec: itm.timestamp_sec, percentage: (itm.price - initial_price) / initial_price }))
                // const return_data = mergeByTime(percentage_data, price_data)
                const result =
                    data.Items.reduce(function (r, a) {
                        r[a.vault_id] = r[a.vault_id] || [];
                        r[a.vault_id].push(a);
                        return r;
                    }, Object.create(null))
                Object.keys(result).forEach(key => {
                    result[key] = bucketTimestamp(result[key], 1800)
                })
                setHomoraPerformace(result)
            }
        });
    }, [])

    useEffect(() => {
        const params = {
            TableName: "homora_performance_backtest",
            Select: "ALL_ATTRIBUTES"
        };

        documentClient.scan(params, function (err, data) {
            if (err) {
                console.error("Error: getBacktestAnalysisData", err);
            } else {
                const initial_price = data.Items![0].value
                const percentage_data = data.Items!.map((itm) => ({ timestamp_sec: itm.timestamp_sec, percentage: (itm.value - initial_price) / initial_price }))
                setBacktestAnalysisData(percentage_data)
            }
        });
    }, []);

    useEffect(() => {
        const params = {
            TableName: 'aperture_whitelist_address',
            Select: 'ALL_ATTRIBUTES',
        };

        documentClient.scan(params, function (err, data) {
            if (err) {
                console.error('Error: getWhitelistData', err);
            } else {
                setWhitelistData(data.Items);
            }
        });
    }, []);

    return { returnData, assetPrices, homoraPerformace, backtestAnalysisData, whitelistData };
};

export default useGraph;
